           Les risques sont multiples, on peut citer les agressions, les vols, les endommagements des colis, etc. Une prime d’assurance a donc été mise en place et permet d’indemniser les clients au cas où un des risques cités plus haut arriverait.<br>
            La prime d’assurance est mensuelle et payable d’avance. Si elle n’est pas payée, l’adhérent ne pourra plus réaliser de transactions (envoi ou transport de colis). Le montant maximal d’indemnisation en cas de sinistre est de 300 euros.<br>

Les différentes options sont les suivantes :<br>
<span class="font-weight-bold">Option Gratuite (Free)</span> : J’envoie au plus 1 colis tous les 2 mois – pas de prime d’assurance<br>
<span class="font-weight-bold">Option Médium (Curent)</span> : J’envoie au plus 2 colis tous les 2 mois – prime d’assurance de 3 euros par mois<br>
<span class="font-weight-bold">Option Médium (Premium) </span>: Pas de limite en termes d’envoi de colis– prime d’assurance de 5 euros par mois<br>
