<template>
 
 
   <div>
<mdb-card narrower>

    <div class="container">
             <!-- Les alertes -->
             <div class="row">
              <div class="col">
       <mdb-alert class="text-center" color="warning">
      ⦁	 Le voyageur devra apporter un justificatif de son voyage à son nom, et se munir de la réservation du billet d'avion pour récupérer les colis à transporter <br>
      ⦁	 Le voyageur devra se présenter lui-même au point de livraison pour déposer les colis transportés, et muni de sa pièce d'identitée

    </mdb-alert>
     <!-- Les alertes -->
        </div>
      </div>
       <h1 class="font-weight-bold text-center h1 my-2">Publier un voyage</h1>
         <v-divider></v-divider>
       <mdb-card>
           <mdb-card-header class="mdb-color grey lighten-3 white-text">
             
   <div class="row">
     <div class="col-lg-1"></div>
                  <span class="col-lg col-md-5 btn btn-white lighten-5rounded" size="lg" disabled><img src="https://image.flaticon.com/icons/svg/1692/1692028.svg" alt="User Photo" class="" style="width: 40px">
                  <select class="browser-default custom-select " v-model="choosencountry2" @change="mydepart" style="border-right:hidden;borber-bottom:solid;border-top:hidden;;border-left:hidden;border-radius:0px" fas icon="flag">
          <option v-for="country in pays" v-bind:value="country"> {{ country.name_country }} </option>
        </select>      
        <span style="font-size:10px">Pays de départ</span>    
                  </span>
             
               <div class="col-lg col-md-2 font-weight-bold grey-text text-center mx-4 h3">Direction</div>
               
               
                  <span class="col-lg col-md-5 btn btn-white lighten-5rounded mx-3" size="lg" disabled><img src="https://image.flaticon.com/icons/svg/944/944557.svg" alt="User Photo" class="" style="width: 40px">
                  <select class="browser-default custom-select " v-model="choosencountry" @change="myarrive" style="border-right:hidden;borber-bottom:solid;border-top:hidden;;border-left:hidden;border-radius:0px" fas icon="flag">
          <option v-for="country in pays" v-bind:value="country" v-if="choosencountry2.id != country.id" > {{ country.name_country }} </option>
        </select><br>
        <span style="font-size:10px">Pays d'arrivée</span>
                  </span>
            <div class="col-lg-1"></div>
             </div>
             
               </mdb-card-header>
               <mdb-alert class="mt-2 mx-2 text-center" color="primary">
     								<mdb-icon fas icon="info" class="pr-1" /> Déposez votre annonce de transport, des expéditeurs vous enverront des demandes de réservation
                 </mdb-alert>
                 <mdb-card class="mx-3">
                   <mdb-card-header class="mdb-color ight-blue accent-1 white-text">
                      <h4 class="title text-center font-weight-bold">Informations sur le voyage</h4>
                   </mdb-card-header>
                  
                  <mdb-card-body>
                     
                          <div class="form-group mt-3">
                              <form class="" @submit.prevent="submit">
                                
                            <div class="input-group mb-4">
                              
            <label class="col-sm-5 control-label">Nombre de kilos mis à disposition pour le voyage *</label>
            <input type="number" ref="nbre" v-model="kilo" @change="nbrekilo()" class="form-control rounded" min="1" step="0.5"  required  data-validation-required-message="Ce champ est requis">
            <div class="input-group-prepend">
    <span class="input-group-text" id="basic-addon1" style="height:38px">Kg</span>
          </div>
            </div>
            <div class="input-group mb-4 ">
             <label class="col-sm-5 control-label">Prix du Kg (€/Kg) *</label>
            <input type="number" ref="nbre2" @change="nbrekilo()" min="0.5" step="0.5" v-model="price"  class="form-control rounded" required="" data-validation-required-message="Ce champ est requis">
           <div class="input-group-prepend">
    <span class="input-group-text" id="basic-addon1" style="height:38px">€</span>
          </div>
            </div>
          
          <div class="input-group mb-3" >
             <label class="col-sm-5 control-label">Ville de depart *</label>
             <select class="form-control border solid rounded" v-model="choosendepart" @change="checkdep" required >
               <option v-for="vdepart in departv" v-bind:value="vdepart"> {{ vdepart.name_city }} </option>
                            </select>
                            </div>
            <div class="input-group mb-3">
             <label class="col-sm-5 control-label">Ville d'arrivée *</label>
             <select class="form-control border solid rounded" v-model="choosenarrive" @change="checkarr" required>
               <option v-for="varrive in arrivev" v-bind:value="varrive"> {{ varrive.name_city }} </option>
                            </select>
            </div>
            <div class="input-group mb-1" >
             <label class="col-sm-5 control-label">Préférence en termes de point de livraison pour récupérer les colis à transporter * </label>
             <select style="font-size:13px" class="form-control border solid rounded" v-model="choosencheckdep" required >
               <option style="font-size:12px" v-for="cdepart in checkpointdep" v-bind:value="cdepart">{{cdepart.city_name}} {{ cdepart.address }} {{ cdepart.phone }} </option>
                            </select>
                            </div>
                <i class="d-xl-none d-md-none" style="font-size:14px">Contact: {{choosencheckdep.phone}} </i> <br/>
            <div class="input-group mt-2 mb-2">
             <label class="col-sm-5 control-label">Préférence en termes de point de livraison pour le dépôt des colis à l’arrivée *</label>
             <select  style="font-size:13px" class="form-control border solid rounded" v-model="choosencheckarrive" required>
               <option style="font-size:12px" v-for="carrive in checkpointarr" v-bind:value="carrive"> {{ carrive.city_name }} {{ carrive.address }} {{ carrive.phone }} </option>
                            </select>
            </div>
            <i class="d-xl-none d-md-none" style="font-size:14px">Contact: {{choosencheckarrive.phone}} </i> <br/>

            <div class="input-group mb-4">
             <label class="col-sm-5 control-label">Compagnie aérienne * </label>
  <multiselect style="border:hidden" class="form-control" v-model="value2" :options="options2" placeholder="Select one" label="name" track-by="name" required></multiselect>
            </div>
                         
                <div class="input-group mb-4">
               <label class="col-sm-5 control-label">Date de départ *</label>
              <datetime min-datetime="19" class="cyan-text form-control" v-model="depart" required />
              </div>

              <div class="input-group mb-4 ">
                  <label class="col-sm-5 control-label">Heure de depart *</label>
                  <input type="time" v-model="hdepart"  class="form-control rounded" required data-validation-required-message="Ce champ est requis">
              </div>
            
             <div class="input-group mb-4">
               <label class="col-sm-5 control-label">Date d'arrivée *</label>
              <datetime :min-datetime="depart"  class="cyan-text form-control" v-model="arrive" required />
              </div>

         <div v-scroll-reveal.reset class="input-group mb-4 ">
             <label class="col-sm-5 control-label">Heure d'arrivée *</label>
            <input type="time" v-model="harrive"  class="form-control rounded" required data-validation-required-message="Ce champ est requis">
         </div>

            <div class="input-group mb-3">
              <label class="col-sm-5 control-label">Catégorie de colis à transporter * </label>
            <!--  <select class="form-control border solid rounded" v-model="choosencategory" required>
               <option v-for="categorie in category" v-bind:value="categorie"> {{ categorie.name_category }} </option>
                            </select>  -->

            <multiselect style="border:hidden" class="form-control" v-model="value" :options="options" :multiple="true" :close-on-select="false" :clear-on-select="false" :preserve-search="true" placeholder="Choisir" label="name" track-by="name" :preselect-first="true" required data-validation-required-message="Ce champ est requis">
              <template slot="selection"  slot-scope="{ values, search, isOpen }"><span class="multiselect__single" v-if="values.length &amp;&amp; !isOpen">{{ values.length }} Catégories choisies</span></template>
            </multiselect>                      
            </div>

            <div class="input-group mb-4">
             <label class="col-sm-5 control-label">Informations supplémentaires  </label>
            <textarea onkeyup="this.value=this.value.replace(/\d+/g, '')" v-model="description" name="content" class="form-control rounded" ></textarea>
            </div>

            <div class="row">
              <input type="checkbox" class="custom-control-input" id="defaultUnchecked" required> <br>
      <label class="ml-5 mt-3 custom-control-label" for="defaultUnchecked"> Valider les  <router-link to="login" >condition générales d'utilisation.</router-link>  </label>
            </div>
            
    <p class="text-center my-5 mx-auto w-responsive"><mdb-btn type="submit" class="green rounded-pill"><mdb-icon icon="car-side" class="mr-1"/>Enregistrer mon annonce</mdb-btn></p>
<div class="row">
  <div class="col-6 mr-n2"></div>
    <div v-bind:class="{'spinner-border': spinner}" class="center" role="status"></div>
    </div>

    <div class="alert" v-bind:class="{'alert-success': success, 'alert-danger': alert, 'text-center':text} "> {{ message }} </div>


                            </form>
                            <mdb-modal frame position="bottom" direction="bottom" :show="modal" @close="modal = false, spinner=false">
        <mdb-modal-body class="text-center">
            <span><span class="font-weight-bold">Annonce enregistrée </span> vous serez mis en relation avec un expediteur dans de brefs delais</span>
            <mdb-btn color="secondary" @click.native="spinner=false, modal = false ">Fermer</mdb-btn>
        </mdb-modal-body>
    </mdb-modal>
   
          
          </div>
          </mdb-card-body>
          
                 </mdb-card>
        
       </mdb-card>
    </div>
    </mdb-card>
  </div>
</template>

<script>
import navbar from './navbar'
import Multiselect from 'vue-multiselect'
import myfooter from './footer'
import { Datetime } from 'vue-datetime';
import axios from 'axios'
import json from './airlines.json'



export default {
    components:{ navbar,Datetime, myfooter, Multiselect},
    data(){
      return{
        time: null,
        menu2: false,
         value: [],
         value2: '',
     /*  options: [
        { name: 'Adonis', language: 'JavaScript' },
        { name: 'Vue.js', language: 'JavaScript' },
        { name: 'Rails', language: 'Ruby' },
        { name: 'Sinatra', language: 'Ruby' },
        { name: 'Laravel', language: 'PHP' },
        { name: 'Phoenix', language: 'Elixir' }
      ], */
        depart:'13' ,
        hdepart:'',
        harrive:'',
        options:[],
        options2:[],
        arrive: '',
        message:'',
        compagnie:'',
        text:'',
        alert:'',
        success:'',
        modal:false,
        alignment: 'center',
        spinner:'',
        category:[],
        categorie:'',
        departv:'',
        pays:[],
        country:'',
        choosencountry:'',
        choosencountry2:'',
        arrivev:'',
        choosencategory:'',
        choosendepart:'',
        choosenarrive:'',
        choosencheckdep:'',
        choosencheckarrive:'',
        checkpointarr:'',
        checkpointdep:'',
        description:'',
        price:'',   
        kilo:'',
        rvalue:[],
        airlines: json,
      }
    },

    methods:{
      
      nbrekilo(){
        if ( this.kilo < 1) {
           this.kilo = 1 //La masse du kilo ne peut etre inferieure a 1
        }
       let n = this.kilo - Math.floor(this.kilo) //Recuperer lapartie décimale du nombre (n)
        console.log('la partie decimale', n)
         if ( !(n == 0) && !(n == 0.5)) { //On regarde si nest pas egal a zero et nest pas egal a 0.5  (n)
           if ( n < 0.5) {
              this.kilo = this.kilo - n
               //Si infereirue a 0.5 on enleve lapartie decimale (n) du nombre ex si on a 1.3 au final ce sera 1
           }
           else{
              this.kilo = this.kilo - n
          this.kilo = this.kilo + 0.5    //Si n>0.5 on enleve la partie decimale et on le cale 0.5 pour quil respecte le pas de 0.5
           }
        }

        if (this.$refs.nbre.value <= 0 || this.$refs.nbre.value == 0.5 ) {  //Ref cest pur recuperer le input ça interagit avec le dom juste pour recuperer et non pour modifier
          this.kilo = 1
          console.log('Valeur du ref', this.$refs.nbre.value)
        }

         if (this.$refs.nbre2.value <= 0 || this.$refs.nbre2.value == 0.5 ) {
          this.price = 1
          console.log('Valeur du ref', this.$refs.nbre2.value)
        }
      },
      checkdep(){
        console.log("id de la ville de depart : ",this.choosendepart.id)
        axios.post('https://safe-community.fr/lara/api/checkpointByCity',{
         
      id: this.choosendepart.id
    
        })
        .then(response => {
          console.log(response)
          this.checkpointdep = response.data
        })
      },
       checkarr(){
        console.log("id de la ville d'arrivée : ",this.choosenarrive.id)
        axios.post('https://safe-community.fr/lara/api/checkpointByCity',{
          id:this.choosenarrive.id
        })
        .then(response => {
          console.log(response)
          this.checkpointarr = response.data
        })
      },
      myarrive(){
         axios.post('https://safe-community.fr/lara/api/cityBC',{
        id:this.choosencountry.id,
      })
      .then( response => {
        this.arrivev = response.data
      })
      },
       mydepart(){
         axios.post('https://safe-community.fr/lara/api/cityBC',{
        id:this.choosencountry2.id
      })
      .then( response => {
        this.departv = response.data
      })
      },
      redirect(){
      let router =  this.$router 
       setTimeout( function () {
                router.push({ name: 'd.voyages' })
              },  3000);
      },

      submit(){
        this.message =''
        this.alert = false
         //mettre la liste des catégories(value) dans un string séparés par une virgulle et dans les guillemets
        this.rvalue =''
          for (let i = 0; i < this.value.length; i++) {
          this.rvalue = this.value[i].name_category + ', ' + this.rvalue
        }
        /////
        console.log(this.rvalue)
        axios.post('https://safe-community.fr/lara/api/post',{
          origin:this.choosendepart.id,
          compagnie:this.value2.name,
          checkpoint_dep:this.choosencheckdep.id,
          checkpoint_arr:this.choosencheckarrive.id,
          destination:this.choosenarrive.id,
          date_depart:this.depart,
          date_arrive:this.arrive,
          description: this.description,
          package_category: this.rvalue,
          kilo:this.kilo,
          price_kilo: this.price,
          heure_depart:this.hdepart,
          heure_arrive:this.harrive,
        },{
           headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        }
        })
        .then( response => {
          if (response.status == 200) {
             this.redirect()
            this.message =''
            this.alert = false
             this.modal= true
             this.spinner = false
             this.kilo='',
             this.price='',
             this.description=''
             this.compagnie=''
             this.hdepart = ''
             this.harrive = ''
            
              
          }
          console.log(response)
        })
        .catch(error => {
          if(error.message){
            console.log(error);
            this.spinner = false
            this.text = true
            this.alert = true
            this.message = error.response.data.message
            
          }
        })
      }
    },  

   created(){
     
        this.options2 = this.airlines
         for (let i = 0; i < this.airlines.length; i++) {
          this.options2[i].name = this.airlines[i].name
          this.options2[i].language = this.airlines[i].id 
          
        } 
     console.log('Les airlines : ', this.options2)
  

window.scrollTo(0,0);
       axios.get('https://safe-community.fr/lara/api/country')
  .then(response => {
    this.pays = response.data.countries
  }),
      axios.get('https://safe-community.fr/lara/api/category')
      .then(respon => {
        console.log('les categories: ',respon)
        this.category = respon.data
        this.options = respon.data
        for (let i = 0; i < this.category.length; i++) {
          this.options[i].name = this.category[i].name_category
          this.options[i].language = this.category[i].id 
          
        }
      })
      .catch(err => {
        console.log(err)
      })
 
      }

   /*  origin, destination, date_depart, date_arrive, description, package_cateory, kilo, price_kilo */

/* api/category */

    
}
</script>


 
<style scoped>
@import url('https://unpkg.com/vue-multiselect@2.1.0/dist/vue-multiselect.min.css');
</style>


