<script>
import axios from 'axios'
 import Multiselect from 'vue-multiselect'
 
export default {
   components: {
    Multiselect
  }, 
  data(){
    return{
       id:'',
       options: [
        { name: 'Lundi' },
        { name: 'Mardi' },
        { name: 'Mercredi',  },
        { name: 'Jeudi',  },
        { name: 'Vendredi' },
        { name: 'Samedi',  },
        { name: 'Dimanche',  },
       ],
       value : [],
       rvalue:'',
       admins: [
        ['Video1', 'mdi-pencil'],
        ['video avion', 'mdi-pencil'],
      ],
      cruds: [
        ['Comment envoyer', 'mdi-pencil'],
        ['comment ....', 'mdi-pencil'],
      ],
      livraisons: [],
      pictures:[],
      dialog:false,
      dialog2:false,
      dialog3:false,
      dialog4:false,
      select:['Image de présentation','Tutoriel video','Video de publicité','Point de livraison'],
      element : '',
      file1:'',
      file2:'',
      snackbar:false,
      txt:'',
      clickedpic:'',
      image1:{},
      pays:[],
      villes:[],
      choosenpays:{},
      choosencity:{},
      posta1:'',
      posta2:'',
      posta3:'',
      posta4:'',
      posta5:'',
      postal:'',
      namecheck:'',
      phonecheck:'',
      spinner:false,
      imgphoto:'',
      imgwebsite:'',
      spinner:false,
      title:'',
      website:'',
      imgtitle:'',


    }

  },
  
  methods:{

    filesupload1(event){
        this.file1 = event.target.files[0];
        console.log(this.file1);
      },
      
    filesupload2(event){
        this.file2 = event.target.files[0];
         console.log(this.file2);
      },

    allimages(){ // recuperer les images slides
      axios.get('https://safe-community.fr/lara/api/slider/pictures') 
        .then(response => {
          console.log('Images du stockage:',response)
          this.pictures = response.data
        })
        .catch(error => {
          console.log(error)
        })
    },

    addcheckpoint(){
      
      //mettre la liste des jours(value) dans un string séparés par une virgulle et dans les guillemets
      this.rvalue =''
          for (let i = 0; i < this.value.length; i++) {
          this.rvalue = this.value[i].name + ', ' + this.rvalue
        }
        ////
        if(this.choosenpays.name_country == 'Cameroun'){
      this.postal = this.posta5
    }
    else{
      this.postal = this.posta1 + ' ' + this.posta2 + ' ' + this.posta3 + ' ' + this.posta4
    }
    console.log('value :',  this.rvalue);
    this.spinner = true
    axios.post('https://safe-community.fr/lara/api/checkpoint/store',{
        name :    this.namecheck,
        address : this.postal,
        phone:    this.phonecheck,
        working_days : this.rvalue,
        city_id:this.choosencity,
      },{
         headers:{
            Authorization: "Bearer " + localStorage.getItem('token')
            } 
      })
      .then(response => {
         this.spinner = false
        console.log('Ajout checkpoint :', response)
        this.dialog = false,
        this.snackbar =true,
        this.txt = response.data.message
      })
