import Vue from 'vue'
import Vuex from 'vuex'
import auth from './auth'
import axios from 'axios'


Vue.use(Vuex)

export default new Vuex.Store({
    state: {
    count:0,
    assur:0,
    user : {},
    token: localStorage.getItem('token') || '',
    },

    mutations: {
        increment (state, n) {
            state.count = n
          },

          takeassur(state, n){
              state.assur = n
          },
          auth_success(state, token, user){
            state.status = 'success'
            state.token = token
            state.user = user
          },
          logout(state){
            state.token = ''
            localStorage.removeItem('token')
            localStorage.removeItem('user')
          },
       
    },

    getters:{
        count: state => state.count,
        isLoggedIn: state => !!state.token,
        assur: state =>state.assur
    },

    actions: {
        increment ({ commit },n){
            commit('increment',n)
        },
        takeassur({ commit },n){
            commit('takeassur',n)
        },
    },

    modules:{
        auth
    }

}) 



