<template>
  <div>
      <navbar></navbar>
       <div class="container p-4">

               <!--Section heading-->
        <h1 class="font-weight-bold text-center h1 my-5">Tous les voyages</h1>

       <!-- Card -->
       
        <mdb-card-group>
  <mdb-row>
  <div v-for="(value,i) in travel">
       <mdb-col>
			   	<mdb-card class="mb-4 grey lighten-2 ml-5 my-2 ">
          <mdb-card-header style="height:50px" class=" mdb-color indigo lighten white-text z-depth-1-half mb-n2 rounded">
                           <h4 class="title text-center font-weight-bold"> <mdb-icon class="mr-2" fas icon="user" />{{ value.name_user }} </h4>
                            <input type="text" v-model="voyages[i]">
                          </mdb-card-header>
          <!-- <img src="https://image.flaticon.com/icons/svg/190/190155.svg" alt="User Photo" class="mx-auto" style="height:100px;width: 170px"> -->
					<mdb-card-body>
						<mdb-card-title style="font-size:14px" class="text-center">
              <mdb-icon fas icon="clock" /> Depart le:<span class="font-weight-bold"> {{ value.date_depart | removeheure }} </span>
              </mdb-card-title>
  
          
            <div class="text-center"><img src="https://image.flaticon.com/icons/svg/1692/1692028.svg" alt="location icon" class="ml-2" style="width: 20px"><span style="font-size: 18px" >{{ value.origin }}</span></div>
            <div class="">
              <div class="text-center"><i class="fas fa-long-arrow-alt-down ml-4" style="font-size: 25px; font-color:red"></i></div>
            </div>
             <div class="text-center"><img src="https://image.flaticon.com/icons/svg/944/944557.svg" style="width:25px" alt="FLAG arrivée" class=""><span style="font-size: 20px" >{{ value.destination }}</span></div>
          
           <div class="text-center">
             <v-btn  depressed small  rounded dark color="blue darken-3" class="mt-2" @click="mod(travel[i])" >intéressé </v-btn>
           </div>
					</mdb-card-body>
           <mdb-card-footer color="" style="height:70px" class="pt-1 white-text indigo darken-1 text-center">
						<ul class="mt-0 list-unstyled list-inline font-small">
              	<li class="mt-0 list-inline-item">
								<mdb-icon fas icon="dollar-sign" style="font-size:12px" class="mr-1" />Prix du kilos : <span class="font-weight-bold"> {{ value.price_kilo }} €</span>
                </li>
							<li class="list-inline-item pr-2 white-text">
								<mdb-icon fas icon="balance-scale" class="" /> <span class="font-weight-bold">{{ value.kilo_available }} kg </span>disponibles
                </li>
							<li class="list-inline-item">
								<mdb-icon fas icon="plane-arrival" style="font-size:8px" class="" />Arrivée le : <span class="font-weight-bold">{{ value.date_arrive | removeheure }}</span>
                </li>
						</ul> 
					</mdb-card-footer>
				</mdb-card>
			</mdb-col>
      
    </div>
    </mdb-row>
        </mdb-card-group>
      
<!-- Card deck -->
            <mdb-modal scrollable removeBackdrop side position="left" direction="left" :show="hide" @close="hide = false" info>
      <mdb-modal-header>
        <mdb-modal-title>Souscription au voyage de <span class="font-weight-bold">{{ name }} </span></mdb-modal-title>
      </mdb-modal-header>
      <mdb-modal-body>
        <mdb-row>
          <mdb-col col="3" class="text-center"><mdb-icon icon="shopping-cart" size="4x"/></mdb-col>
          <mdb-col col="9">
            <p>
              <div class="my-3"><span style="font-weight:bold">Ville de Départ : </span> {{ origin }}</div>
        <div class="my-3"><span style="font-weight:bold">Ville de D'arrivée : </span> {{ destination }}</div>
        <div class="my-3"><span style="font-weight:bold">Date de Départ : </span> {{ date_depart }} </div>
              <div class="my-3"><span style="font-weight:bold">Date d'arrivée : </span>{{ date_arrive }}</div>
              <div class="my-3"><span style="font-weight:bold">Compagnie aérienne : </span>{{ compagnie }}</div>
              <div class="my-3"><span style="font-weight:bold">Prix/kg (Eur) : </span>{{ prix }} ({{ prix * 655 }} FCFA)</div>
              <div class="my-3"><span style="font-weight:bold">Quantité disponible : </span>{{ kilos }} Kg</div>
             <div class="my-3"> <span style="font-weight:bold">Catégorie d'objets a tranporter :  </span>{{categorie }}</div>
              <div class="my-3"><span style="font-weight:bold">Plus d'informations : </span>{{ details }}</div>
              
            </p>
          </mdb-col>
        </mdb-row>
      </mdb-modal-body>
      <mdb-modal-footer center>
        <mdb-btn color="success" @click="subs= true">Chosir <mdb-icon icon="gem" far class="ml-1" color="white"/></mdb-btn>
        <mdb-btn outline="info" @click="hide = false">Non, merci</mdb-btn>
      </mdb-modal-footer>
    </mdb-modal>
    <mdb-modal :show="subs" @close="subs = false" info>
        <form @submit.prevent="submit">
      <mdb-modal-header>
        <mdb-modal-title>Demande de réservation</mdb-modal-title>
      </mdb-modal-header>
      <mdb-modal-body>
         <mdb-alert color="primary">
      {{ kilos }} Kg à {{ prix }} €/Kg
    </mdb-alert>
    <label class="font-weight-bold">Poids de votre colis *</label>  <i style="font-size:12px" class="fa fa-info my-0 ml-1"></i><a href="#" class="ml-1" style="opacity:7 v0%;font-size:15px" @click.prevent="infocolis=true">Info</a><br>
     <mdb-modal size="sm" :show="infocolis" @close="infocolis = false">
        <mdb-modal-header>
            <mdb-modal-title class="font-weight-bold">Poids du colis</mdb-modal-title>
        </mdb-modal-header>
        <mdb-modal-body>Vous devez <span class="font-weight-bold">majorer</span> le poids de votre colis lors de l'enregistrement de cette demande, car si il est supérieur au point de dépôt, des pénalités vous seront imputées<br>
</mdb-modal-body>
        <mdb-modal-footer>
      <mdb-btn color="secondary" type="button" size="sm" @click.native="infocolis = false">Compris</mdb-btn>
        </mdb-modal-footer>
    </mdb-modal>
    <input type="number" min=0 step="0.5" :max='kilos' @change="price" v-model="ask_kilo" class="form-control" required> <br>

    <label class="font-weight-bold">Tarif total</label> <br>
    <span>{{ tarif }}€  ({{ tarif * 655 }} FCFA)</span> <br><br>

      <label class="font-weight-bold">Point de depot du colis </label><br>
      <span style="font-size:15px" > {{ checkpointdep.city_name }} {{ checkpointdep.address }} {{ checkpointdep.phone }} </span><br><br>

      <label class="font-weight-bold">Point de retrait du colis par defaut</label><br>
     <span style="font-size:15px" > {{ checkpointarr.city_name }} {{ checkpointarr.address }} {{ checkpointarr.phone }} </span><br><br>

    <label class="font-weight-bold">Contenu de votre colis *</label>
    <textarea class="form-control" onkeyup="this.value=this.value.replace(/\d+/g, '')" v-model="contenu" cols="20" rows="5" required/><div class="custom-control custom-checkbox">
      <input type="checkbox" class="custom-control-input" id="defaultUnchecked" required> <br>
      <label class="custom-control-label" for="defaultUnchecked"> Valider les  <router-link to="login" >condition générales d'utilisation.</router-link>  <span class="red-text font-weight-bold"> La valeur maximale d’indemnisation en cas de sinistre c’est 300 euros.</span></label>
  </div>

      </mdb-modal-body>
      <mdb-modal-footer center>
        <div v-bind:class="{'spinner-border': spinner}" class="" role="status"></div>
        <mdb-btn color="success" type="submit" >Valider</mdb-btn>
        <mdb-btn outline="info" @click="nonsubs">Annuler</mdb-btn>
      </mdb-modal-footer>
      </form>
    </mdb-modal>
      <!-- Frame Modal Bottom -->
   <mdb-modal frame position="bottom" direction="bottom" :show="modal" @close="modal = false, spinner=false">
        <mdb-modal-body class="text-center">
            <span><span class="font-weight-bold">Demande de réservation envoyée.</span> Vous aurez une réponse par mail sur la validation ou non par le voyageur dans de brefs délais</span>
            <mdb-btn color="secondary"  @click.native="spinner=false,hide=false, subs = false, modal = false">Fermer</mdb-btn>
        </mdb-modal-body>
    </mdb-modal>

       </div>
       <!-- <pagination-page :settings="settings">
      <div slot="page" slot-scope="item">
        <div> {{item.originalEvent.data}} </div>
      </div>
    </pagination-page>
    <pagination-control :settings="settings" /> -->
    
    
       <myfooter></myfooter>
  </div>

</template>

<script>

import { PaginationControl, PaginationPage } from 'vue-smart-pagination'
import navbar from './navbar'
import axios from 'axios'
import myfooter from './footer'
export default {
    components: {myfooter,navbar, PaginationControl, PaginationPage },
    data(){
        return{
            travel:{},
            voyages:[],
             pageOfItems: [],
            infocolis:false,
            contenu:'',
            hide:false,
            modal:false,
            subs:false,
            success:'',
            alert:'',
            message:'',
            name:'',
            ask_kilo:'',
            tarif:'0',
            origin:'',
            destination:'',
            date_arrive:'',
            date_depart :'',
            kilos:'',
            details:'',
            categorie:'',
            successMessage:'',
            alertSuccess:'',
            prix:'',
            id_post:'',
            spinner:false,
            checkpointdep:'',
            choosencheckdep:'',
            choosencheckarrive:'',
            checkpointarr:{},
            checkpointdep:'',
            id_cityarr:'',
            id_citydep:'',

            
        }
    },

    computed: {
    settings: function () {
      return {
        voyages: this.voyages
      }
    }
  },

    filters :{
      removeheure(value){  //pour enlever l'heure de la date
         value = value.substring(0, 10);
         return value
      }
    },

    methods:{
      
       onChangePage(pageOfItems) {
            // update page of items
            this.pageOfItems = pageOfItems;
        },

      price(){
        let n = this.ask_kilo - Math.floor(this.ask_kilo) //Recuperer lapartie décimale du nombre
        console.log('la partie decimal', n)
         if ( !(n == 0) && !(n == 0.5)) {
           if ( n < 0.5) {
              this.ask_kilo = this.ask_kilo - n
           }
           else{
              this.ask_kilo = this.ask_kilo - n
          this.ask_kilo = this.ask_kilo + 0.5
           }
           }
        this.tarif = this.ask_kilo * this.prix
        if(this.ask_kilo > this.kilos){
           this.ask_kilo = this.kilos
           this.tarif = this.ask_kilo * this.prix
        }
      },
      mod(info){
         
        this.checkpointarr =  JSON.parse( info.checkpoint_arr)
        this.checkpointdep = JSON.parse( info.checkpoint_dep)
        this.id_post = info.id
        this.id_cityarr = info.id_cityarr
        this.id_citydep = info.id_citydep
        this.name = info.name_user
        this.origin = info.origin
        this.destination = info.destination
        this.compagnie = info.compagnie
        this.date_depart = info.date_depart
        this.date_arrive = info.date_arrive
        this.kilos = info.kilo_available
        this.details = info.description
        this.prix = info.price_kilo
        this.categorie = info.package_category
        this.hide = true
        console.log('Les infos',info)
        console.log('Le nom',this.name)
        console.log('id ville de depart : ', info.id_citydep)
      },
      submit(){
        console.log('Kilos demandés:',this.ask_kilo)
        console.log('Tarif:',this.tarif)
        this.spinner = true
        axios.post('https://safe-community.fr/lara/api/reservation',{
          category:this.categorie,
          kilo:this.ask_kilo,
          description:this.contenu,
          amount:this.tarif,
          checkpoint_dep: this.choosencheckdep,
          checkpoint_arr: this.checkpointarr.id,
          post_id:this.id_post
        },{headers:{
          Authorization: 'Bearer ' + localStorage.getItem('token')
        }
        })
        .then(response => {
          console.log(response)
          if(response.status == 200){
            this.spinner = false,
            this.modal = true,
            this.ask_kilo = ''
            this.tarif = ''
            this.description = ''
            this.contenu = ''
          }
        })
      }
    },
    created(){
        axios.get('https://safe-community.fr/lara/api/allposts')
        .then(response => {
             console.log(response)
            console.log(response.data)
            this.travel = response.data
        })
    },
    computed:{

    }
}
</script>

<style scoped>
  .card {
  display: flex;
  height: 280px;
  width: 200px;
  background-color: #17141d;
  border-radius: 10px;
  box-shadow: -1rem 0 3rem #000;
/*   margin-left: -50px; */
  transition: 0.4s ease-out;
  position: relative;
  left: 0px;
}

.card:not(:first-child) {
    margin-left: -50px;
}

.card:hover {
  transform: translateY(-20px);
  transition: 0.4s ease-out;
}

.card:hover ~ .card {
  position: relative;
  left: 50px;
  transition: 0.4s ease-out;
}

.card:hover .filledbar {
  width: 120px;
  transition: 0.4s ease-out;
}
.card:hover .stroke {
  stroke-dashoffset: 100;
  transition: 0.6s ease-out;
}
</style>


