<template>
  <div id="app">
      <transition name="slide" mode="out-in">
            <router-view/>
      </transition>
       <div class="footer">
         <router-view name="footer"/>
       </div>
         <mdb-modal size="sm" :show="modal" @close="logout()">
        <mdb-modal-header>
            <mdb-modal-title class="font-weight-bold">Déconnexion</mdb-modal-title>
        </mdb-modal-header>
        <mdb-modal-body>Votre session a expiré vous allez etre déconnecté!
</mdb-modal-body>
        <mdb-modal-footer>
      <mdb-btn color="secondary" size="sm" @click="logout()">Ok</mdb-btn>
        </mdb-modal-footer>
    </mdb-modal>
  </div>
</template>


<script>
import axios from 'axios'
export default {

  data(){
    return{
      modal : false
    }
  },
  
  methods: {
    
      logout(){
          this.$router.push({name : 'login'})
          this.modal=false
      }
  },

  mounted(){


  },
 created(){
     
 
       axios.interceptors.response.use(
  res => res,
  err => {
    if (err.response.data.error.message == 'Token has expired') {
     this.modal = true
    }
    throw err;
  }
);

  },
 
}

</script>

<style>

@import url('https://fonts.googleapis.com/css?family=Roboto+Condensed');

html, body {
  font-family: 'Roboto Condensed', sans-serif;
}

#app {
  font-family: 'Roboto Condensed', sans-serif;
}


.slide-enter-active,
.slide-leave-active{
  transition: opacity 0.5s, transform 0.5s
}

.slide-enter,
.slide-leave-to{
  opacity: 0;
  transform:translateX(-30%)
}
</style>



