<template>
<div>
    <v-app style="background-color:rgb(236,236,237)">
        <v-dialog
    v-model="dialog"
    max-width="290"
    >
      <v-card>
        <v-card-title color="red" class="headline"> Modifier le statut </v-card-title>
        <v-card-text>
        <v-select
                :items="select"
                v-model="statut"
                label="Choisissez un statut"
                ></v-select>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
        <v-btn color="blue darken-1" text @click="editStatut()">Continuer</v-btn>
        <v-btn color="red" text @click="dialog = false">Annuler</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
   
  <section class="mt-5">
     <h3 class="font-weight-bold text-center h3 my-3 mt-5">Paiements</h3>
     <mdb-card>
    <mdb-tabs class="mb-5 my-2 mx-2"
    :active="0"
    default
    :links="[
      { text: 'Reçu', slot: 'custom-slot' },
      { text: 'Reglements' },
      { text: 'Remboursement' }]"
      
    :transition-duration="0.5"
    transition-style="linear"
  >
  <template :slot="'custom-slot'">
    <v-data-table
    :headers="headers"
    :items="receipt"
    sort-by="Date"
    class="elevation-1"
  >

  </v-data-table>
    </template>
    <template :slot="'Reglements'">
    <v-data-table
    :headers="headers"
    :items="reglements"
    sort-by="Date"
    class="elevation-1"
  >
  </v-data-table>
    </template>

    <template :slot="'Remboursement'">
    <v-data-table
    :headers="headers"
    :items="remboursements"
    sort-by="Date"
    class="elevation-1"
  >
  </v-data-table>
    </template>
    
    
  </mdb-tabs>
  </mdb-card>
  </section>
  
  <section>
    <h3 class="font-weight-bold text-center h3 mb-3 mt-5">Voyages</h3>
  <v-card>
    <v-card-title>
      <v-text-field
        v-model="search2"
        label="Rechercher un utilisateur"
        single-line
        hide-details
      ></v-text-field>
    </v-card-title>
    <v-data-table
      :headers="headers3"
      :items="voyages"
      :search="search2"
    >
    </v-data-table>
  </v-card>
  </section>
 
  <section>
    <h3 class="font-weight-bold text-center h3 mb-3 mt-5">Réservations </h3>
  <v-card>
    <v-card-title>
      <v-text-field
        v-model="search3"
        label="Rechercher un utilisateur"
        single-line
        hide-details
      ></v-text-field>
    </v-card-title>
    <v-data-table
      :headers="headers2"
      :items="reservations"
      :search="search1"
    >
    </v-data-table>
  </v-card>
  </section>
<v-snackbar
      v-model="snackbar"
    >
      {{ txt }}
      <v-btn
        color="red"
        text
        @click="snackbar = false"
      >
        Fermer
      </v-btn>
    </v-snackbar>
     </v-app>
    </div>
</template>

<script>
import axios from 'axios'
export default {
    data(){
        return{
             headers: [
                        {
                        text: "Motif de la transaction",
                        align: 'start',
                        sortable: false,
                        value: 'user_number',
                        
                        },
                        { text: "#Numéro de  la transaction", value: 'postuser_number' },
                        { text: "#Numéro de reférence", value: 'publisher_name' },
                        { text: 'Crediteur', value: 'depart' },
                        { text: "Debiteur", value: 'arrive' },
                        { text: 'Moyen de paiement', value: 'date_depart' },
                        { text: 'Montant', value: 'amount' },
                        { text: "Statut", value: 'statut' },
                        { text: '', value: 'actions', sortable: false },
                    ],
            headers2: [
                    {
                text: "#Numéro de la réservation",
                align: 'start',
                sortable: false,
                value: 'user_number',
                
                },
                 { text: "Nom de l'utilisateur", value: 'depart' },
                { text: 'Ville de départ', value: 'depart' },
                { text: "Ville de d'arrivée", value: 'arrive' },
                { text: 'Date de départ', value: 'date_depart' },
                { text: "Date de d'arrivée", value: 'date_arrive' },
                { text: 'Poids du colis(Kg)', value: 'kilo' },
                { text: 'Prix du kilos(€)', value: 'price_kilo' },
                { text: 'Détails', value: 'description' },
                { text: 'Cout global de la réservation(€)', value: 'amount' },
                { text: '', value: 'actions', sortable: false },
                ],
            headers3: [
                {
                  text: '#numéro du voyage',
                  align: 'start',
                  sortable: false,
                  value: 'user_number',
                },
                { text: "Nom de l'utilisateur", value: 'name_user' },
                 { text: "Email de l'utilisateur", value: 'email' },
                { text: 'Ville de départ', value: 'origin' },
                { text: "Ville d'arrivée", value: 'destination' },
                { text: 'Date de départ', value: 'date_depart' },
                { text: "Date de d'arrivée", value: 'date_arrive' },
                { text: 'Prix du kilo(€)', value: 'price_kilo' },
                { text: 'Kilos de départ', value: 'kilo' },
                { text: '', value: 'actions', sortable: false },
              ],
            select:['réglé'],
            reservations:[],
            voyages:[],
            reglements:[],
            search1: '',
            search2: '',
            search3: '',
            dialog:false,
            statut:[],
            snackbar : false,
            txt:'',
            reservations:[],
            remboursements:[],
            receipt : [],
        }
    },

    methods : {

        editReservation(){
             axios.post('https://safe-community.fr/lara/api/s_insurance/annuler',{
            statut : this.statut
            },{
        headers:{
            Authorization: "Bearer " + localStorage.getItem('token')
            } 
    })
        },

        editVoyage(){
             axios.post('https://safe-community.fr/lara/api/s_insurance/annuler',{
            statut : this.statut
            },{
        headers:{
            Authorization: "Bearer " + localStorage.getItem('token')
            } 
    })
        },

        Allreglements(){
             let user = JSON.parse(localStorage.getItem('user')) 
          axios.get('https://safe-community.fr/lara/api/admin/reglements',{
            headers:{
            Authorization: "Bearer " + localStorage.getItem('token')
            } 
          })
          .then(response => { 
            console.log('Les regelemnents : ',response);      
            this.reglements = response.data
          })
        },

        Allremboursements(){
             let user = JSON.parse(localStorage.getItem('user')) 
          axios.get('https://safe-community.fr/lara/api/admin/remboursements',{
            headers:{
            Authorization: "Bearer " + localStorage.getItem('token')
            } 
          })
          .then(response => { 
            console.log('Les remboursements : ',response);      
            this.remboursements = response.data
          })
        },
      
        allreservations(){
          let user = JSON.parse(localStorage.getItem('user')) 
          axios.get('https://safe-community.fr/lara/api/allreservations',{
            headers:{
            Authorization: "Bearer " + localStorage.getItem('token')
            } 
          })
          .then(response => {
            console.log('Toutes les reservations:',response);          
            this.reservations = response.data
          })
        },

            reçu(){
          let user = JSON.parse(localStorage.getItem('user')) 
          axios.get('https://safe-community.fr/lara/api/admin/recu',{
            headers:{
            Authorization: "Bearer " + localStorage.getItem('token')
            } 
          })
          .then(response => { 
            console.log('Les reçu : ',response);      
            this.receipt = response.data
          })
        },

        allvoyages(){
          let user = JSON.parse(localStorage.getItem('user')) 
          axios.get('https://safe-community.fr/lara/api/alltrips',{
            headers:{ 
            Authorization: "Bearer " + localStorage.getItem('token')
            } 
          })
          .then(response => {
            console.log('Tous les voyages : ',response);
            this.voyages = response.data
          })
        },

        editStatut(){
          axios.post('https://safe-community.fr/lara/api/admin/t_valider',{
            id: this.id
          },{
              headers:{
            Authorization: "Bearer " + localStorage.getItem('token')
            } 
          })
          .then(response => {
            this.dialog = false
            this.snackbar = true,
            this.txt = response.data.message
          })
          .catch(error => {
            this.dialog = false
            this.snackbar =true,
            this.txt = eror.response.data.message
          })
        }
      
    },

    created(){
      this.reçu()
      this.allvoyages()
      this.allreservations()
      this.Allreglements()
      this.Allremboursements()
    }


}
</script>

<style>

</style>


