<template>
<div>
     <mdb-navbar expand="xl" class="mycolor" hamburger togglerIcon="paper-plane" position="top" far dark color="indigo darken-2">
      <!-- Navbar brand -->
      <mdb-navbar-brand class="text-white">Safe Community</mdb-navbar-brand> 
      <div class="col"></div>   
    <span class="white-text" style="opacity:50%">Menu</span>
      <mdb-navbar-toggler>
        <mdb-navbar-nav right>
           <router-link  v-if="!token" :to="{name: 'login'}" ><mdb-nav-item href="#" waves-fixed>Se connecter</mdb-nav-item></router-link>
          <router-link :to="{name: 'contact'}" ><mdb-nav-item href="#" waves-fixed>Aide</mdb-nav-item></router-link>
          <router-link :to="{name: 'home'}" ><mdb-nav-item href="#" waves-fixed>Accueil</mdb-nav-item></router-link>
          <router-link  v-if="!token"  :to="{name: 'register'}" > <mdb-nav-item href="#" waves-fixed>Inscription</mdb-nav-item></router-link>
        </mdb-navbar-nav>
      </mdb-navbar-toggler>
    </mdb-navbar>
  <section style="background:rgb(245,245,245); padding-bottom: 100px">
    <!-- Purple Header -->

    <!-- Card Container -->
   <mdb-container class="mt-5">
     <h1 class="font-weight-bold text-center h1 my-5">Laissez votre message ici 😊</h1>
   <mdb-card>
     <form  @submit.prevent="contact">
    <mdb-card-header class="pt-4 amy-crisp-gradient">
      <mdb-card-title><strong></strong></mdb-card-title>
      L'adhésion à Safe-community.fr se fait uniquement par parrainage. Si vous souhaitez avoir un parrain ou alors si vous avez des questions ou des préoccupations, n'hésitez pas à nous écrire 😉
    </mdb-card-header>
    <mdb-card-body class="mb-5">
      <mdb-row>
        <mdb-col>
          <mdb-input label="Nom*" v-model="name" icon="user" required/>
        </mdb-col>
        <mdb-col>
          <mdb-input label="Email*" v-model="email" type="email" icon="envelope" required/>
        </mdb-col>
      </mdb-row>
   <mdb-col>
          <mdb-input icon="tag" v-model="subject" type="text" label="Sujet *" required/>
        </mdb-col>
      <mdb-row>
        <mdb-col>
          <mdb-input type="textarea" v-model="message" label="Message...*" icon="pen" :rows="4" required/>
        </mdb-col>
      </mdb-row>
      <i style="font-size:12px" class="fa fa-info my-0 ml-5 "></i><a href="#" class="ml-1" style="opacity:7 v0%;font-size:15px" @click.prevent="modal=true">Info sur les fichiers</a><br>
      
        
         
   <!--  <div class="input-group-prepend">
      <span class="input-group-text" id="inputGroupFileAddon01">Upload</span>
    </div> -->
    <mdb-row v-if="show == true">
      <div class="col-1"><mdb-btn v-if="filleadd < 1 " type="button" color="red" @click="annuler1, show = false" style="font-size:11px" class="ml-n1 mb-0 p-1">Annuler</mdb-btn></div>
    <div class="col ml-5 mb-2 mt-0 custom-file">
     <input type="file" id="file1" class="custom-file-input" @change="filesupload1"/>
      <label class="custom-file-label" style="border-right:hidden;borber-bottom:solid;border-top:hidden;border-left:hidden;border-radius:0px" for="inputGroupFile01">{{file1.name}}</label>
    </div>
    </mdb-row>
    <mdb-row  v-if="filleadd >= 1 ">
       <div class="col-1"><mdb-btn v-if="filleadd < 2 " color="red" @click="annuler2" style="font-size:11px"  class="ml-n1 mb-0 p-1">Annuler</mdb-btn></div>
      <div class="col ml-5 mb-2 mt-0 custom-file">
       <input type="file" id="file2" class="custom-file-input" @change="filesupload2"/>
      <label class="custom-file-label" style="border-right:hidden;borber-bottom:solid;border-top:hidden;border-left:hidden;border-radius:0px" for="inputGroupFile01">{{file2.name}}</label>
    </div>
    </mdb-row>
    <mdb-row  v-if="filleadd >= 2 " >
       <div class="col-1"><mdb-btn color="red" @click="annuler3" style="font-size:11px"  class="ml-n1 mb-0 p-1">Annuler</mdb-btn></div>
     <div class="col ml-5 mt-0 custom-file">
       <input type="file" id="file3" class="custom-file-input"  @change="filesupload3"/>
      <label class="custom-file-label" style="border-right:hidden;borber-bottom:solid;border-top:hidden;border-left:hidden;border-radius:0px" for="inputGroupFile01">{{file3.name}}</label>
    </div>
    </mdb-row>
     <mdb-row>
      <div class="col-3"></div>
      <div v-if="show == true">
      <mdb-btn  v-if="filleadd < 2 " type="button" color="green" @click="add" style="font-size:11px" class="ml-n1 mb-0 p-1"> <i style="font-size:12px" class="fa fa-plus mx-1"></i>AJouter un autre fichier</mdb-btn>
    </div>
    </mdb-row>
    <div class="row justify-content-center mt-3">
   <v-btn
   v-if="show == false"
      :loading="loading5"
      :disabled="loading5"
      color="grey"
      class="ma-2 white--text"
      fab
      @click="show = true"
    >
      <v-icon dark>mdi-cloud-upload</v-icon>
    </v-btn>
     <span  v-if="show == false" class="mt-3 ml-2"> Ajouter un fichier </span>
    </div>
  
 <!--  <a @click="files = '', name_file = ''" style="opacity:70%" class="red-text ml-5">Retirer fichier</a>  -->
  <!-- 
    -->
    </mdb-card-body>
     <div class="alert text-center" v-bind:class="{'alert-success': success, 'alert-danger': alert} "> {{ Alertmessage }} </div>
    <mdb-card-footer class="white d-flex justify-content-end">
      <div v-bind:class="{'spinner-border': spinner}" class="mt-3" role="status"></div>
          <span class="red-text" style="font-size:12px" > <span style="font-size:15px" class="font-weight-bold">*</span> : Champs obligatoires</span>
      <mdb-btn gradient="amy-crisp" class="black-text" type="submit" icon="paper-plane" rounded>Envoyer</mdb-btn>
    </mdb-card-footer>
    </form>
  </mdb-card>
  </mdb-container>
    <!-- /.Card Container -->
  </section>
  <myfooter></myfooter>
  <mdb-modal size="sm" :show="modal" @close="modal = false">
        <mdb-modal-header>
            <mdb-modal-title class="font-weight-bold">Fichiers</mdb-modal-title>
        </mdb-modal-header>
        <mdb-modal-body>Les extensions acceptées sont <span class="font-weight-bold">pdf, docs, doc, png, jpeg ou jpg.La taille maximale doit etre de 7Mo</span><br>
        Le nombre maximale de fichier est de 3
</mdb-modal-body>
        <mdb-modal-footer>
      <mdb-btn color="secondary" size="sm" @click.native="modal = false">Compris</mdb-btn>
        </mdb-modal-footer>
    </mdb-modal>
  </div>
</template>

<script>
import navbar from './navbar'
import axios from 'axios'
import myfooter from './footer'

  export default {
    components:{ navbar, myfooter },
    data(){
        return{
          name_file:'Choisir un fichier',
           name:'',
           show:false,
           modal:false,
           email:'',
           subject:'',
           email:'',
           Alertmessage:'',
           spinner:false,
           success:false,
           message:'',
           alert:false,
           file1:{},
           file2:'',
           file3:'',
           ann1:false,
           ann2:false,
           ann3:false,
           filleadd:0,
           token:'',
        }
    },
    methods:{

      annuler1(){
       this.file1 = ''
    },

    annuler2(){
       this.filleadd = 0
       this.file2 = ''
       this.file3 = ''
    },
     annuler3(){
      this.filleadd = 1
      this.file3 = ''
    },

     redirect(){
      let router =  this.$router 
       setTimeout( function () {
                router.push({ name: 'home' })
              },  3000);
      },


      add(){
        if (this.filleadd >=2 ) {
       this.filleadd =   2
        }
        this.filleadd = this.filleadd + 1;
        console.log('filleadd', this.filleadd)
        console.log('ann2:', this.ann2)
        console.log('ann3:', this.ann3)
      },
      filesupload1(event){
        this.file1 = event.target.files[0];
         console.log('fichier 1 :', this.file1)

         
      },
       filesupload2(event){
       this.file2 = event.target.files[0];
         console.log('fichier 2 :', this.file2)

      },
       filesupload3(event){
        this.file3 = event.target.files[0];
         console.log('fichier 3 :', this.file3)

      },
       contact(){
          let formData = new FormData();
          formData.append('file1', this.file1)
          formData.append('file2', this.file2)
          formData.append('file3', this.file3)
          formData.append("name",this.name );
        formData.append("email",this.email );
        formData.append("subject",this.subject );
        formData.append("message",this.message );
        console.log('Les données envoyées : ',formData)
        for (var value of formData.values()) {
   console.log(value); 
}
       this.spinner = true
       this.Alertmessage=""
       this.alert= false
       this.success=false
       axios.post("https://safe-community.fr/lara/api/contactUs",formData,
        {
         headers: {
             'Content-Type': 'multipart/form-data'
         }
        })
       .then(response => {
         console.log(response)
         if(response.status == 200){
           this.redirect()
           console.log(response)
           this.file1 = ''
           this.file2 =''
           this.file3 =''
           this.name = ''
           this.email =''
           this.subject = ''
           this.message =''
            this.name_file = 'Choisir un fichier',
             this.success = true
             this.Alertmessage = "Message envoyé"
             this.spinner = false
             
             
             }
           
       })
       .catch(error => {
         console.log(error)
        if (error.response.data.message) {
            this.spinner = false
            this.alert = true
            this.Alertmessage = error.response.data.message
          }
       })
     }
    },
    created(){
      this.token = localStorage.getItem("token")
    }
  }
</script>

<style scoped>
  
</style>


