<template>
<div>
  <v-app>
   <mdb-alert v-if="!idcard" class="mt-3 text-center" color="warning">
   <strong class="font-weight-bold"> Veuillez ajouter un justificatif de piece d'dentité !</strong>
    </mdb-alert>  
  <mdb-modal :show="modal" @close="modal = false">
            <mdb-modal-header>
                <mdb-modal-title>Ajouter un filleul</mdb-modal-title>
            </mdb-modal-header>
          <mdb-card-body class="grey-text text-center">
               <div class="alert" v-bind:class="{'alert-success': success2, 'alert-danger': alert2} "> {{ message2 }} </div>
            <form @submit.prevent="addgodson">
                              <mdb-input size="sm" placeholder="Adresse Email du filleul" v-model="email_godson" group type="email" validate error="wrong" success="right" required />
                              <div v-bind:class="{'spinner-border': spinner2}" class="" role="status"></div>
                              <mdb-btn size="sm" class="rounded">Ajouter</mdb-btn>
            </form>
                          </mdb-card-body> 
            <mdb-modal-footer  style="height:50px" >
                <mdb-btn size="sm" class="rounded" color="danger" @click.native="modal = false, success2 = false, alert2 = false, message2 = ''">Annuler</mdb-btn>
            </mdb-modal-footer>
        </mdb-modal>

        <mdb-modal :show="modal2" @close="modal2 = false" scrollable>
      <mdb-modal-header>
        <mdb-modal-title>Tous mes filleuls</mdb-modal-title>
      </mdb-modal-header>
      <mdb-modal-body>
        <mdb-list-group flush v-for="godson in godsons" v-bind:key="godson.email_godson">
    <mdb-list-group-item>{{ godson.email_godson }} inscrit le {{godson.created_at}} </mdb-list-group-item>
  </mdb-list-group>
        </mdb-modal-body>
      <mdb-modal-footer  style="height:50px" >
                <mdb-btn size="sm" class="rounded" color="danger" @click.native="modal2 = false">Fermer</mdb-btn>
            </mdb-modal-footer>
    </mdb-modal>
     <v-dialog
    v-model="modal3"
    max-width="290"
    >
      <v-card>
        <v-card-title color="red" class="headline p-2 ml-1"> Supprimer mon compte </v-card-title>
        <v-card-text>
          Cette action est irreversible etes vous sur de votre choix?
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
        <v-btn color="red darken-1" text  @click="deleteaccount()">Continuer</v-btn>
        <v-btn color="blue" text @click="modal3 = false">Annuler</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

         <mdb-modal :show="modal4" @close="modal4 = false">
            <mdb-modal-header>
                <mdb-modal-title>Ajouter une photo</mdb-modal-title>
            </mdb-modal-header>
          <mdb-card-body class="grey-text text-center">
              <div class="alert" v-bind:class="{'alert-success': success, 'alert-danger': alert} "> {{ msg }} </div>
            <form @submit.prevent="putphoto">
       <div class="custom-file">
     <input type="file" id="file1" class="custom-file-input" @change="filesupload1"/>
      <label class="custom-file-label" style="border-right:hidden;borber-bottom:solid;border-top:hidden;border-left:hidden;border-radius:0px" for="inputGroupFile01"> {{file1.name}} </label>
    </div>
    <mdb-btn size="sm" class="mt-4 rounded">Ajouter</mdb-btn> <div v-bind:class="{'spinner-border': spinner}" class="mt-3" role="status"> </div>
            </form>
            
                          </mdb-card-body> 
            <mdb-modal-footer  style="height:50px" >
                <mdb-btn size="sm" class="rounded" color="danger" @click.native="modal4 = false">Annuler</mdb-btn>
            </mdb-modal-footer>
        </mdb-modal>
        <mdb-modal :show="modal5" @close="modal5 = false">
            <mdb-modal-header>
                <mdb-modal-title>Supprimer photo</mdb-modal-title>
            </mdb-modal-header>
            <mdb-modal-body>
            <span class="font-weight-bold"></span> etes vous surs?
            </mdb-modal-body>
            <mdb-modal-footer  style="height:50px" >
                <mdb-btn size="sm" class="rounded" color="primary" @click.native="modal5 = false">Annuler</mdb-btn>
                <mdb-btn size="sm" class="rounded" color="danger" @click="deletephoto()">Oui</mdb-btn>
            </mdb-modal-footer>
        </mdb-modal>

          <mdb-modal :show="modal6" @close="modal6 = false">
            <mdb-modal-header>
                <mdb-modal-title>Ajouter piece d'identité </mdb-modal-title>
            </mdb-modal-header>
            <mdb-modal-body>
            <span class="font-weight-bold"></span> Vous allez ajouter une piece d'identité,verifiez bien votre choix, toute modification passera par les administrateurs
            </mdb-modal-body>
            <mdb-modal-footer  style="height:50px" >
                <mdb-btn size="sm" class="rounded" color="primary" @click.native="modal6 = false">Annuler</mdb-btn>
                <mdb-btn size="sm" class="rounded" color="danger" @click="uploadpiece()">Oui</mdb-btn>
            </mdb-modal-footer>
        </mdb-modal>

         <mdb-modal :show="modal7" @close="modal7 = false">
            <mdb-modal-header>
                <mdb-modal-title>Ajouter Justificatif de domiciliation </mdb-modal-title>
            </mdb-modal-header>
            <mdb-modal-body>
            <span class="font-weight-bold"></span> Continuer?
            </mdb-modal-body>
            <mdb-modal-footer  style="height:50px" >
                <mdb-btn size="sm" class="rounded" color="primary" @click.native="modal7 = false">Annuler</mdb-btn>
                <mdb-btn size="sm" class="rounded" color="danger" @click="uploadjustif()">Oui</mdb-btn>
            </mdb-modal-footer>
        </mdb-modal>
       

   <v-bottom-navigation class="mb-4"
    color="red"
  >
    <v-btn @click="modal = true" value="favorites">
      <span>Ajouter filleul</span>
      <v-icon>mdi-plus</v-icon>
    </v-btn>

    <v-btn @click="modal2 = true" value="nearby">
      <span>Mes filleuls</span>
      <v-icon>mdi-book</v-icon>
    </v-btn>

    <v-btn @click="modal3 = true">
      <span class="red-text">Supprimer mon compte </span>
      <v-icon>mdi-delete</v-icon>
    </v-btn>

  </v-bottom-navigation>
 <mdb-row>
			<mdb-col sm="4">
				<mdb-card narrower>
          <mdb-card-header class="mdb-color lighten-3 white-text mb-2 z-depth-1-half mb-3 mx-4 mt-n2 mb-n2 rounded">
                            <h4 class="title text-center font-weight-bold"><span class="black-text">Photo</span> de profil</h4>
                          </mdb-card-header>
<img data-v-11613719="" data-v-732af18d="" style="width:150px" :src="photo" alt="User photo" class="z-depth-1 mb-3 mx-auto">
       <v-icon @click="modal5=true" color="red" >mdi-delete</v-icon>
					<mdb-card-body>
						<mdb-card-title class="text-center h-25 ">{{ prenom }} {{nom}}</mdb-card-title>
						<hr />  
            <p class="my-0 text-center">
            <mdb-btn size="md" @click="modal4 = true" class="blue-gradient rounded-pill center">Importer photo</mdb-btn>
             </p>
					</mdb-card-body>
				</mdb-card>
			</mdb-col>
      <mdb-col >
        <form @submit.prevent="upgrade">
    <mdb-card cascade class="text-left">
                          <mdb-card-header class="mdb-color lighten-3 white-text z-depth-1-half mb-3 mx-4 mt-n3 mb-n2 rounded">
                            <h4 class="title text-center font-weight-bold"><fa class="fas fa-pencil-alt" />Mes coordonnées</h4>
                          </mdb-card-header>
                          
                          <mdb-card-body class="grey-text">
                            <mdb-input size="sm" icon="user" placeholder="Nom" v-model="nom" group type="text" validate error="wrong" success="right"/>
                            <mdb-input size="sm" icon="user-circle" placeholder="Prenom" v-model="prenom" group type="text" validate error="wrong" success="right"/>
                             <mdb-input size="sm" icon="phone" placeholder="Telephone" v-model="telephone" group type="text" validate error="wrong" success="right"/>
                             <mdb-input size="sm" icon="phone" placeholder="Adresse postale ou Quartier" v-model="adresse" group type="text" validate error="wrong" success="right"/>
                              <mdb-input size="sm" icon="envelope" placeholder="Adresse Email" v-model="email" group type="email" validate error="wrong" success="right" disabled />
                              <mdb-row>
     
    <div class="col ml-5 mb-2 mt-0 custom-file">
     <input type="file" id="file2" class="custom-file-input" @change="filesupload2"/>
      <label class="custom-file-label" style="border-right:hidden;borber-bottom:solid;border-top:hidden;border-left:hidden;border-radius:0px">{{txtfile2}}</label>
    </div>
    </mdb-row>
    <mdb-row v-if="!idcard">
      
      <div class="col ml-5 mb-2 mt-0 custom-file">
       <input  type="file" id="file3" class="custom-file-input" @change="filesupload3"/>
      <label class="custom-file-label" style="border-right:hidden;borber-bottom:solid;border-top:hidden;border-left:hidden;border-radius:0px" for="inputGroupFile01">{{txtfile3}}</label>
    </div>
    </mdb-row>
    <div class="mt-3">
      <div v-bind:class="{'spinner-border': spinnerU}" class="mt-3" role="status"> </div>
              <u>Consulter mes pieces:</u> <br> <a target="_blank" v-if="domicil" :href="domicil" ><span class="ml-5"> Justificatif de domiciliation</span> <br> </a>
               <a target="_blank" v-if="idcard" v-bind:href="idcard"> <span class="ml-5">Justificatif de pièce d'identité</span> </a>
  </div>
                          </mdb-card-body> 
                          <mdb-card-footer>
                            <div data-v-11613719="" data-v-732af18d="" class="col-md-12 text-center">
                              <input type="submit" value="Mettre à jour" class="btn btn-info btn-rounded"></div>                    
                          </mdb-card-footer>
                        </mdb-card>
                        </form>
                        </mdb-col>
                        </mdb-row>
                        <v-snackbar
      v-model="snackbar"
    >
      {{ txt }}
      <v-btn
        color="red"
        text
        @click="snackbar = false"
      >
        Fermer
      </v-btn>
    </v-snackbar>
    </v-app>
                        </div>
</template>

<script>
import axios from 'axios'
export default {
  
  data () {
    return {
      modal:false,
      modal2:false,
      modal3:false,
      modal4:false,
      modal5:false,
      modal6:false,
      modal7:false,
      idcard:'',
      txtfile2:'',
      spinner:'',
      txtfile3:'',
      txt:'',
      nom:'',
      prenom:'',
      success:false,
      message:'',
      adresse:'',
      telephone:'',
      email:'',
      spinnerU:false,
      user:'',
      file1:'',
      file2:'',
      file3:'',
      idcard:'',
      domicil:'',
      alert:false,
      alert2:false,
      success2:false,
      spinner2:false,
      message2:'',
      snackbar:false,
      photo:'',
      msg:'',
      id:'',
      godsons:'',
      email_godson:'',
    }
  },

  methods:{
    deleteaccount(){
       let user =  this.user = JSON.parse(localStorage.getItem('user'))
      axios.post('https://safe-community.fr/lara/api/delete/account',{
        id: user.id
      },{
        headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        }
      })
      .then(response => {
        console.log(response);
        
        this.modal3 =false
        this.snackbar = true
        this.txt = response.data.message
         this.$router.push({ name: 'login' }); 
      })
      .catch(error => {
        this.modal3 = false
this.snackbar = true
this.txt = error.response.data.message
      })
    },
    deletephoto(){
      let user =  this.user = JSON.parse(localStorage.getItem('user'))
      axios.post('https://safe-community.fr/lara/api/deletep',{
        id: user.id
      },{
        headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        }
      })
      .then(response => {
        this.modal5 =false
        this.snackbar = true
        this.txt = response.data.message
        this.chargephoto()
      })
      .catch(error => {
        this.modal5 = false
this.snackbar = true
this.txt = error.response.data.message
      })
    },
    filesupload1(event){
        this.file1 = event.target.files[0];
         console.log('fichier 1 :', this.file1)
      },
      
    filesupload2(event){
        this.file2 = event.target.files[0];
         console.log('fichier 2 :', this.file2)
         this.txtfile2 = this.file2.name
          setTimeout(() => {
          this.modal7 = true
          console.log('weeerrr');
        }, 1000);
      },

      uploadjustif(){
        let formData = new FormData();
        formData.append('file', this.file2)
          axios.post('https://safe-community.fr/lara/api/justificatif/upload',formData,{
        headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        }
      })
      .then(response => {
        this.modal7 = false
        this.snackbar = true
        this.txt = response.data.message
        console.log(response)
      })
      .catch(error => {
        this.snackbar = true
        this.txt = error.response.data.message
      })
      this.chargedomicil()
      },
      
    filesupload3(event){
        this.file3 = event.target.files[0];
         console.log('fichier 3 :', this.file3)
         this.txtfile3 = this.file3.name
        setTimeout(() => {
          this.modal6 = true
          console.log('weeerrr');
        }, 1000);
      },

      uploadpiece(){
         let formData = new FormData();
        formData.append('file', this.file3)
          axios.post('https://safe-community.fr/lara/api/idpiece/upload',formData,{
        headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        }
      })
      .then(response => {
         this.modal6 = false
        this.snackbar = true
        this.txt = response.data.message
        console.log(response)
      })
      .catch(error => {
        this.snackbar = true
        this.txt = error.response.data.message
      })
      this.chargepiece()
      },

        annuler1(){
       this.file1 = ''
    },

    annuler2(){
       this.file2 = ''
    },
     annuler3(){
      this.file3 = ''
    },

    putphoto(){
      this.success = false
      this.alert = false 
      this.spinner = true
       let formData = new FormData();
        formData.append('photo', this.file1)
      axios.post('https://safe-community.fr/lara/api/profile/upload',formData,{
        headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        }
      })
      .then(response => {
        this.success = true
        this.msg = response.data.message
        console.log(response)
        this.chargephoto()
        this.spinner = false
      })
      .catch(error => {
        console.log(error);
        this.spinner = false
        this.alert = true
        this.alert = true
        this.msg = error.response.data.message
      })
          },
          chargephoto(){
             axios.get('https://safe-community.fr/lara/api/profile/get',{
        headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        }
      })
      .then(response => {
        console.log(response)
        this.photo = response.data
      })
          },
    /* Liste des flilleuls */
 godsonlist(){
       
    axios.get("https://safe-community.fr/lara/api/godson",{
       headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        }
      })
    .then(response => {
      this.godsons = response.data
      console.log('tous mes filleuls',response)
    })
    .catch(error => {
      console.log(error)
    })
    /* Liste des flilleuls -FIN */
     },
     /* Anouter un flilleul */
     addgodson(){
       this.alert2 = false
       this.message2 = ''
       this.spinner2 = true
       this.success2 = false
      axios.post("https://safe-community.fr/lara/api/godson",{
        email_godson: this.email_godson
      },{
        headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        }
      })
      .then(response => {
 
   this.success2 = true
   this.message2 = response.data.message
  this.spinner2 = false
  this.email_godson=''
  
  }).catch(error => {
   this.spinner = false
            this.alert2 = true
            this.message2 = "Utilisateur existant"
            this.spinner2 = false
    console.log(error)
    })
         },
       /*  MISE A JOUR DES INFORMATIONS DU PROFIL */
    upgrade(){
      this.spinnerU = true
      axios.post('https://safe-community.fr/lara/api/updateUser',{
        id:this.id,
        name:this.nom,
        surname:this.prenom,
        phone_number:this.telephone,
        postal_address:this.adresse,
      },{
        headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        }
        })
      .then(response => {
         this.spinnerU = false
        console.log(response)
        if(response.status == 200){
          console.log(response)
          this.success = true
          this.message = "Mise a jour éffectuée"
          this.user = response.data.user
           this.nom = this.user.name
          this.prenom = this.user.surname
          this.adresse = this.user.postal_address
          this.telephone =this.user.phone_number
          this.email = this.user.email
          this.pays = this.user.country_id
          this.ville = this.user.city_id
          localStorage.setItem('user',JSON.stringify(this.user))
          this.snackbar = true
          this.txt = response.data.message
          
        }
        
      
      })
      .catch(error => {
         this.spinnerU = true
        console.log(error)
      })
    },
    chargepiece(){
        axios.get("https://safe-community.fr/lara/api/idpiece/get",{
       headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        }
      })
    .then(response => {
      this.idcard = response.data
      console.log(response)
    })
    .catch(error => {
      console.log(error)
    })
    },
    chargedomicil(){
        axios.get("https://safe-community.fr/lara/api/justificatif/get",{
       headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        }
      })
    .then(response => {
      this.domicil = response.data
      console.log(response)
    })
    .catch(error => {
      console.log(error)
    })
    
    }
  },
  created(){

  
    this.chargepiece()
    this.chargephoto()
    this.chargedomicil()  
    this.txtfile2 = "Justificatif domiciliation"
    this.txtfile3 = "Justificatif piece identité"
    console.log(localStorage.getItem('user'))
    this.user = JSON.parse(localStorage.getItem('user'))
    this.id = this.user.id
    this.nom = this.user.name
    this.prenom = this.user.surname
    this.adresse = this.user.postal_address
    this.telephone =this.user.phone_number
    this.email = this.user.email
    this.godsonlist()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.full {
  height: 70vh;
}
.bad-gateway-row {
  margin: 0;
  position: absolute;
  top: 50%;
  left: 55%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
</style>



