<template>
  <div>
      <v-app class="mt-3" style="background-color:rgb(236,236,237)">
        
    <v-dialog class="mt-5" v-model="dialog" persistent max-width="600px">
      <v-card class="mt-5">
        <v-card-title>
          <v-avatar size="65">
      <img
        :src="info.profile"
        alt="User photo"
      >
    </v-avatar> <span class="h5 ml-2 mr-2">{{ info.name }} {{ info.surname }} </span> 
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-row>
             
              <v-col cols="12" sm="6" md="4">
                 <span class="h5">Statut :</span>   <span class="">{{ info.statut }}</span>
              </v-col>
              <v-col cols="12" sm="6" md="4">
                <span class="h5">Parrain :</span>   <span class="">{{ info.proposer_email }}</span>
                </v-col>
              <v-col cols="12" sm="6" md="4">
                <span class="h5">Assurance :</span>   <span v-if=" info.insurance_id == 3 " class="">Premium</span><span v-if=" info.insurance_id == 2 " class="">Medium</span> <span v-if=" info.insurance_id == 1 " class="">Free</span>
              </v-col>
              <v-col >
                  <span class="h5">Adresse email :</span>   <span class="">{{ info.email }}</span>
              </v-col>
              
              </v-row>
              <v-row>
              <v-col>
                  <span class="h5">Carte d'identité :</span> <span v-if="!info.identity_piece" >Aucune piece d'identité</span>   <a target="_blank" v-if="info.identity_piece" v-bind:href="info.identity_piece">Consulter</a>
              </v-col>             
              <v-col>
                 <span class="headline"> <v-checkbox @change="validatecni()" v-if="info.identity_piece"  color="success" class="mt-n1" :label="`${cnilabel}`" v-model="cnival" ></v-checkbox> </span>
              </v-col>
               <v-col cols="12">
                <!--  <div v-if="blq == false"> -->
                   <v-switch
              v-model="blq"
              @change="bloquerUser()"
              :label="info.statut == 'Actif' ? 'Utilisateur actif' :  'Utilisateur bloqué'"
              color="red"
              hide-details
            ></v-switch>
                 <!-- </div>
                 <div v-if="blq == true">
                   <v-switch
              v-model="blq"
              label="Debloquer un utilisateur"
              color="red"
              value="red"
              hide-details
            ></v-switch>
                 </div> -->
               
              </v-col>
              </v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="dialog = false">Fermer</v-btn>
          <v-btn color="blue darken-1" text @click="dialog = false">Enregistrer</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
     <v-dialog
      v-model="dialog2"
      max-width="350"
    >
      <v-card>
        <v-card-title class="headline">Rechercher un utilisateur</v-card-title>

        <v-card-text>
         <v-select
          :items="elements"
          label="Rechercher par"
        ></v-select>
        <mdb-input class="mt-n2" label="texte" v-model="recherche" />
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn
            color="green darken-1"
            text
            @click="dialog2 = false"
          >
          Valider
          </v-btn>

          <v-btn
            color="green darken-1"
            text
            @click="dialog2 = false"
          >
            Annuler
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>


       <v-dialog
      v-model="dialog3"
      max-width="350"
    >
      <v-card>
        <v-card-title>
          <v-avatar size="65">
      <img
        :src="info.profile"
        alt="User photo"
      >
    </v-avatar> <span class="h5 ml-2 mr-2">Supprimer <strong> {{ info.name }} {{ info.surname }} </strong> </span> 
        </v-card-title>

        <v-card-text>
         Cette action est irreversible voulez vous continuer?
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn
            color="green darken-1"
            text
            @click="deleteUser()"
          >
          Valider
          </v-btn>

          <v-btn
            color="green darken-1"
            text
            @click="dialog3 = false"
          >
            Annuler
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>


      <v-card
   style="width : 4000px"
    class="mx-auto"
  >
    <v-toolbar
      color="deep-purple accent-4"
      dark
    >
    

      <v-toolbar-title>Tous les utilisateurs</v-toolbar-title>

      <v-spacer></v-spacer>

      <v-btn @click="dialog2 = true" icon>
        <v-icon>mdi-magnify</v-icon>
      </v-btn>
    </v-toolbar>

    <v-list subheader>
      <v-subheader>Utilisateurs actifs</v-subheader>

      <v-list-item
        v-for="item in items"
        :key="item.id"
        @click="dialog = true, blq=false,checkbox1=false, info = item"
      >
        <v-list-item-avatar>
          <v-img :src="item.profile"></v-img>
        </v-list-item-avatar>

        <v-list-item-content>
          <v-list-item-title> {{item.name}} {{item.surname}} </v-list-item-title>
        </v-list-item-content>
        <v-list-item-icon>
          <v-icon :color="item.active ? 'deep-purple accent-4' : 'grey'">mdi-pencil</v-icon>
        </v-list-item-icon>
      </v-list-item>
    </v-list>

    <v-divider></v-divider>

    <v-list subheader>
      <v-subheader>Utilisateurs demandant suppression de leurs comptes</v-subheader>

      <v-list-item
        v-for="item in items2"
        :key="item.title"
        @click="dialog3 = true, info = item"
      >
        <v-list-item-avatar>
          <v-img :src="item.avatar"></v-img>
        </v-list-item-avatar>

        <v-list-item-content>
          <v-list-item-title v-text="item.title"></v-list-item-title>
        </v-list-item-content>
      </v-list-item>

    </v-list>
    <v-divider></v-divider>
     <v-subheader class="font-weight-bold">Envoyer un message aux utilisateurs</v-subheader>
      <div class="alert" v-bind:class="{'alert-success': success, 'alert-danger': alert} "> {{ message2 }} </div>
     <form class="m-4">
    <v-text-field
      v-model="Sujet"
      :counter="10"
      label="Sujet"
      required
    ></v-text-field>
    <v-textarea
          name="input-7-1"
          value="Saisissez votre message"
          hint="Hint text"
        ></v-textarea>
   

    <v-btn class="mr-4" @click="messageUsers()">Envoyer</v-btn> <div v-bind:class="{'spinner-border': spinner}" class="mt-3" role="status"> </div>
  </form>
  </v-card>
  <v-snackbar
      v-model="snackbar"
    >
      {{ txt }}
      <v-btn
        color="red"
        text
        @click="snackbar = false"
      >
        Fermer
      </v-btn>
    </v-snackbar>
  </v-app>
  </div>
</template>

<script>
import axios from 'axios'
export default {
    data(){
        return{
          items:[],
          items2:[],
          
           /*  items: [
        { active: true, title: 'Jason Oner', avatar: 'https://cdn.vuetifyjs.com/images/lists/1.jpg' },
        { active: true, title: 'Ranee Carlson', avatar: 'https://cdn.vuetifyjs.com/images/lists/2.jpg' },
        { title: 'Cindy Baker', avatar: 'https://cdn.vuetifyjs.com/images/lists/3.jpg' },
        { title: 'Ali Connors', avatar: 'https://cdn.vuetifyjs.com/images/lists/4.jpg' },
      ], */
       elements: ['Adresse email', 'Nom'],
      dialog:false,
      dialog2: false,
      dialog3: false,
      info:{},
      cnival: true,
      users:[],
      todelete:[],
      recherche:'',
      Sujet:'',
      label:'',
      snackbar:false,
      txt:'',
      success:false,
      alert:false,
      message2:'',
      spinner:false,

        }
    },
    methods:{

      submit(){
       /*  axios.post('https://safe-community.fr/lara/api/diffusion', {  
          
        },{
            headers:{
            Authorization: "Bearer " + localStorage.getItem('token')
            } 
          })
          .then(response => {
            console.log(response)
            this.snackbar = true
            this.txt = response.data.message
          })
          .catch(error => {
            console.log(error);
            
          }) */
      },
      bloquerUser(){

           axios.post('https://safe-community.fr/lara/api/blocker',{
          id : this.info.id, 
        },{
          headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        }
        })
        .then(response => {
          console.log('Bloquer utilisateur :',response);
          this.snackbar = true,
          this.txt = response.data.message
        })
        .catch(error => {
           console.log(error);
          this.snackbar = true,
          this.txt = error.response.data.message
        })

         
      },

    validatecni(){
      if (this.cnival == true) {
           axios.post('https://safe-community.fr/lara/api/idpiece/validate',{
          id : this.info.id, 
        },{
          headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        }
        })
        .then(response => {
          console.log('Valider CNI:',response);
          this.snackbar = true,
          this.txt = response.data.message
        })
        .catch(error => {
           console.log(error);
          this.snackbar = true,
          this.txt = error.response.data.message
        })
        }
    },

      allusers(){ // reçevoir tous les utilisateurs 
        axios.get('https://safe-community.fr/lara/api/user',{
            headers:{
            Authorization: "Bearer " + localStorage.getItem('token')
            } 
          })
          .then(response => {
            console.log(response)
            this.items = response.data
          })
          .catch(error => {
            console.log(error);
            
          })
      },

    deletedUsers(){
       /* axios.get('https://safe-community.fr/lara/api/account/delet',{
            headers:{
            Authorization: "Bearer " + localStorage.getItem('token')
            } 
          })
          .then(response => {
            console.log(response)
            this.items2 = response.data
          })
          .catch(error => {
            console.log(error);
            
          }) */
    },

      messageUsers(){ // message a tous les utilisateurs
       this.alert2 = false
       this.message2 = ''
       this.spinner = true
       this.success2 = false
        axios.post('https://safe-community.fr/lara/api/diffusion',{
          subject: this.sujet,
          message: this.message 
        },{
          headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        }
        })
        .then(response => {
          this.spinner = false
          this.message2= response.data.message
           this.success = true
        })
        .catch(error => {
          this.spinner = false
           this.alert = true
           this.message2 = error.response.data.message
        })
      },

      deleteUser(){
        axios.post('https://safe-community.fr/lara/api/deleted/account',{
          id : this.info.id, 
        },{
          headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        }
        })
        .then(response => {
          console.log('supprimer utilisateur :',response);
          this.dialog3 = false
          this.snackbar = true,
          this.txt = response.data.message
        })
        .catch(error => {
           console.log(error);
          this.dialog3 = false
          this.snackbar = true,
          this.txt = error.response.data.message
        })
      }
    },

    computed :{ // propietées calculées ou computed property
      blq : function() {
        let bloq
      if (this.info.statut == 'Actif') {
        bloq = false
      }
      else{
        bloq = true
      }
        return bloq
      },
      
       cnilabel : function() {
        let message = 'CNI non validée'
        if(this.cnival == true){
          message = 'CNI validée'
        }
        return message
      }
    },

    created(){ // la fonction qui se declanche au demarage
      this.allusers()
    }
}
</script>

<style>

</style>


