<template>

<div>
  
 <mdb-alert class="mt-5 text-center" color="warning">
   <strong>Important</strong> L'annulation d'une réservation doit se faire dans un délai d’une semaine avant la date de départ, sinon des pénalités de l’ordre de 30% paiement initial vous seront inculpées
    </mdb-alert>   
       
   <mdb-modal frame position="bottom" direction="bottom" :show="modal5" @close="modal5 = false, spinner=false">
        <mdb-modal-body class="text-center">
            <span> {{ message }} </span>
            <mdb-btn color="secondary" @click.native="spinner=false, modal5 = false">Fermer</mdb-btn>
        </mdb-modal-body>
    </mdb-modal>

<mdb-modal :show="modal" @close="modal = false">
            <mdb-modal-header>
                <mdb-modal-title> Regler une Réservation</mdb-modal-title>
            </mdb-modal-header>
            <mdb-modal-body>
                    Veillez choisir le mode de payement
               
               <v-container fluid>  
    <v-radio-group v-model="radios" :mandatory="false" row>
      <v-radio  color="orange" label="Orange money" value="orange money"></v-radio>
      <v-radio  color="yellow"  label="Mtn mobile money" value="mtn mobile money"></v-radio>
      <v-radio  color="green" label="Espèces" value="especes"></v-radio>
      <v-radio color="success"  label="Virement Bancaire" value="virement bancaire"></v-radio>
       <v-radio  color="blue" label="Paypal" value="paypal"></v-radio>
    </v-radio-group>
  </v-container>
                </mdb-modal-body>
            <mdb-modal-footer style="height:50px" >
                <mdb-btn size="sm" class="rounded" color="danger" @click.native="modal = false">Annuler</mdb-btn>
                <mdb-btn size="sm" class="rounded" color="primary" @click="payer()">Oui</mdb-btn>
            </mdb-modal-footer>
        </mdb-modal>

         <mdb-modal :show="modal2" @close="modal2 = false">
            <mdb-modal-header>
                <mdb-modal-title> Annuler reservation </mdb-modal-title>
            </mdb-modal-header>
            <mdb-modal-body>
                Pour  annuler une réservation vous devez passer par les administrateurs en allant sur la page <router-link :to="{name: 'contact'}">Contactez-nous </router-link>
            </mdb-modal-body>
            <mdb-modal-footer  style="height:50px" >
                <mdb-btn size="sm" class="rounded" color="danger" @click.native="modal2 = false">Ok</mdb-btn>
            </mdb-modal-footer>
        </mdb-modal>
  
  
  <section>
    <h3 class="font-weight-bold text-center h3 mb-3 mt-5">Demandes de réservations en attente de payement</h3>
  <v-data-table
    :headers="headers"
    :items="wait"
    sort-by="Ville de départ"
    class="elevation-1"
  >
  
   <template v-slot:item.actions="{ item }">

        <v-btn 
      depressed
       small 
       rounded
       dark
       class="mt-1"
       @click="modal = true, ind = item, id=item.id">
       Régler
       </v-btn>
       
     <v-btn 
      depressed
       small 
       rounded
       dark
       color="red darken-2"
       class="mt-1"
        @click="modal2 = true , ind = item, id=item.id"
       >
       Annuler
       </v-btn>

    </template> 
     </v-data-table>
  </section>
  
  <section>
    <h3 class="font-weight-bold text-center h3 mb-3 mt-5">Demandes de réservations payées</h3>
  <v-data-table
    :headers="headers2"
    :items="refused"
    sort-by="Ville de départ"
    class="elevation-1"
  >
   </v-data-table>
  </section>
 
  <section>
    <h3 class="font-weight-bold text-center h3 mb-3 mt-5">Demandes de réservations annulées/refusées</h3>
  <v-data-table
    :headers="headers2"
    :items="refused"
    sort-by="Ville de départ"
    class="elevation-1"
  >
   </v-data-table>
  </section>
<v-snackbar
      v-model="snackbar"
    >
      {{ txt }}
      <v-btn
        color="red"
        text
        @click="snackbar = false"
      >
        Fermer
      </v-btn>
    </v-snackbar>
  
    </div>
</template>

<script>
import axios from 'axios'

export default {

  data () {
    return {
        radios : false,
        modal : false,
        modal2 : false,
        modal3:false,
        modal4:false,
        modal5:false,
        dialog: false,
        txt:'',
        snackbar:false,
        message:'',
        ind:0,
        nvprix:'',
        id:'',
        reserv:[],
        regl:[],
        details:[],
        wait:[],
        accepted:[],
        canceled:[],
        payed:[],
        refused:[],
         cycle: false,
         colors: [
          'green',
          'secondary',
          'yellow darken-4',
          'red lighten-2',
          'orange darken-1',
        ],
      headers: [
       {
          text: "#Numéro de la réservation",
          align: 'start',
          sortable: false,
          value: 'user_number',
          
        },
         { text: 'Ville de départ', value: 'depart' },
         { text: "Ville de d'arrivée", value: 'arrive' },
         { text: 'Date de départ', value: 'date_depart' },
         { text: "Date de d'arrivée", value: 'date_arrive' },
        { text: 'Poids du colis(Kg)', value: 'kilo' },
        { text: 'Prix du kilos(€)', value: 'price_kilo' },
        { text: 'Détails', value: 'description' },
        { text: 'Cout global de la réservation(€)', value: 'amount' },
        { text: '', value: 'actions', sortable: false },
      ],
      headers2: [
        {
          text: "#Numéro de la réservation",
          align: 'start',
          sortable: false,
          value: 'user_number',
          
        },
         { text: 'Ville de départ', value: 'depart' },
         { text: "Ville de d'arrivée", value: 'arrive' },
         { text: 'Date de départ', value: 'date_depart' },
         { text: "Date de d'arrivée", value: 'date_arrive' },
        { text: 'Poids du colis(Kg)', value: 'kilo' },
        { text: 'Prix du kilos(€)', value: 'price_kilo' },
        { text: 'Détails', value: 'description' },
        { text: 'Cout global de la réservation(€)', value: 'amount' },
        { text: 'Statut', value: 'statut' },
        { text: '', value: 'actions', sortable: false },
      ],
       defaultItem: {
        origin: '',
        destination: 0,
        destination: 0,
        price_kilo: 0,
        kilo: 0,
        kilo_available:0,
      },
    }
  },
            methods: {
               

    
            },

    created() {
        console.log(localStorage.getItem('token'))
     
     /*   axios.get("https://safe-community.fr/lara/api/reservation/accepted",
       {
          headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        }
       })
      .then(response => {
          this.accepted = response.data
          console.log('reservations acceptées : ',this.accepted[2])
      }) */

      axios.get("https://safe-community.fr/lara/api/accepted",
       {
          headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        }
       })

      .then(response => {
          this.wait= response.data
          console.log('reservations en attente : ',this.wait[2])
      })

        axios.get("https://safe-community.fr/lara/api/refused",
       {
          headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        }
       })
      .then(response => {
          this.refused = response.data
          console.log('Reservations refusées : ',this.refused[2])
      })
/*
       axios.get("https://safe-community.fr/lara/api/voyage/annules",
       {
          headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        }
       })
      .then(response => {
          this.canceled =  response.data
          console.log('Voyages terminés : ',this.canceled[2])
      })

           */
    },
    
    watch: {
    dialog (val) {
      val || this.close()
    },
  },

    methods: {
      close() {
      this.dialog = false
    },
     refuse(){
axios.post("https://safe-community.fr/lara/api/reservation/annuler",{
            id: this.id 
           },
           {
              headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        }
           })
      .then(response => {
          console.log('Annulation dune reservation : ',response.data)
      })
      this.modal2 = false
      this.accepted.splice(this.ind, 1)
    },

       payer(){
         console.log('données : ',this.id, this.radios );
         
axios.post("https://safe-community.fr/lara/api/reservation/payer",{
            id: this.id ,
             paid_mode:this.radios,
           },
           {
              headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        }
           })
      .then(response => {
        this.modal5 = true
          this.message = 'Un administrateur vous contactera vous le payement'
          this.modal = false
          console.log('Payement dune reservation : ',response.data)
          this.accepted.splice( this.accepted.indexOf(this.ind) , 1)
      })
      .catch(error => {
        this.txt = error.response.data.message
        this.snackbar = true
      })
    },

           deleteitem(item) {
      axios.post("https://safe-community.fr/lara/api/reservation/refused",
      {
        id : this.id
      },
     {
          headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        }
       })
        .then(response => {
          console.log('annulation du voyage ' , this.id, response)
        })

          this.pending.splice(this.pending.indexOf(this.ind), 1)
          this.modal = false
       
      },

     réglé(item) {
       axios.get(`https://safe-community.fr/lara/api/voyage/reservation/pay/${item.id}`)
      .then(response => {
        this.regl = response.data
        this.modal4 = true
        console.log('Reservation accpetées : ',response)
      })
      },
  
      detailsitem(item){
         axios.get(`https://safe-community.fr/lara/api/post/${item.id}`,
         {
      headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
      }
        })
      .then(response => {
        this.details = response.data.post
        console.log('details du voyage : ',response)
      })
        this.modal3 = true

      },

       editItem(){
          axios.put(`https://safe-community.fr/lara/api/post/${this.id}`,
          {
            price_kilo :  this.nvprix
          },
         {
      headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
      }
        })
        .then(response => {
           console.log('Modification' ,response.data)
        })
         let voy = {
           id:this.ind.id,
           origin :this.ind.origin,
           destination :this.ind.destination,
           price_kilo : this.nvprix,
           kilo : this.ind.kilo,
           kilo_available : this.ind.kilo_available
         }
        this.pending.splice(this.pending.indexOf(this.ind), 1, voy)
        this.modal5 = false

       
      }, 

      reservation(item){
         axios.get(`https://safe-community.fr/lara/api/voyage/reservation/accepted/${item.id}`)
      .then(response => {
        this.reserv = response.data
        this.modal2 = true
        console.log('Reservation accpetées : ',response)
      })
      }

    },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



