import Vue from 'vue'
import store from '../store/index'
import Router from 'vue-router'

Vue.use(Router)



let router  = new Router({
  mode: 'history',
  routes: [
    /* {
      path: '/',
      components:{
   default: require('@/components/home.vue').default,
     footer: require('@/components/footer.vue')
      } 
    }, */{
      path: '/Login',
      name: 'login',
      component: require('@/components/login.vue').default
    },{
      path: '/videos',
      name: 'videos',
      component: require('@/components/ExampleComponent.vue').default
    },{
      path: '/home',
      name: 'home',
      component: require('@/components/home.vue').default
    },{
      path: '/verify',
      name: 'verify',
      component: require('@/components/verify.vue').default
    },{
      path: '/password',
      name: 'password',
      component: require('@/components/password.vue').default
    },{
      path: '/contact',
      name: 'contact',
      component: require('@/components/contact.vue').default
    },{
      path: '/reset',
      name: 'reset',
      component: require('@/components/reset.vue').default
    },{
      path: '/travels',
      name: 'travels',
      component: require('@/components/travels.vue').default
    },{
      path: '/dashboard',
      name: 'dashboard',
      props: { page: 1 },
      component: require('@/components/dashboard.vue').default,
      meta: { 
        requiresAuth: true
      },
      children: [{
        path:'assurance',
        name:'d.assurance',
        props: { page: 2 },
        component: require('@/components/assurance.vue').default,
      },{
         path:'transactions',
         name:'d.transactions',
         props: { page: 3 },
         component: require('@/components/transactions.vue').default,
        },{
          path:'historique',
          name:'d.historique',
          props: { page: 4 },
          component: require('@/components/historique.vue').default,
        },{
          path:'profil',
          name:'d.profil',
          props: { page: 5 },
          component: require('@/components/profil.vue').default,
        },{
          path:'voyages',
          name:'d.voyages',
          props: { page: 4 },
          component: require('@/components/voyages.vue').default,
        },{
          path:'reservations',
          name:'d.reservations',
          props: { page: 6 },
          component: require('@/components/reservations.vue').default,
        },{
          path:'bank',
          name:'d.bank',
          props: { page: 6 },
          component: require('@/components/bank.vue').default,
        },{
          path:'filleul',
          name:'d.filleul',
          props: { page: 7 },
          component: require('@/components/filleul.vue').default,
        },{
          path: '/publish',
          name: 'd.publish',
          component: require('@/components/publish.vue').default
        },{
          path:'AdminHome',
          name:'d.homeA',
          props: { page: 8 },
          component: require('@/components/adminHome.vue').default,
        },{
          path:'AdminStats',
          name:'d.statsA',
          props: { page: 9 },
          component: require('@/components/adminStats.vue').default,
        },{
          path:'AdminVoyages',
          name:'d.voyagesA',
          props: { page: 10 },
          component: require('@/components/adminVoyages.vue').default,
        },{
          path:'AdminReservations',
          name:'d.reservationsA',
          props: { page: 11 },
          component: require('@/components/adminReservations.vue').default,
        },{
          path:'AdminPaiements',
          name:'d.paiementsA',
          props: { page: 12 },
          component: require('@/components/adminPaiements.vue').default,
        },{
          path:'AdminAccount',
          name:'d.accountA',
          props: { page: 13 },
          component: require('@/components/adminAccount.vue').default,
        }]
     },{
      path: '/register',
      name: 'register',
      component: require('@/components/register.vue').default
    }, {
      path: '/publish',
      name: 'publish',
      component: require('@/components/publish.vue').default
    },{
      path: '*',
      component: require('@/components/home.vue').default
    }
  ]
})

router.beforeEach((to, from, next) => {
  if(to.matched.some(record => record.meta.requiresAuth)) {
    if (store.getters.isLoggedIn) {
      next()
      return
    }
    next('/login') 
  } else {
    next() 
  }
})

export default router



