<template>

<div>
 
      <h3 class="font-weight-bold text-center h3 my-3">Voyages en cours</h3>
      

<mdb-modal :show="modal" @close="modal = false">
            <mdb-modal-header>
                <mdb-modal-title>Supprimer le trajet</mdb-modal-title>
            </mdb-modal-header>
            <mdb-modal-body>
               Pour Supprimer un trajet vous devez passer par les administrateurs en allant sur la page <router-link :to="{name: 'contact'}">Contactez-nous </router-link>
            </mdb-modal-body>
            <mdb-modal-footer style="height:50px" >
                <mdb-btn size="sm" color="secondary" @click.native="modal = false">Fermer</mdb-btn>
            </mdb-modal-footer>
        </mdb-modal>

        <mdb-modal :show="modal5" @close="modal5 = false">
            <mdb-modal-header>
                <mdb-modal-title>Modifier le Trajet</mdb-modal-title>
            </mdb-modal-header>
            <mdb-modal-body>
               <mdb-input type="number" label="Nouveau prix (Euro) " v-model="nvprix" />
               <i style="font-size:14px">Pour toute modification supplementaire veuillez <router-link :to="{name: 'contact'}">contacter les administrateurs</router-link></i>
            </mdb-modal-body>
            <mdb-modal-footer style="height:50px">
                <mdb-btn size="sm" color="secondary" @click.native="modal5 = false">Close</mdb-btn>
                <mdb-btn size="sm" color="danger" @click=" editItem()">Oui</mdb-btn>
            </mdb-modal-footer>
        </mdb-modal>

        <mdb-modal :show="modal3" @close="modal3 = false">
            <mdb-modal-header>
                <mdb-modal-title>Details du trajet</mdb-modal-title>
            </mdb-modal-header>
            <mdb-modal-body>
        <mdb-row>
          <mdb-col  col="3" class="text-center"><mdb-icon icon="briefcase" size="4x"/></mdb-col>
          <mdb-col col="9">
            <p>
              <div class="my-3"><span style="font-weight:bold">Ville de Départ : </span> {{ details.origin }}</div>
        <div class="my-3"><span style="font-weight:bold">Ville de D'arrivée : </span> {{ details.destination }}</div>
        <div class="my-3"><span style="font-weight:bold">Date de Départ : </span> {{ details.date_depart }} </div>
              <div class="my-3"><span style="font-weight:bold">Date d'arrivée : </span>{{ details.date_arrive }}</div>
              <div class="my-3"><span style="font-weight:bold">Prix/kg : </span>{{ details.price_kilo }} € ({{ details.price_kilo * 655 }} FCFA)</div>
              <div class="my-3"><span style="font-weight:bold">Compagnie aérienne : </span>{{ details.compagnie }}</div>
              <div class="my-3"><span style="font-weight:bold">Quantité disponible : </span>{{ details.kilo }} Kg</div>
             <div class="my-3"> <span style="font-weight:bold">Catégorie d'objets a tranporter :  </span>{{details.package_category }}</div>
             <div class="my-3"> <span style="font-weight:bold">Renumeration totale :  </span>{{details.remuneration }}</div>
              <div class="my-3"> <span style="font-weight:bold">Renumeration parrain :  </span>{{details.rem_parrain }}</div>
                <div class="my-3"> <span style="font-weight:bold">Rénumeration voyageur :  </span>{{details.rem_voyageur }}</div>
              <div class="my-3"><span style="font-weight:bold">Plus d'informations : </span>{{ details.description }}</div>
              
            </p>
          </mdb-col>
        </mdb-row>
              
            </mdb-modal-body>
            <mdb-modal-footer style="height:50px">
                <mdb-btn size="sm" class="rounded" color="secondary" @click.native="modal3 = false">Fermer</mdb-btn>
            </mdb-modal-footer>
        </mdb-modal>

        <mdb-modal :show="modal4" @close="modal4 = false">
            <mdb-modal-header>
                <mdb-modal-title>Colis expédiés</mdb-modal-title>
            </mdb-modal-header>
             <mdb-modal-body>
               <v-card
    elevation="24"
    max-width="444"
    class="mx-auto"
  >
   
    <v-carousel
      :continuous="false"
      :cycle="cycle"
      :show-arrows="false"
      height="300"
    >
      <v-carousel-item
        v-for="(colis, i) in regl"
        :key="i"
      >
       <v-sheet
          :color="colors[i]"
          height="100%"
          tile
        >
          <v-row >
        <img src="https://image.flaticon.com/icons/svg/2877/2877073.svg" alt="location icon" class="ml-4" style="width: 40px"> <div class="col ml-n2" style="font-size:20px">Description du colis : <span class="font-weight-bold">{{regl[i].description}}</span>  </div>
          </v-row>
           <v-row >
           <img src="https://www.flaticon.com/premium-icon/icons/svg/756/756379.svg" alt="location icon" class="ml-4" style="width: 35px"><div class="col ml-n1" style="font-size:20px">Poids du colis : <span class="font-weight-bold">{{regl[i].kilo}} Kg</span> </div>
           </v-row>
              <v-row >
          <img src="https://image.flaticon.com/icons/svg/778/778288.svg" alt="location icon" class="ml-4" style="width: 40px">  <div class="col ml-n2" style="font-size:19px">Montant de la transaction : <span class="font-weight-bold">{{regl[i].amount}} euros ({{regl[i].amount * 655}}  </span> FCFA )</div>
          </v-row>

        </v-sheet>
      </v-carousel-item>
    </v-carousel>
   <!--  <v-list two-line>
      <v-list-item>
        <v-list-item-avatar>
          <v-img src="https://cdn.vuetifyjs.com/images/john.png"></v-img>
        </v-list-item-avatar>
        <v-list-item-content>
          <v-list-item-title> Sidney hod </v-list-item-title>
          <v-list-item-subtitle>Author</v-list-item-subtitle>
        </v-list-item-content>
        <v-list-item-action>
        </v-list-item-action>
      </v-list-item>
    </v-list> -->
  </v-card>
            </mdb-modal-body>
            <mdb-modal-footer style="height:50px">
                <mdb-btn size="sm" class="rounded" color="secondary" @click.native="modal4 = false">Fermer</mdb-btn>
            </mdb-modal-footer>
        </mdb-modal>

        <mdb-modal class="rounded" :show="modal2" @close="modal2 = false">
            <mdb-modal-header>
                <mdb-modal-title>Demandes de réservations acceptées</mdb-modal-title>
            </mdb-modal-header>
            <mdb-modal-body>
               <v-card
    elevation="24"
    max-width="444"
    class="mx-auto"
  >
   
    <v-carousel
      :continuous="false"
      :cycle="cycle"
      :show-arrows="false"
      height="300"
    >
      <v-carousel-item
        v-for="(colis, i) in reserv"
        :key="i"
      >
       <v-sheet
          :color="colors[i]"
          height="100%"
          tile
        >
          <v-row >
        <img src="https://image.flaticon.com/icons/svg/2877/2877073.svg" alt="location icon" class="ml-4" style="width: 40px"> <div class="col ml-n2" style="font-size:20px">Description du colis : <span class="font-weight-bold">{{reserv[i].description}}</span>  </div>
          </v-row>
           <v-row >
           <img src="https://www.flaticon.com/premium-icon/icons/svg/756/756379.svg" alt="location icon" class="ml-4" style="width: 35px"><div class="col ml-n1" style="font-size:20px">Poids du colis : <span class="font-weight-bold">{{reserv[i].kilo}} Kg</span> </div>
           </v-row>
              <v-row >
          <img src="https://image.flaticon.com/icons/svg/778/778288.svg" alt="location icon" class="ml-4" style="width: 40px">  <div class="col ml-n2" style="font-size:19px">Montant de la transaction : <span class="font-weight-bold">{{reserv[i].amount}} euros ({{reserv[i].amount * 655}}  </span> FCFA )</div>
          </v-row>

          <mdb-row class="justify-content-end">
            <div class="text-center">
     <v-btn 
      depressed
       small 
       rounded
       dark
       justify="center"
       class="mr-4 mt-4"
       @click="overlay = !overlay">
       Supprimer <v-icon name="delete"></v-icon>
       </v-btn>

    <v-overlay :value="overlay">
      <v-btn
      
        @click="deletereserv(reserv[i])"
      >
       Vous allez etre rediriger vers contacter les administrateurs pour supprimer cette réservation
      </v-btn>
    </v-overlay>
  </div>
          </mdb-row>
        </v-sheet>
      </v-carousel-item>
    </v-carousel>
   <!--  <v-list two-line>
      <v-list-item>
        <v-list-item-avatar>
          <v-img src="https://cdn.vuetifyjs.com/images/john.png"></v-img>
        </v-list-item-avatar>
        <v-list-item-content>
          <v-list-item-title> Sidney hod </v-list-item-title>
          <v-list-item-subtitle>Author</v-list-item-subtitle>
        </v-list-item-content>
        <v-list-item-action>
        </v-list-item-action>
      </v-list-item>
    </v-list> -->
  </v-card>
            </mdb-modal-body>
            <mdb-modal-footer style="height:50px">
                <mdb-btn class="rounded" color="danger" size="sm" @click.native="modal2 = false">Fermer</mdb-btn>
            </mdb-modal-footer>
        </mdb-modal>
<template>
 <v-data-table 
    :headers="headers2"
    single-expand
    mobile-breakpoint
    :items="pending"
    sort-by="Ville de départ"
    class="elevation-1"
    >
   <template v-slot:item.actions="{ item }">
      <v-btn 
      depressed
       small 
       rounded
       dark
       color="blue darken-3"
       class="mt-2"
       @click="detailsitem(item)">
       Details
       </v-btn>

        <v-btn 
      depressed
       small 
       rounded
       dark
       class="mt-1"
       @click="modal5 = true, ind = item, id=item.id">
       Modifier
       </v-btn>
       
     <v-btn 
      depressed
       small 
       rounded
       dark
       color="red darken-2"
       class="mt-1"
        @click="modal = true , ind = item, id=item.id"
       >
       Annuler
       </v-btn>

        <v-btn 
       small 
       rounded
       dark
       color="grey lighten-1"
       class="mt-1 mb-2 ml"
       style="font-size: 11px"
       @click="reservation(item)"
      >
       Reservations
       </v-btn>

    </template> 

    </v-data-table>
    </template>
  
  <section>
    <h3 class="font-weight-bold text-center h3 mb-3 mt-5">Voyages terminés</h3>
  <v-data-table
    :headers="headers"
    :items="finished"
    sort-by="Ville de départ"
    class="elevation-1"
  >
    <template v-slot:item.actions="{ item }">
      <v-btn 
      depressed
       small 
       rounded
       dark
       color="blue darken-3"
       class="m-1"
       @click="detailsitem(item)">
       Details
       </v-btn>

        <v-btn 
       small 
       rounded
       color="grey lighten-1"
       class="my-2"
       style="font-size: 9.5px"
       @click="reservation(item)"
      >
       Reservations
       </v-btn>

    </template> 
  </v-data-table>
  </section>

  
  <section>
    <h3 class="font-weight-bold text-center h3 mb-3 mt-5">Voyages Annulés</h3>
  <v-data-table
    :headers="headers3"
    :items="canceled"
    sort-by="Ville de départ"
    class="elevation-1"
  >
    
  </v-data-table>
  </section>
  
    </div>
</template>

<script>
import axios from 'axios'

export default {

  data () {
    return {
        modal : false,
        modal2 : false,
        modal3:false,
        modal4:false,
        modal5:false,
        dialog: false,
        overlay: false,
        ind:0,
        nvprix:'',
        id:'',
        reserv:[],
        regl:[],
        details:[],
        termines:[],
        canceled:[],
        finished:[],
        pending:[],
         cycle: false,
         colors: [
          'green',
          'secondary',
          'yellow darken-4',
          'red lighten-2',
          'orange darken-1',
        ],
      headers: [
        {
          text: '#numéro du voyage',
          align: 'start',
          sortable: false,
          value: 'user_number',
        },
        { text: 'Ville de départ', value: 'origin' },
        { text: "Ville d'arrivée", value: 'destination' },
        { text: 'Date de départ', value: 'date_depart' },
        { text: "Date de d'arrivée", value: 'date_arrive' },
        { text: 'Prix du kilo(€)', value: 'price_kilo' },
        { text: 'Kilos de départ', value: 'kilo' },
        { text: '', value: 'actions', sortable: false },
      ],
      headers2: [
        {
          text: '#numéro du voyage',
          align: 'start',
          sortable: false,
          value: 'user_number',
        },
        { text: 'Ville de départ', value: 'origin' },
        { text: "Ville d'arrivée", value: 'destination' },
        { text: 'Date de départ', value: 'date_depart' },
        { text: "Date de d'arrivée", value: 'date_arrive' },
        { text: 'Prix du kilo(€)', value: 'price_kilo' },
        { text: 'Kilos de départ', value: 'kilo' },
        { text: 'Kilos restants', value: 'kilo_available' },
        { text: 'Statut', value: 'statut' },
        { text: '', value: 'actions', sortable: false },
      ],
      headers3: [
        {
          text: '#numéro du voyage',
          align: 'start',
          sortable: false,
          value: 'user_number',
        },
        { text: 'Ville de départ', value: 'origin' },
        { text: "Ville d'arrivée", value: 'destination' },
        { text: 'Date de départ', value: 'date_depart' },
        { text: "Date de d'arrivée", value: 'date_arrive' },
        { text: 'Prix du kilo(€)', value: 'price_kilo' },
        { text: 'Kilos de départ', value: 'kilo' },
        { text: 'Kilos restants', value: 'kilo_available' },
        { text: '', value: 'actions', sortable: false },
      ],
    }
  },
  computed: {
     
    },

    created () {
     
       axios.get("https://safe-community.fr/lara/api/voyage/termines",
       {
          headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        }
       })
      .then(response => {
          this.termines = response.data
          this.finished =  response.data
          console.log('Voyages terminés : ',this.termines[2])
      })

       axios.get("https://safe-community.fr/lara/api/voyage/en_cours",
       {
          headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        }
       })
      .then(response => {
          this.pending = response.data
          console.log('Voyages en cours : ',this.pending[2])
      })

       axios.get("https://safe-community.fr/lara/api/voyage/annules",
       {
          headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        }
       })
      .then(response => {
          this.canceled =  response.data
          console.log('Voyages terminés : ',this.canceled[2])
      })

          
    },
    

    methods: {

      réglé(item) {
        axios.get(`https://safe-community.fr/lara/api/voyage/reservation/pay/${item.id}`)
        .then(response => {
          this.regl = response.data
          this.modal4 = true
          console.log('Reservation accpetées : ',response)
        })
        },
    
        detailsitem(item){
          axios.get(`https://safe-community.fr/lara/api/post/${item.id}`,
          {
        headers:{
            Authorization: "Bearer " + localStorage.getItem('token')
        }
          })
        .then(response => {
          this.details = response.data.post
          console.log('details du voyage : ',response)
        })
          this.modal3 = true

        },

        editItem(){
            axios.put(`https://safe-community.fr/lara/api/post/${this.id}`,
            {
              price_kilo :  this.nvprix
            },
          {
        headers:{
            Authorization: "Bearer " + localStorage.getItem('token')
        }
          })
          .then(response => {
            console.log('Modification' ,response.data)
          })
          let voy = {
            id:this.ind.id,
            origin :this.ind.origin,
            destination :this.ind.destination,
            price_kilo : this.nvprix,
            kilo : this.ind.kilo,
            kilo_available : this.ind.kilo_available,
            date_depart : this.ind.date_depart,
            date_arrive : this.ind.date_arrive,
            statut : this.ind.statut
          }
          this.pending.splice(this.pending.indexOf(this.ind), 1, voy)
          this.modal5 = false

        
        }, 

        reservation(item){
          axios.get(`https://safe-community.fr/lara/api/voyage/reservation/accepted/${item.id}`)
        .then(response => {
          this.reserv = response.data
          this.modal2 = true
          console.log('Reservation accpetées : ',response)
        })
        }

    },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



