<template>
  <v-app style="background-color:rgb(236,236,237)">
      <section>
    <h3 class="font-weight-bold text-center h3 mb-3 mt-5">Tous les voyages en cours</h3>
      <v-dialog
    v-model="dialog3"
    max-width="290"
    >
      <v-card>
        <v-card-title color="red" class="headline"> Supprimer un Voyage</v-card-title>
        <v-card-text>
        <p>Cette action est irreversible, voulez-vous continuer?</p>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
        <v-btn color="blue darken-1" text @click="deletevoy()">Continuer</v-btn>
        <v-btn color="red" text @click="dialog3 = false">Annuler</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog
    class="mt-3"
    v-model="dialog"
    max-width="400"
    >
      <v-card>
          <v-card-title color="red" class="headline"> Détails du voyages</v-card-title>
          <v-card-text>
        
          <mdb-col  col="3" class="text-center"><mdb-icon icon="briefcase" size="4x"/></mdb-col>
                <div class="my-3"><span style="font-weight:bold">Ville de Départ : </span> {{ details.origin }}</div>
          <div class="my-3"><span style="font-weight:bold">Ville de D'arrivée : </span> {{ details.destination }}</div>
          <div class="my-3"><span style="font-weight:bold">Date de Départ : </span> {{ details.date_depart}} </div>
                <div class="my-3"><span style="font-weight:bold">Date d'arrivée : </span>{{ details.date_arrive }}</div>
                <div class="my-3"><span style="font-weight:bold">Prix/kg : </span>{{ details.price_kilo }} € ({{ details.price_kilo * 655 }} FCFA)</div>
                <div class="my-3"><span style="font-weight:bold">Compagnie aérienne : </span>{{ details.compagnie }}</div>
                <div class="my-3"><span style="font-weight:bold">Quantité disponible : </span>{{ details.kilo }} Kg</div>
              <div class="my-3"> <span style="font-weight:bold">Catégorie d'objets a tranporter :  </span>{{details.package_category }}</div>
              <div class="my-3"> <span style="font-weight:bold">Renumeration totale :  </span>{{details.remuneration }}</div>
                <div class="my-3"> <span style="font-weight:bold">Renumeration parrain :  </span>{{details.rem_parrain }}</div>
                  <div class="my-3"> <span style="font-weight:bold">Rénumeration voyageur :  </span>{{details.rem_voyageur }}</div>
                <div class="my-3"><span style="font-weight:bold">Plus d'informations : </span>{{ details.description }}</div>
                
            
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
          <v-btn color="red" text @click="dialog = false">Fermer</v-btn>
          </v-card-actions>
      </v-card>
    </v-dialog>
    
      <v-dialog
    v-model="dialog2"
    max-width="800"
    >
      <v-card>
        <v-card-title color="red" class="headline"> Modifier un trajet </v-card-title>
        <v-card-text>
        <v-form v-model="valid">
    <v-container>
      <v-row>
          <v-col>
            <label class="col-sm-5 control-label">Date de départ </label>
              <datetime :min-datetime="depart"  class="cyan-text form-control" v-model="el.date_depart" required />
          </v-col>
          <v-col class="input-group mb-4 ">
                  <label class="col-sm-5 control-label">Heure de depart *</label>
                  <input type="time" :min-datetime="hdepart"  class="form-control rounded" v-model="el.hdepart" required data-validation-required-message="Ce champ est requis">
          </v-col>

          <v-col>
           <label class="col-sm-5 control-label">Date d'arrivée</label>
              <datetime :min-datetime="depart"  class="cyan-text form-control" v-model="el.date_arrive" required />
          </v-col>

          <v-col v-scroll-reveal.reset class="input-group mb-4 ">
             <label class="col-sm-5 control-label">Heure d'arrivée *</label>
            <input type="time" :min-datetime="harrive"  class="form-control rounded" v-model="el.harrive" required data-validation-required-message="Ce champ est requis">
         </v-col>

          <v-col
            cols="12"
            md="4"
          >
            <v-text-field
              v-model="el.kilo"
              label="Nombre de kilos disponibles"
              required
            ></v-text-field>
          </v-col>
        </v-row>
      </v-container>
    </v-form>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
        <v-btn color="blue darken-1" text @click="editvoy()">Confirmer</v-btn><div v-bind:class="{'spinner-border': spinner}" class="mt-3" role="status"> </div>
        <v-btn color="red" text @click="dialog2 = false">Annuler</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

  
   <mdb-modal class="rounded" :show="modal" @close="modal = false">
            <mdb-modal-header>
                <mdb-modal-title>Demandes de réservations attachées</mdb-modal-title>
            </mdb-modal-header>
            <mdb-modal-body>
               <v-card
    elevation="24"
    max-width="444"
    class="mx-auto"
  >
   
    <v-carousel
      :continuous="false"
      :show-arrows="false"
      height="300"
    >
      <v-carousel-item
        v-for="(colis, i) in reserv"
        :key="i"
      >
       <v-sheet
          :color="colors[i]"
          height="100%"
          tile
        >
          <v-row>
        <img src="https://image.flaticon.com/icons/svg/2877/2877073.svg" alt="location icon" class="ml-4" style="width: 40px"> <div class="col ml-n2" style="font-size:20px">Description du colis : <span class="font-weight-bold">{{reserv[i].description}}</span>  </div>
          </v-row>
           <v-row >
           <img src="https://www.flaticon.com/premium-icon/icons/svg/756/756379.svg" alt="location icon" class="ml-4" style="width: 35px"><div class="col ml-n1" style="font-size:20px">Poids du colis : <span class="font-weight-bold">{{reserv[i].kilo}} Kg</span> </div>
           </v-row>
              <v-row >
          <img src="https://image.flaticon.com/icons/svg/778/778288.svg" alt="location icon" class="ml-4" style="width: 40px">  <div class="col ml-n2" style="font-size:19px">Montant de la transaction : <span class="font-weight-bold">{{reserv[i].amount}} euros ({{reserv[i].amount * 655}}  </span> FCFA )</div>
          </v-row>

          <mdb-row class="justify-content-end">
            <div class="text-center">
     <v-btn 
      depressed
       small 
       rounded
       dark
       justify="center"
       class="mr-4 mt-4"
       @click="deletereserv()">
       Supprimer <v-icon name="delete"></v-icon>
       </v-btn>

    
  </div>
          </mdb-row>
        </v-sheet>
      </v-carousel-item>
    </v-carousel>
  
  </v-card>
            </mdb-modal-body>
            <mdb-modal-footer style="height:50px">
                <mdb-btn class="rounded" color="danger" size="sm" @click.native="modal = false">Fermer</mdb-btn>
            </mdb-modal-footer>
        </mdb-modal>
<template>
    <v-card>
    <v-card-title>
      <v-text-field
        v-model="search"
        label="Rechercher un utilisateur"
        single-line
        hide-details
      ></v-text-field>
    </v-card-title>
 <v-data-table 
    :headers="headers"
    :search="search"
    single-expand
    :items="voyages"
    sort-by="Ville de départ"
    class="elevation-1"
    >
   <template v-slot:item.actions="{ item }">
      <v-btn 
      depressed
       small 
       rounded
       dark
       color="blue darken-3"
       class="mt-2"
       @click="detailsitem(item)">
       Details
       </v-btn>

        <v-btn 
      depressed
       small 
       rounded
       dark
       class="mt-1"
       @click="dialog2 = true, el=item">
       Modifier
       </v-btn>
       
     <v-btn 
      depressed
       small 
       rounded
       dark
       color="red darken-2"
       class="mt-1"
        @click="dialog3 = true , el = item"
       >
      Supprimer
       </v-btn>

        <v-btn 
       small 
       rounded
       dark
       color="grey lighten-1"
       class="mt-1 mb-2 ml"
       style="font-size: 11px"
       @click="reservation(item)"
      >
       Reservations
       </v-btn>

    </template> 

    </v-data-table>
    </v-card>
    </template>
  </section>
   <v-snackbar
      v-model="snackbar"
    >
      {{ txt }}
      <v-btn
        color="red"
        text
        @click="snackbar = false"
      >
        Fermer
      </v-btn>
    </v-snackbar>
  </v-app>
</template>

<script>
import axios from 'axios'
import { Datetime } from 'vue-datetime';
export default {
   components:{Datetime},
  data(){
    return{
      dialog:false,
      voyages:[],
      modal : false,
      el:'',
      valid:'',
      depart:'',
      hdepart:'',
      harrive:'',
      reserv:[],
      details:[],
      dialog2:false,
      dialog3:false,
      dialog:false,
      search:'',
      modal:false,
      snackbar:false,
      txt:'',
      overlay:false,
      colors: [
          'green',
          'secondary',
          'yellow darken-4',
          'red lighten-2',
          'orange darken-1',
        ],
      headers: [
        {
          text: '#numéro du voyage',
          align: 'start',
          sortable: false,
          value: 'user_number',
        },
        { text: "Nom de l'utilisateur", value: 'name_user' },
        { text: 'Ville de départ', value: 'origin' },
        { text: "Ville d'arrivée", value: 'destination' },
        { text: 'Date de départ', value: 'date_depart' },
        { text: "Date de d'arrivée", value: 'date_arrive' },
        { text: 'Prix du kilo(€)', value: 'price_kilo' },
        { text: 'Kilos de départ', value: 'kilo' },
        { text: '', value: 'actions', sortable: false },
      ],

    }
  },

  methods :{
    /* rese(){
      let user = JSON.parse(localStorage.getItem('user')) 
          axios.get('https://safe-community.fr/lara/api/admin/voyages',{
            id : user.id
          },{
            headers:{
            Authorization: "Bearer " + localStorage.getItem('token')
            } 
          })
          .then(response => {
            this.reservations = response.data
          })
    }, */

    deletereserv(){
       axios.post('https://safe-community.fr/lara/api/reservation/annuler',{
            author : 'admin'
            },{
        headers:{
            Authorization: "Bearer " + localStorage.getItem('token')
            } 
            })
            .then(response => {
                this.modal = false
                console.log('Supprimer reservation Voyage : ',response)
                this.txt = response.data.message
                this.snackbar = true
            })
            .catch(error => {
               this.txt = response.data.message
               this.snackbar = true
            })
    },

    deletevoy(){
      axios.post('https://safe-community.fr/lara/api/admin/v_annuler',{
            id: this.el.id
            },{
        headers:{
            Authorization: "Bearer " + localStorage.getItem('token')
            } 
            })
            .then(response => {
                this.dialog3 = false
                console.log('Supprimer Voyage : ',response)
                this.voyages.splice(this.voyages.indexOf(this.el),1) 
                this.txt = response.data.message
                this.snackbar = true
            })
            .catch(error => {
              this.dialog3 = false
               this.txt = error.response.data.message
               this.snackbar = true
            })
    },

    detailsvoy(item){
         axios.get(`https://safe-community.fr/lara/api/post/${item.id}`,
         {
      headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
       }
        })
      .then(response => {
        this.details = response.data.post
        console.log('details du voyage : ',response)
        this.dialog = true
      })
    },

    detailsitem(item){
          axios.get(`https://safe-community.fr/lara/api/post/${item.id}`,
          {
        headers:{
            Authorization: "Bearer " + localStorage.getItem('token')
        }
          })
        .then(response => {
          this.details = response.data.post
          console.log('details du voyage : ',response)
          this.dialog = true
        })
          

        },

    editvoy(){
      this.spinner = true
      axios.put(`https://safe-community.fr/lara/api/admin/v_update/${this.el.id}`,
          {
            date_depart : this.el.date_depart,
            heure_depart: this.el.hdepart,
            date_arrive : this.el.date_arrive,
            heure_arrive: this.el.harrive,
            kilo : this.el.kilo,
          },
         {
      headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
      }
        })
        .then(response => {
          this.spinner = false
           console.log('Modification voyage : ' ,response.data)
            let voy = {
           id:this.el.id,
           name_user:this.el.name_user,
           origin :this.el.origin,
           destination :this.el.destination,
           price_kilo : this.nvprix,
           kilo : this.el.kilo,
           kilo_available : this.el.kilo_available,
            date_depart : this.el.date_depart,
            heure_depart: this.el.hdepart,
            date_arrive : this.el.date_arrive,
            heure_arrive: this.el.harrive,
            kilo : this.el.kilo,
           statut : this.el.statut
         }
           this.voyages.splice(this.voyages.indexOf(this.el), 1, response.data.post)
           this.dialog2 = false
           this.snacbar =true
           this.txt = response.data.message
        })
        .catch(error => {
          this.dialog2 = false
          this.spinner = false,
          this.snackbar = true,
          this.txt = error.response.data.message
        })
        
    },

    reservation(item){
       axios.get(`https://safe-community.fr/lara/api/voyage/reservation/accepted/${item.id}`)
        .then(response => {
          this.reserv = response.data
          this.modal = true
          console.log('Reservation accpetées : ',response)
        })
    }
  },
  created(){
    axios.get('https://safe-community.fr/lara/api/admin/voyages',{
            headers:{
            Authorization: "Bearer " + localStorage.getItem('token')
            } 
          })
          .then(response => {
            console.log('tous les voyages',response);           
            this.voyages = response.data
          })
  }
}
</script>

<style>

</style>


