  <template>
<div>  
  <mdb-navbar expand="xl" class="mycolor" hamburger togglerIcon="paper-plane" position="top" far dark color="indigo darken-2">
      <!-- Navbar brand -->
      <mdb-navbar-brand class="text-white">Safe Community</mdb-navbar-brand> 
      <div class="col"></div>   
    <span class="white-text" style="opacity:50%">Menu</span>
      <mdb-navbar-toggler>
        <mdb-navbar-nav right>
          <router-link :to="{name: 'home'}" ><mdb-nav-item href="#" waves-fixed>Accueil</mdb-nav-item></router-link>
           <router-link :to="{name: 'register'}" > <mdb-nav-item href="#" waves-fixed>Inscription</mdb-nav-item></router-link>
          <router-link :to="{name: 'contact'}" ><mdb-nav-item href="#" waves-fixed>Contactez-nous</mdb-nav-item></router-link>
          <router-link :to="{name: 'contact'}" ><mdb-nav-item href="#" waves-fixed>Aide</mdb-nav-item></router-link>
        </mdb-navbar-nav>
      </mdb-navbar-toggler>
    </mdb-navbar>
  <div class="classic-form-page">
      <mdb-mask class="d-flex gradient justify-content-center align-items-center">
        <mdb-container>
          <mdb-row>
            <div class="white-text text-center text-md-left col-md-6 mt-xl-5 mb-5">
              <h1 class="h1-responsive font-weight-bold animated fadeInLeft">Safe Community</h1>
              <hr class="hr-light animated fadeInLeft" />
              <h6
                class="ml-5 ml-n2 font-weight-bold mb-4 animated fadeInLeft" style="font-size:25px;color: black"
              >Expédiez l'esprit tranquille</h6>
            </div>
            <mdb-col md="6" xl="5" class="mb-4 animated fadeInRight">
              <!-- Card -->
              <mdb-card class="mt-5">
                
                <mdb-card-body>
                  <form @submit.prevent="submit">
                    <p class="h4 text-center py-4">Connectez-vous!</p>
                     <div class="alert" v-bind:class="{'alert-danger': alert}"> {{ message }} </div>
                    <div class="grey-text">
                      <mdb-input label="Email" icon="envelope" group type="email" validate error="wrong" success="right" id="email"
                    v-model="email"  />
                      <mdb-input label="Mot de passe" icon="lock" group type="password" validate id="password" v-model="password" />
               
                    </div>
                    <div class="text-center py-4 mt-3">
                      <mdb-btn class="btn btn-cyan rounded" type="submit" >Connexion</mdb-btn><br>
                      <router-link :to="{name: 'reset'}" class="font-italic dark-grey-text ml" style="font-size:11px" >Mot de passe oublié</router-link>
                    </div>
                  </form>
                    <div class="row">
                      <div class="col-5"></div>
                    <div v-bind:class="{'spinner-border': spinner}" class="ml-3" role="status">

  </div>
  </div>
                   <mdb-col md="" class="d-flex justify-content-end">
                  <p class="font-small grey-text mt-3">Pas de compte? <router-link :to="{name: 'register'}" class="dark-grey-text ml-1 font-weight-bold" > Inscription</router-link> </p>
                </mdb-col>
                </mdb-card-body>
              </mdb-card>
              <!-- Card -->
            </mdb-col>
            
          </mdb-row>
        </mdb-container>
      </mdb-mask>
    

    <mdb-container>
      <mdb-row class="py-5">
        <mdb-col md="12" class="text-center">
          &copy; 2020 Copyright: <a href="https://Safe-community.fr"> Safe-community.fr </a>
        </mdb-col>
      </mdb-row>
    </mdb-container>
  </div>
  
  </div>
</template>



<script>
import axios from 'axios';
import store from '../store/index'
export default {

  data (){
    return {
        email: '',
        password: '',
         alert:'',
         message:'',
        token:'',
        user:'',
        spinner:''
    }
  }, 
  methods:{
     submit(){
/*        this.alert = false
       this.message = ""
       this.spinner = true
        let email = this.email 
        let password = this.password
        this.$store.dispatch('login', { email, password })
       .then(reponse =>{
         this.user = response.data.user,
         localStorage.setItem('user',JSON.stringify(this.user)),
        this.$router.push({ name: 'home' })
        })
       .catch(error =>{
         if (error) {
           if(error.response.data.message){
             this.message = error.response.data.message
             this.spinner = false
              this.alert = true
           }
           else{
             this.spinner = false
            this.alert = true
            console.log(error.response)
            this.message = "Connexion impossible veuillez reessayer plus tard"
           }
           
          }
       })
 */      

       this.alert = false
       this.message = ""
       this.spinner = true 
       axios.post('https://safe-community.fr/lara/api/auth/login',{
         email:this.email,
         password:this.password
       })
       .then(response => {
          if (response.status == 200) {
         this.token = response.data.token
         localStorage.setItem('token', this.token)
         this.user = response.data.user;
         localStorage.setItem('user',JSON.stringify(this.user))
         store.commit('auth_success', this.token, this.user)
         this.$router.push({ name: 'home' }); 
          }
       })
       .catch(error =>{
         if (error) {
           if(error.response.data.message){
             this.message = error.response.data.message
             this.spinner = false
              this.alert = true
           }
           else{
             this.spinner = false
            this.alert = true
            console.log(error.response)
            this.message = "Echec de connexion"
           }
           
          }
       }) 
    
  }

  },
  created(){
    
  }

 


    
}
</script>



<style>
.classic-form-page .view {
 /* The image used */
  background-image: url("https://cdn.pixabay.com/photo/2019/06/27/06/49/plane-4301615_960_720.png");

  /* Full height */
  height: 100%;

  /* Center and scale the image nicely */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.classic-form-page .gradient {
  background-image: url("https://cdn.pixabay.com/photo/2019/06/27/06/49/plane-4301615_960_720.png");

  /* Full height */
  height: 100%;

  /* Center and scale the image nicely */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.classic-form-page h6 {
  line-height: 1.7;
}

.classic-form-page .navbar {
  transition: background 0.5s ease-in-out, padding 0.5s ease-in-out;
}

.classic-form-page .navbar .md-form {
  margin: 0;
}

.top-nav-collapse {
  background: #424f95 !important;
}

@media (max-width: 768px) {
  .classic-form-page .navbar:not(.top-nav-collapse) {
    background: #424f95 !important;
  }
}



body, html {
  height: 100%;
}

</style>


