<template>
<div>
  <nav class="fixed-top">
  <mdb-navbar expand="xl" hamburger far togglerIcon="paper-plane" position="top" dark color="mdb-color">
      <!-- Navbar brand -->
 <mdb-navbar-brand href="https://mdbootstrap.com/">
      <img class="animated bounce infinite" src="https://mdbootstrap.com/img/logo/mdb-transparent.png" height="30" alt="">
    </mdb-navbar-brand>
    <div class="col"></div>   
    <span v-if="auth==true" @click="menu" class="white-text" style="font-size:12px;opacity:80%">Bonjour {{ user.surname}} 🙂</span>
    <span v-if="auth==false" class="white-text">  <v-btn rounded to="login" class="d-xs-none" color="mdb-color" dark>Se connecter</v-btn></span>      
        <mdb-navbar-toggler>
           <mdb-navbar-nav right>
             <mdb-nav-item href="#" waves-fixed v-if="auth==true" disabled ></mdb-nav-item>
          <mdb-nav-item href="#" waves-fixed v-if="auth==true" to="home" >Accueil</mdb-nav-item> 
          <mdb-nav-item href="#" v-if="auth==true" to="dashboard/transactions" waves-fixed>Mon compte</mdb-nav-item>
          <mdb-nav-item href="#" v-if="auth==true" to="travels" waves-fixed>Tous les voyages</mdb-nav-item>
           <mdb-nav-item href="#" waves-fixed to="contact" >A propos</mdb-nav-item>
           <mdb-nav-item href="#" waves-fixed to="contact"> Contactez-nous</mdb-nav-item>
           <mdb-nav-item href="#" v-if="auth==true" waves-fixed @click.prevent="modal = true ">Déconnexion</mdb-nav-item>
        </mdb-navbar-nav>
       </mdb-navbar-toggler>
    </mdb-navbar> 
   <mdb-modal size="sm" :show="modal" @close="modal = false">
        <mdb-modal-header>
            <mdb-modal-title class="font-weight-bold">Déconnexion</mdb-modal-title>
        </mdb-modal-header>
        <mdb-modal-body>Vous allez etre déconnecté!
</mdb-modal-body>
        <mdb-modal-footer>
      <mdb-btn color="secondary" size="sm" @click="logout()">Ok</mdb-btn>
        </mdb-modal-footer>
    </mdb-modal>
    </nav>
  </div>
</template>

<script>
export default {
name:'navbar',

data(){
  return{
    user:'',
    auth:false,
    modal: false,
    hamburger:false,
  }
},
 methods: {
   logout() {
          localStorage.removeItem('token')
          localStorage.removeItem('user')
          this.$router.push('/login')
      
      },

    menu(){
      this.hamburger = true
    },
     callFunction () {
            setTimeout(function () {
                this.modal = true
            }, 900000);
        },


    /* logout(){
     localStorage.removeItem('user')
      localStorage.removeItem('notif')
      localStorage.removeItem('token')
       this.$router.push({ name: 'login'}); 
        }, */
      refresh(){
        location.reload();
      } 
  },
    created(){
      console.log('localstorage:',localStorage.getItem('user'))
     if(localStorage.getItem('token') == null){ /* on regarde si le token ne contient rien */
          this.user = ''
          this.auth =false
          this.user.surname =''
      }
      else{
        this.user = JSON.parse(localStorage.getItem('user'))
        this.auth = true
      }
        
        
     
    },
     mounted () {
      this.callFunction()
    },


}
</script>

<style>

</style>


