<template>
  <div>
          <form class="mt-5" @submit.prevent="addgodson">
    <mdb-card cascade class="text-left">
                          <mdb-card-header class="mdb-color lighten-3 white-text z-depth-1-half mb-3 mx-4 mt-n3 mb-n2 rounded">
                            <h4 class="title text-center font-weight-bold"><fa class="fas fa-plus" />Ajouter filleul</h4>
                          </mdb-card-header>
                          
                          <mdb-card-body class="grey-text">
                              <div class="alert" v-bind:class="{'alert-success': success}"> {{ message }} </div>
                              <mdb-input size="sm" icon="envelope" placeholder="Adresse Email du filleul" v-model="email_godson" group type="email" validate error="wrong" success="right" />
                          </mdb-card-body> 
                          <mdb-card-footer>
                            <div data-v-11613719="" data-v-732af18d="" class="col-md-12 text-center">
                              <input type="submit" value="Ajouter" class="btn btn-info btn-rounded"></div>
                              <div class="row"> <div class="col"><a href="#" @click.prevent="showModal = true,  modal= false, godsonlist" class="ml-5"> <a href="#" @click.prevent="godsonlist">Tous mes filleuls</a></a></div> </div> 
                               <div v-bind:class="{'spinner-border': spinner}" class="ml-3" role="status"></div>                 
                          </mdb-card-footer>
                        </mdb-card>
          </form>
          <mdb-container> 
    <mdb-modal :show="showModal" @close="showModal = false,  modal= true" scrollable>
      <mdb-modal-header>
        <mdb-modal-title>Tous mes filleuls</mdb-modal-title>
      </mdb-modal-header>
      <mdb-modal-body>
        <mdb-list-group flush v-for="godson in godsons" v-bind:key="godson">
    <mdb-list-group-item>{{ godson.email_godson }} inscrit le {{godson.created_at}} </mdb-list-group-item>
  </mdb-list-group>
        </mdb-modal-body>
      <mdb-modal-footer>
        <mdb-btn color="secondary" @click.native="showModal = false, modal =true ">Close</mdb-btn>
      </mdb-modal-footer>
    </mdb-modal>
  </mdb-container>
  </div>
</template>

<script>
import axios from 'axios';
export default {
    data(){
        return{
         modal:false,
         email_godson:'',
         showModal: false,
         spinner:'',
         success:'',
         alert:'',
         message:'',
         godsons:'',
        }
    },
    methods:{
        /* Liste des flilleuls */
     godsonlist(){
       
    axios.get("https://safe-community.fr/lara/api/godson",{
       headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        }
      })
    .then(response => {
      this.godsons = response.data
      console.log(response)
    })
    .catch(error => {
      console.log(error)
    })
    /* Liste des flilleuls -FIN */
     },
     /* Anouter un flilleul */
     addgodson(){
       this.alert = false
       this.message = ""
       this.spinner = true
      axios.post("https://safe-community.fr/lara/api/godson",{
        email_godson: this.email_godson
      },{
        headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        }
      })
      .then(response => {
 if(response.status == 200){
  this.success = true
  this.message = "Ajout effectué"
  this.spinner = false
  this.email_godson=''
      }
  }).catch(error => {
if (error) {
   this.spinner = false
            this.alert = true
            this.alert = true
            this.message = "Utilisateur existant"
          }
    console.log(error)
    })
         },
         /* Anouter un flilleul -FIN */
    }
}
</script>

<style>

</style>


