<script>
/* import { mapGetters } from 'vuex'
import { mapState } from 'vuex' */
import assurance from './assurance'
import profil from './profil'
import axios from 'axios';
import store from '../store/index'

export default {
  store:store,
  components:{
assurance,
profil
  },

  data () {
    return {
      activeItem: 1,
      user:[],
      fonction:'',
      content:'',
      idcard:'',
      adminshow:false,
      txt:'Aller a admin'
    }
  },
    computed: {
    count: function () {
      return store.state.count
    }
  }, 
   methods:{
     /* AUto logout */
      /* callFunction: function () {
            var v = this;
            setTimeout(function () {
                 localStorage.removeItem('user')
                 localStorage.removeItem('token')
                v.$router.push({ name: 'login'})
                v.showModal = true
            }, 900000);
        } */

         /* Déconnexion */
    changer(){
      this.adminshow = !this.adminshow
      if (this.adminshow == true) {
        this.txt = 'Mode Utilisateur'
        this.$router.push({name: 'd.homeA'})
        this.activeItem = 2
        
      }
      else if(this.adminshow == false){
        this.txt = 'Aller à admin'
        this.activeItem = 1
         this.$router.push({name: 'd.transactions'})
      }
    },
     logout(){
      localStorage.removeItem('user')
      localStorage.removeItem('token')
       this.$router.push({ name: 'login'});
        },
        chargepiece(){
        axios.get("https://safe-community.fr/lara/api/idpiece/get",{
       headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        }
      })
    .then(response => {
      this.idcard = response.data
      console.log(response)
    })
    .catch(error => {
      console.log(error)
    })
    },
    },
    /* Déconnexion -FIN*/
  beforeMount () {
   /*  associer la classe active au menu de la sidebar selectioné */
    this.activeItem = this.$route.matched[0].props.default.page
  },

  


  created(){
    this.chargepiece()
    console.log(localStorage.getItem('user'), localStorage.getItem('token'))
    this.content = localStorage.getItem('notif')

   /*  verification si l'utilisateur est coonnecté --DEBUT*/
if(localStorage.getItem('token') == null){ /* on regarde si le token ne contient rien */
          this.$router.push({ name: 'login', query: { redirect: '/login' } });
      }
      else{
        this.user = JSON.parse(localStorage.getItem('user'))
        console.log('fontion du gar: ',this.user.function)
        this.function = this.user.function
      }
       /*  verification si l'utilisateur est coonnecté --END*/


  },
   mounted () {

    },

}


</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
main {
  background-color: #EDEDEE;
}

.flexible-content {
  transition: padding-left 0.3s;
  padding-left: 270px;
}

.flexible-navbar {
  transition: padding-left 0.5s;
  padding-left: 270px;
}

.sidebar-fixed {
  left: 0;
  top: 0;
  height: 100vh;
  width: 270px;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
  z-index: 1050;
  background-color: #fff;
  padding: 1.5rem;
  padding-top: 0;
}

.sidebar-fixed .logo-wrapper img{
  width: 100%;
  padding: 2.5rem;
}

.sidebar-fixed .list-group-item {
  display: block !important;
  transition: background-color 0.3s;
}

.sidebar-fixed .list-group .active {
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
  border-radius: 5px;
}

@media (max-width: 1199.98px) {
  .sidebar-fixed {
    display: none;
  }
  .flexible-content {
    padding-left: 0;
  }
  .flexible-navbar {
    padding-left: 10px;
  }
}
</style>
<style>
.navbar-light .navbar-brand {
  margin-left: 15px;
  color: #2196f3 !important;
  font-weight: bolder;
}
.btn-group-sm .btn-fab{
  position: fixed !important;
  right: ;: 29px;
}
.btn-group .btn-fab{
  position: fixed !important;
  left: 5px;
}
#main{
  bottom: 20px;
}
#mail{
  bottom: 80px
}
#sms{
  bottom: 125px
}
#autre{
  bottom: 170px
}
</style>



