<template>
<!-- Consulter les réservations, annuler une réservation -->
<v-app style="background-color:rgb(236,236,237)">
  <section>
    <h3 class="font-weight-bold text-center h3 mb-3 mt-5">Toutes les réservations en cours</h3>
            <v-dialog
    v-model="dialog"
    max-width="290"
    >
      <v-card>
        <v-card-title color="red" class="headline"> Supprimer un réservation</v-card-title>
        <v-card-text>
        <p>Cette action est irreversible, voulez-vous continuer?</p>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
        <v-btn color="blue darken-1" text @click="deletereserv()">Continuer</v-btn>
        <v-btn color="red" text @click="dialog = false">Annuler</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  <v-card>
    <v-card-title>
      <v-text-field
        v-model="search"
       
        label="Rechercher(utilisateur, kilos, destination...)"
        single-line
        hide-details
      ></v-text-field>
    </v-card-title>
    <v-data-table
      :headers="headers"
      :items="reservations"
      :search="search"
    >
    <template v-slot:item.actions="{ item }">
       
     <v-btn 
      depressed
       small 
       rounded
       dark
       color="red darken-2"
       class="mt-1"
        @click="dialog = true,el = item "
       >
       Supprimer
       </v-btn>

    </template> 
    </v-data-table>
  </v-card>
  </section>
  <v-snackbar
      v-model="snackbar"
    >
      {{ txt }}
      <v-btn
        color="red"
        text
        @click="snackbar = false"
      >
        Fermer
      </v-btn>
    </v-snackbar>
  </v-app>
</template>

<script>
import axios from 'axios'
export default {
    data(){
        return{
            search:'',
            reservations:[],
            el:'',
            snackbar:false,
            dialog:false,
            txt:'',
            headers: [
                    {
                text: "#Numéro de la réservation",
                align: 'start',
                sortable: false,
                value: 'user_number',
                
                },
                 { text: "Nom de l'utilisateur", value: 'depart' },
                { text: 'Ville de départ', value: 'depart' },
                { text: "Ville de d'arrivée", value: 'arrive' },
                { text: 'Date de départ', value: 'date_depart' },
                { text: "Date de d'arrivée", value: 'date_arrive' },
                { text: 'Poids du colis(Kg)', value: 'kilo' },
                { text: 'Prix du kilos(€)', value: 'price_kilo' },
                { text: 'Détails', value: 'description' },
                { text: 'Cout global de la réservation(€)', value: 'amount' },
                { text: '', value: 'actions', sortable: false },
                ],
        }
    },

    methods : {
        allreservations(){
             let user = JSON.parse(localStorage.getItem('user')) 
          axios.get('https://safe-community.fr/lara/api/admin/reservations',{
            headers:{
            Authorization: "Bearer " + localStorage.getItem('token')
            } 
          })
          .then(response => {
            console.log(response);
            this.reservations = response.data
          })
        },

        deletereserv(){
            axios.post('https://safe-community.fr/lara/api/reservation/annuler',{
            id: this.el.id
            },{
        headers:{
            Authorization: "Bearer " + localStorage.getItem('token')
            } 
            })
            .then(response => {
                this.dialog = false
                console.log('Supprimer réservation : ',response)
                this.reservations.splice(this.reservations.indexOf(el),1) 
                this.txt = response.data.message
                this.snackbar = true
            })
            .catch(error => {
              console.log(error);
              this.dialog = false
              this.txt = error.response.data.message
              this.snackbar = true
            })
        }
    },
    created(){
      this.allreservations()
    }
}
</script>

<style>

</style>


