<template>
    <section class="mt-5">
     <h3 class="font-weight-bold text-center h3 my-3 mt-5">Historique des transactions</h3>
     <mdb-card>
    <mdb-tabs class="mb-5 my-2 mx-2"
    :active="0"
    default
    :links="[
      { text: 'Reçus', slot: 'custom-slot' },
      { text: 'Reglements' },
      { text: 'Remboursements' }]"
      
    :transition-duration="0.5"
    transition-style="linear"
  >
  <template :slot="'custom-slot'">
    <v-data-table
    :headers="headers3"
    :items="reçus"
    sort-by="Date"
    class="elevation-1"
  >
  </v-data-table>
    </template>
    <template :slot="'Reglements'">
    <v-data-table
    :headers="headers5"
    :items="reglements"
    sort-by="Date"
    class="elevation-1"
  >
  </v-data-table>
    </template>

    <template :slot="'Remboursements'">
    <v-data-table
    :headers="headers4"
    :items="remboursements"
    sort-by="Date"
    class="elevation-1"
  >
  </v-data-table>
    </template>
    
    
  </mdb-tabs>
  </mdb-card>
  </section>
</template>

<script>
import axios from 'axios'

export default {

  data () {
    return {
      remboursements:[],
      reglements:[],
      reçus:[],
      headers3: [
        {
          text: "#Numéro de la facture",
          align: 'start',
          sortable: false,
          value: 'num_facture',
          
        },
         { text: "#Date", value: 'date' },
         { text: 'Intitulé', value: 'intitule' },
         { text: "Description", value: 'description' },
         { text: 'Montant(€)', value: 'total_price' },
         { text: "Statut", value: 'statut' },
        { text: '', value: 'actions', sortable: false },
      ],
      
      headers4: [
        {
          text: "Entreprise",
          align: 'start',
          sortable: false,
          value: 'custumer',
          
        },
         { text: "#Numéro de la réservation à rembourser", value: 'comment_id' },
         { text: "#Date", value: 'date' },
         { text: 'Intitulé', value: 'intitule' },
         { text: "Description", value: 'description' },
         { text: 'Montant(€)', value: 'total_price' },
         { text: "Statut", value: 'statut' },
        { text: '', value: 'actions', sortable: false },
      ],
      headers5: [
        {
          text: "Numéro du voyage",
          align: 'start',
          sortable: false,
          value: 'post_id',
          
        },
         { text: "#Date", value: 'date' },
         { text: 'Intitulé', value: 'intitule' },
         { text: 'Montant(€)', value: 'rem_voyageur' },
         { text: "Statut", value: 'statut' },
        { text: '', value: 'actions', sortable: false },
      ],
      reservations : [],
      voyages : [],
      penalites :[]
    }
  },
  methods: {

  },
  created(){
    axios.get('https://safe-community.fr/lara/api/recus',{  //recupreration des reçus
        headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        }
      })
      .then(response => {
         console.log(response);
          this.reçus =  response.data
          console.log('Penalites en attente : ',this.wait)          
      })

      axios.get('https://safe-community.fr/lara/api/reglements',{  //recupreration des reglements
        headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        }
      })
      .then(response => {
         console.log(response);
          this.reglements =  response.data
          console.log('Voyages en attente : ',this.wait)          
      })

       axios.get('https://safe-community.fr/lara/api/remboursements',{  //recupreration des remboursements
        headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        }
      })
      .then(response => {
         console.log(response);
          this.remboursements =  response.data
          console.log('Réservations en attente : ',this.wait)          
      })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.full {
  height: 70vh;
}

</style>



