<template>
  <div>
        <mdb-modal frame position="bottom" direction="bottom" :show="modal" @close="modal = false, spinner=false">
        <mdb-modal-body class="text-center">
            <span> {{ message3}} </span>
            <mdb-btn color="secondary" @click.native="spinner=false, modal = false">Fermer</mdb-btn>
        </mdb-modal-body>
    </mdb-modal>
     
     <mdb-modal :show="modal3" @close="modal3 = false">
            <mdb-modal-header>
                <mdb-modal-title>Payer réservation</mdb-modal-title>
            </mdb-modal-header>
            <mdb-modal-body>
               Veillez choisir le mode de payement
               
               <v-container fluid>
    <v-radio-group v-model="radios" :mandatory="false" row>
      <v-radio  color="orange" label="Orange money" value="orange money"></v-radio>
      <v-radio  color="yellow"  label="Mtn mobile money" value="mtn mobile money"></v-radio>
      <v-radio  color="green" label="Espèces" value="espèces"></v-radio>
      <v-radio color="success"  label="Virement Bancaire" value="virement bancaire"></v-radio>
       <v-radio  color="blue" label="Paypal" value="paypal"></v-radio>
    </v-radio-group>
  </v-container>

            </mdb-modal-body>
            <mdb-modal-footer  style="height:50px" >
                <mdb-btn size="sm" class="rounded" color="danger" @click.native="modal3 = false">Annuler</mdb-btn>
                <mdb-btn size="sm" class="rounded" color="primary" @click="reserv()">Oui</mdb-btn>
            </mdb-modal-footer>
        </mdb-modal>

    <mdb-modal :show="modal2" @close="modal2 = false">

            <mdb-modal-header>
                <mdb-modal-title>Payer assurance </mdb-modal-title>
            </mdb-modal-header>
            <mdb-modal-body>
               Veillez choisir le mode de payement
               
               <v-container fluid>  
    <v-radio-group v-model="radios" :mandatory="false" row>
      <v-radio  color="orange" label="Orange money" value="orange money"></v-radio>
      <v-radio  color="yellow"  label="Mtn mobile money" value="mtn mobile money"></v-radio>
      <v-radio  color="green" label="Espèces" value="especes"></v-radio>
      <v-radio color="success"  label="Virement Bancaire" value="virement bancaire"></v-radio>
       <v-radio  color="blue" label="Paypal" value="paypal"></v-radio>
    </v-radio-group>
  </v-container>

            </mdb-modal-body>
            <mdb-modal-footer  style="height:50px" >
                <mdb-btn size="sm" class="rounded" color="danger" @click.native="modal2 = false">Annuler</mdb-btn>
                <mdb-btn size="sm" class="rounded" @click="assurance()" color="primary">Oui</mdb-btn>
            </mdb-modal-footer>
          
        </mdb-modal>

        <v-bottom-navigation
    v-model="bottomNav" color="red"
  >
    <v-btn @click="modal2 = true" value="favorites">
      <span>Payer assurance</span>
      <v-icon>mdi-heart</v-icon>
    </v-btn>

    <v-btn @click="modal3 = true" value="nearby">
      <span>Payer réservation</span>
      <v-icon>mdi-map-marker</v-icon>
    </v-btn>
  </v-bottom-navigation>
      <mdb-card cascade class="mt-5 text-left">
        
                          <mdb-card-header class="mdb-color lighten-3 white-text z-depth-1-half mb-3 mx-4 mt-n3 mb-n2 rounded">
                            <h4 class="title text-center font-weight-bold"><fa class="mx-2 fas fa-dollar-sign" style="font-size:30px"  />Moyens de paiement</h4>
                          </mdb-card-header>
                          <mdb-card-body class="grey-text">
                              <mdb-row>
                              <mdb-col>

                                  <p class="font-weight-bold text-center h1">Informations bancaire(RIB)</p>
                                  <form @submit.prevent="rib">
                              <div class="alert" v-bind:class="{'alert-success': success}"> {{ message }} </div>
                            <mdb-input size="sm" icon="user" placeholder="Code Banque" v-model="code_banque" group type="text" validate error="wrong" success="right" required/>
                            <mdb-input size="sm" icon="user-circle" placeholder="Code guichet" v-model="code_guichet" group type="text" validate error="wrong" success="right" required/>
                             <mdb-input size="sm" icon="phone" placeholder="Numéro de compte" v-model="numero_de_compte" group type="number" validate error="wrong" success="right" required/>
                              <mdb-input size="sm" icon="envelope" placeholder="Clé RIB" v-model="cle_RIB" group type="text" validate error="wrong" success="right"  required/>
                              <mdb-input size="sm" icon="user-circle" placeholder="Domiciliation" v-model="domiciliation" group type="text" validate error="wrong" success="right" required/>
                              <mdb-input size="sm" icon="user-circle" placeholder="Code IBAM" v-model="code_IBAM" group type="text" validate error="wrong" success="right" required/>
                              <mdb-input size="sm" icon="user-circle" placeholder="Code BIC" v-model="code_BIC" group type="text" validate error="wrong" success="right" required/>
                               <v-file-input multiple v-model="file" label="File input"></v-file-input>
                             <p class="text-center"><mdb-btn class="btn btn-outline-blue center" type="submit" style=""> <div v-bind:class="{'spinner-border': spinner}" class="h-75" role="status"></div>Valider</mdb-btn></p> 
                             
                             </form>
                         </mdb-col>
                         </mdb-row>
                         <mdb-row>
                             <mdb-col sm="12" >
                                 <p class="font-weight-bold text-center h1">Paypal</p>
                                 <form @submit.prevent="momo" >
                                     <div class="alert" v-bind:class="{'alert-success': success2}"> {{ message4 }} </div>
                                  <mdb-input size="sm" icon="user" placeholder="Adresse email" v-model="email" group type="email" validate error="wrong" success="right" required/>
                            <mdb-input size="sm" icon="user-circle" placeholder="Nom d'utilisateur" v-model="namepaypal" group type="text" validate error="wrong" success="right" required/>
                             <p class="text-center"><mdb-btn class="btn btn-outline-blue center" type="submit" style=""><div v-bind:class="{'spinner-border': spinner2}" class="h-25" role="status"></div> Valider</mdb-btn></p>
                             
                            </form>
                             </mdb-col>
                    </mdb-row> 
                         <mdb-row>
                             <mdb-col sm="12" >
                                 <p class="font-weight-bold text-center h1">Mobile Money</p>
                                 <form @submit.prevent="momo" >
                                     <div class="alert" v-bind:class="{'alert-success': success2}"> {{ message2 }} </div>
                                 <mdb-input  v-model="nomMOMO" class="my-0" placeholder="Nom mobile money" required>
                                <span class="teal-text input-group-text px-1" slot="prepend"></span>
                                <mdb-input v-model="prenomMOMO" noWrapper   placeholder="Prenom mobile money" required/>
                                </mdb-input>
                            <mdb-input size="sm" icon="user-circle" placeholder="Télephone" v-model="number" group type="number" validate error="wrong" success="right" required/>
                             <mdb-input size="sm" icon="user-circle" placeholder="Numéro carte d'identité" v-model="cardid" group type="number" validate error="wrong" success="right" required/>
                              <v-file-input multiple label="File input"></v-file-input>
                             <p class="text-center"><mdb-btn class="btn btn-outline-blue center" type="submit" style=""><div v-bind:class="{'spinner-border': spinner2}" class="h-25" role="status"></div> Valider</mdb-btn></p>
                             
                            </form>
                             </mdb-col>
                    </mdb-row> 
                          </mdb-card-body>
                        </mdb-card>
 <v-snackbar
      v-model="snackbar"
      :multi-line="multiLine"
    >
      {{ txt }}
      <v-btn
        color="red"
        text
        @click="snackbar = false"
      >
        Fermer
      </v-btn>
    </v-snackbar>
  </div>
</template>
<script>
import axios from 'axios'
import store from '../store'
export default {
    data(){
        return{
           radios:'',
          modal2:false,
          modal3:false,
          nomMOMO:'',
          prenomMOMO:'',
          message4:'',
          file:'',
          multiLine: true,
          snackbar: false,
            spinner : false,
            spinner2 : false,
            success:false,
            success2:false,
            modal:false,
            message2:'',
            message3:'',
            txt:'',
            File:[],
            message:'',
            name:'',
            number:'',
            code_banque:'',
            code_guichet:'',
            numero_de_compte:'',
            cle_RIB:'',
            domiciliation:'',
            code_IBAM:'',
            namepaypal:'',
            email:'',
            code_BIC :'',
            filecard:'',
            reseau:'',
            surname:'',
            cardid:'',
            ipaypal:'',
            irib:'',
            imobile:'',
        }
    },
    methods:{
        rib(){
             this.success = false,
            this.message = ''
            this.spinner = true
            axios.post('https://safe-community.fr/lara/api/rib',{
                code_banque : this.code_banque,
                 code_guichet:this. code_guichet,
                 numero_de_compte:this.numero_de_compte,
                 domiciliation:this.domiciliation,
                 cle_RIB:this.cle_RIB,
                 code_IBAM:this.code_IBAM,
                 code_BIC :this.code_BIC,
                 file : this.file
            },{
           headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        }
        })
        .then(response =>{
            console.log(response)
            if(response.status == 200){
                this.spinner = false
                this.success = true,
                this.message = 'Informations RIB enregistrées'
            }
        })
        },
        momo(){
             this.success2 = false,
            this.message2 = ''
            this.spinner2 = true
            axios.post('https://safe-community.fr/lara/api/mobile',{
                number :this.number,
                name:this.name,
                surname:this.surname,
                reseau:this.reseau,
                file: this.filecard,
                id_card_number:this.cardid,
            },{
           headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        }
        })
            .then(response => {
                console.log(response)
                if(response.status == 200){
                    this.spinner2 = false
                    this.success2 = true,
                    this.message2 = 'Informations Mobile money enregistrées'
                }
            })
        },
        assurance(){
            if (store.state.assur == 0) {
                this.txt = "Veuillez faire un choix d'assurance ou mettre a jour votre situation actuelle dans la rubrique assurance"
                this.snackbar = true
            }
            else{
             axios.post('https://safe-community.fr/lara/api/s_insurance',{
      insurance_id:store.state.assur,
      paid_mode:this.radios,
    },{
      headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        }
      
    })
    .then(response=>{
      console.log(response)
      if (response.status == 206) {
             this.txt = "Vous n'avez plus d'effectuer un paiement pour cette option d'assurance"
                this.snackbar = true
                store.commit('takeassur',0)
          }
      else if(response.status == 200){
          this.modal = true
          this.modal2  = false
        this.message3 = 'Un administrateur vous contactera pour effectuer le payement'
        store.commit('takeassur',0)
      }
    })
    .catch(error => {
      this.spinner = false
       this.txt = error.message
        this.snackbar = true
      console.log(error)
    })
    }
        },
        paypal(){
            this.success2 = false,
            this.message4 = ''
            this.spinner2 = true
            axios.post('https://safe-community.fr/lara/api/paypal',{
                address :this.email,
                name:this.namepaypal
            },{
           headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        }
        })
            .then(response => {
                console.log(response)
                if(response.status == 200){
                    this.spinner2 = false
                    this.success2 = true,
                    this.message4 = 'Informations paypal enregistrées'
                }
            })
        },
        created(){
          //////////////////////
           axios.get('https://safe-community.fr/lara/api/paypal',{
           headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        }
        })
            .then(response => {
                console.log(response)
                if(response.status == 200){
                   this.ipaypal = this.response.data
                }
            })
          ///////////////////////////
            axios.get('https://safe-community.fr/lara/api/mobile',{
           headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        }
        })
            .then(response => {
                console.log(response)
                if(response.status == 200){
                   this.imobile = this.response.data
                }
            })
       ///////////////////
            axios.get('https://safe-community.fr/lara/api/rib',{
           headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        }
        })
            .then(response => {
                console.log(response)
                if(response.status == 200){
                   this.irib = this.response.data
                }
            })
        }
    }
}
</script>

<style>

</style>


