<script>
import JQuery from 'jquery'
import navbar from './navbar'
import myfooter from './footer'
import axios from 'axios'
import { Datetime } from 'vue-datetime';
import { mdbToastNotification } from "mdbvue";

  export default {
    components:{ navbar,myfooter,Datetime, mdbToastNotification },
    data(){
      return{
        showModal: false,
      showModal2: false,
      showModal3: false,
    max:10,
    msg:'Prochains voyages',
    travel:{},
    voyages:[],
    infocolis:false,
    modal:false,
    subs:false,
    hide:false,
    snackbar: true,
    show: true,
    text:'',
    spinner:false,
    date:'',
    user:'',
    text: 'Bienvenue ',
    timeout: 4000,
    auth:false,
    success:false,
    Alertmessage:'',
     y: 'top',
     x:'right',
     villes:[],
     erreur:'',
     city:'',
     choosencity:'',
     choosencity2:'',
     name:'',
     subject:'',
     email:'',
     contenu:'',
     alert:false,
     alert2:false,
     message:'',
     click:null,
     time:false,
     lastravels:{},
     compagnie:'',
     name:'',
     ask_kilo:'',
     tarif:'0',
     origin:'',
     destination:'',
     date_arrive:'',
     date_depart :'',
     kilos:'',
     details:'',
     categorie:'',
     successMessage:'',
     alertSuccess:'',
     prix:'',
     id_post:'',
     txt:'',
     video: null,
     checkpointdep:'',
      choosencheckdep:'',
      choosencheckarrive:'',
      checkpointarr:{},
      checkpointdep:'',
      id_cityarr:'',
      id_citydep:'',
      videos:[],
      videos2:[],

     pictures:'',
      }
    },
    filters :{
      removeheure(value){  //pour enlever l'heure de la date
         value = value.substring(0, 10);
         return value
      }
    },
   methods:{
     filtrer(){
       this.txt = ''
       this.alert = false
       this.msg = 'Prochains voyages'
       this.txt = ''
       this.date = this.date.replace('T00:00:00.000Z','')//ça reduit le nombre de caracteres inutiles quil il ya dans la date
         axios.post('https://safe-community.fr/lara/api/indexPostsWhere',{
          origin:this.choosencity2.id,
          destination:this.choosencity.id,
          date_depart:this.date
        })
        .then(response => {
          console.log(response)
          if(response.status == 200){
            window.scrollTo(0,1750);
            console.log(response.data.data)
            this.msg = 'Resultats de votre recherche'
            this.travel = response.data.data
            this.txt = response.data.message
          }
        })
        .catch( error => {
          console.log(error)
          this.alert = true
          this.travel = this.lastravels
        })
     },
     seemore(){//fonction qui nous montre tous les voyages
       if(localStorage.getItem('token') == null){
          this.$router.push({ name: 'login' });
       }
       else{
          this.$router.push({ name: 'travels' });
       }
     },
     price(){
        let n = this.ask_kilo - Math.floor(this.ask_kilo) //Recuperer la partie décimale du nombre
        console.log('la partie decimal', n)
         if ( !(n == 0) && !(n == 0.5)) {
           if ( n < 0.5) {
              this.ask_kilo = this.ask_kilo - n
           }
           else{
              this.ask_kilo = this.ask_kilo - n
          this.ask_kilo = this.ask_kilo + 0.5
           }
           }
        this.tarif = this.ask_kilo * this.prix
        if(this.ask_kilo > this.kilos){
           this.ask_kilo = this.kilos
           this.tarif = this.ask_kilo * this.prix
        }
      },
      position(vi){
        return indexOf(vi)
      },
 mod(info){

        if(this.auth == false){
          this.$router.push({name: 'login'})
        }
         console.log(info)
         this.alert2 = false, 
         this.erreur = '',
        this.id_post = info.id
        this.id_cityarr = info.id_cityarr
        this.id_citydep = info.id_citydep
        
       this.checkpointarr =  JSON.parse( info.checkpoint_arr)
       this.checkpointdep =  JSON.parse( info.checkpoint_dep)
        this.name = info.name_user
        this.origin = info.origin
        this.destination = info.destination
        this.compagnie = info.compagnie
        this.date_depart = info.date_depart
        this.date_arrive = info.date_arrive
        this.kilos = info.kilo_available
        this.details = info.description
        this.prix = info.price_kilo
        this.categorie = info.package_category
        this.hide = true
        console.log('Les infos',info)
        console.log('Le nom',this.name) 
      },
      submit(){
        this.alert2 = false
        this.erreur = ''
        if(!this.auth == true){
          this.$router.push({ name: 'login'});
        }
        console.log('Kilos demandés:',this.ask_kilo)
        console.log('Tarif:',this.tarif)
        this.spinner = true
        axios.post('https://safe-community.fr/lara/api/reservation',{
          category:this.categorie,
          kilo:this.ask_kilo,
          description:this.contenu,
          amount:this.tarif,
          checkpoint_dep: this.checkpointdep.id,
          checkpoint_arr: this.checkpointarr.id,
          post_id:this.id_post
        },{headers:{
          Authorization: 'Bearer ' + localStorage.getItem('token')
        }
        })
        .then(response => {
          console.log(response)
          if(response.status == 200){
            this.spinner = false,
            this.modal = true,
            this.ask_kilo = ''
            this.tarif = ''
            this.contenu = ''
            this.description = ''
          }
        })
        .catch(error => {
          this.spinner = false
            this.alert2 = true
            this.erreur = error.response.data.message
        })
      },
   nonsubs(){
     this.alert2 = false
     this.erreur = ''
     this.ask_kilo = ''
     this.tarif=''
     this.contenu = ''
     this.choosencheckdep = ''
     this.choosencheckarr =''
     this.subs = false
   },
   checkpoint(){

   },
   },

     created(){
       axios.get('https://safe-community.fr/lara/api/slider/videospub')  // recuperer les videos de publicité
       .then(response => {
          console.log('Videos pub du stockage:',response)
          this.videos = response.data
          console.log(this.videos)
       })

        axios.get('https://safe-community.fr/lara/api/slider/videosprest')  // recuperer les videos de presetation
       .then(response => {
          console.log('Videos presentation du stockage:',response)
          this.videos2 = response.data
          console.log(this.videos)
       })

        axios.get('https://safe-community.fr/lara/api/slider/pictures') // recuperer les images slides
        .then(response => {
          console.log(response)
          console.log('Images du stockage:',response)
          this.pictures = response.data
          console.log(this.pictures)
        })
        .catch(error => {
          console.log(error)
        })

       var d = new Date()
       console.log(d.getFullYear())
       axios.get('https://safe-community.fr/lara/api/nextTrips') 
        .then(response => {
             console.log(response)
            console.log('Prochains voyages',response)
            this.lastravels = response.data
            this.travel = response.data
        }),
    
     
       axios.get('https://safe-community.fr/lara/api/showAllCity') // recuperer toute les villes
    .then(response => {
      console.log(response)
    this.villes = response.data
  })

     if(localStorage.getItem('token') == null){
          this.user = ''
          this.auth =false
          this.user.surname =''
      }
      else{
        this.user = JSON.parse(localStorage.getItem('user'))
        this.auth = true
      }
        
        console.log(this.user.name)
     
    },


  }

</script>

<style scoped>

@media screen and (min-width: 200px) and (max-width: 450px) {
  .montitre {
    font-size: 17px;
  }
}

.pb-video-container {
        padding-top: 20px;
        background: white;
        font-family: Lato;
    }

    .pb-video {
        border: 1px solid #e6e6e6;
        padding: 5px;
    }

        .pb-video:hover {
            background: #2c3e50;
        }

    .pb-video-frame {
        transition: width 0.5s, height 0.5s;
    }

        .pb-video-frame:hover {
            height: 300px;
            
        }

    .pb-row {
        margin-bottom: 10px;
    }

@import url('https://unpkg.com/gijgo@1.9.13/css/gijgo.min.css');

 html,
    body,
    header,
    .jarallax {
      height: 700px;
    }

    /* @media (max-width: 740px) {
      html,
      body,
      header,
      .jarallax {
        height: 100vh;
      }
    } */
    
    

    @media (min-width: 800px) and (max-width: 850px) {
      html,
      body,
      header,
      .jarallax {
        height: 100vh;
      }
    }

    @media (min-width: 560px) and (max-width: 650px) {
      header .jarallax h1 {
        margin-bottom: .5rem !important;
      }
      header .jarallax h5 {
        margin-bottom: .5rem !important;
      }
    }

    @media (min-width: 660px) and (max-width: 700px) {
      header .jarallax h1 {
        margin-bottom: 1.5rem !important;
      }
      header .jarallax h5 {
        margin-bottom: 1.5rem !important;
      }
    }

    .top-nav-collapse {
      background-color: #7283A7 !important;
    }

    .navbar:not(.top-nav-collapse) {
      
    }

    @media (max-width: 768px) {
      .navbar:not(.top-nav-collapse) {
        background: #7283A7 !important;
      }
    }
    @media (min-width: 800px) and (max-width: 850px) {
        .navbar:not(.top-nav-collapse) {
            background: #7283A7!important;
        }
    }

    footer.page-footer {
      background-color: #383838;
    }

    @media (max-width: 450px) {
      .display-3 {
        font-size: 3rem;
      }
    }

    .card {
  display: flex;
  height: 280px;
  width: 200px;
  background-color: #17141d;
  border-radius: 10px;
  box-shadow: -1rem 0 3rem #000;
/*   margin-left: -50px; */
  transition: 0.4s ease-out;
  position: relative;
  left: 0px;
}

.card:not(:first-child) {
    margin-left: -50px;
}

.card:hover {
  transform: translateY(-20px);
  transition: 0.4s ease-out;
}

.card:hover ~ .card {
  position: relative;
  left: 50px;
  transition: 0.4s ease-out;
}

.card:hover .filledbar {
  width: 120px;
  transition: 0.4s ease-out;
}
.card:hover .stroke {
  stroke-dashoffset: 100;
  transition: 0.6s ease-out;
}

</style>



