  <template>
<div>  
  <div class="classic-form-page">
      <mdb-mask class="d-flex gradient justify-content-center align-items-center">
        <mdb-container>
          <mdb-row>
            <div class="white-text text-center text-md-left col-md-6 mt-xl-5 mb-5">
              <h1 class="h1-responsive font-weight-bold animated fadeInLeft">Safe Community</h1>
              <hr class="hr-light animated fadeInLeft" />
              <h6
                class="mb-4 animated fadeInLeft" style="color: black"
              >Envoyez des Colis a l'international par un reseau de confiance</h6>
            </div>
            <mdb-col md="6" xl="5" class="mb-4 animated fadeInRight">
              <!-- Card -->
              <mdb-card class="mt-5">
                
                <mdb-card-body>
                  <form @submit.prevent="submit">
                    <p class="h4 text-center py-4">Vérification du compte</p>
                     <div class="alert" v-bind:class="{'alert-danger': alert}"> {{ message }} </div>
                    <div class="grey-text">
                      <mdb-input label="Email" icon="envelope" group type="email" validate error="wrong" success="right" id="email"
                    v-model="email"  />
                      <mdb-input label="Code de verification" icon="lock" group type="password" validate id="password" v-model="password" />
                    </div>
                    <div class="text-center py-4 mt-3">
                      <mdb-btn class="btn btn-cyan rounded" type="submit" >Valider</mdb-btn>
                    </div>
                  </form>
                    <div class="row">
                      <div class="col-5"></div>
                    <div v-bind:class="{'spinner-border': spinner}" class="ml-3" role="status">

  </div>
  </div>
  <mdb-col md="" class="d-flex justify-content-end">
                  <p class="font-small grey-text mt-3"><router-link :to="{name: 'login'}" class="dark-grey-text ml-1 font-weight-bold" >Se connecter</router-link> </p>
                </mdb-col>
                </mdb-card-body>
                
              </mdb-card>
              <!-- Card -->
            </mdb-col>
            
          </mdb-row>
        </mdb-container>
      </mdb-mask>
    

    <mdb-container>
      <mdb-row class="py-5">
        <mdb-col md="12" class="text-center">
          <p>Copyright ©2020</p>
        </mdb-col>
      </mdb-row>
    </mdb-container>
  </div>
    <!-- Frame Modal Bottom -->
    <mdb-modal frame position="bottom" direction="bottom" :show="modal2" @close="modal2 = false">
        <mdb-modal-body class="text-center">
            <span>Inscription validée veuillez vous connecter</span>
            <mdb-btn color="secondary" @click="modal2 = false"><router-link :to="{name: 'login'}" class="text-white" >Fermer </router-link> </mdb-btn>
        </mdb-modal-body>
    </mdb-modal>
  
  </div>
</template>



<script>
import axios from 'axios';
export default {

  data (){
    return {
        email: '',
        password: '',
        modal2:false,
         alert:'',
         message:'',
        token:'',
        user:'',
        spinner:''
    }
  }, 
  methods:{
     submit(){
       this.alert = false
       this.message = ""
       this.spinner = true
       axios.post('https://safe-community.fr/lara/api/auth/confirm',{
         email:this.email,
         code:this.password
       })
       .then(response => {
          if (response.status == 200) {
        this.modal2= true
        this.spinner = false
          }
       })
      .catch(error => {
      if (error.response.data.message) {
        this.spinner = false
        console.log(this.password)
                  this.alert = true
                  this.message = error.response.data.message
                  console.log(error)
          }
       })
    
  }

  },
  created(){
    console.log(localStorage.token)
  }

 


    
}
</script>



<style>
.classic-form-page .view {
 /* The image used */
  background-image: url("https://cdn.pixabay.com/photo/2019/06/27/06/49/plane-4301615_960_720.png");

  /* Full height */
  height: 100%;

  /* Center and scale the image nicely */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.classic-form-page .gradient {
  background-image: url("https://cdn.pixabay.com/photo/2019/06/27/06/49/plane-4301615_960_720.png");

  /* Full height */
  height: 100%;

  /* Center and scale the image nicely */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.classic-form-page h6 {
  line-height: 1.7;
}

.classic-form-page .navbar {
  transition: background 0.5s ease-in-out, padding 0.5s ease-in-out;
}

.classic-form-page .navbar .md-form {
  margin: 0;
}

.top-nav-collapse {
  background: #424f95 !important;
}

@media (max-width: 768px) {
  .classic-form-page .navbar:not(.top-nav-collapse) {
    background: #424f95 !important;
  }
}



body, html {
  height: 100%;
}

</style>


