<template>
<div>
  <section >
    <mdb-modal :show="modal" @close="modal = false">
            <mdb-modal-header>
                <mdb-modal-title> Accepter colis </mdb-modal-title>
            </mdb-modal-header>
            <mdb-modal-body>
               Etes vous sur de votre choix?
            </mdb-modal-body>
            <mdb-modal-footer  style="height:50px" >
                <mdb-btn size="sm" class="rounded" color="danger" @click.native="modal = false">Annuler</mdb-btn>
                <mdb-btn size="sm" class="rounded" color="primary" @click="accept()">Oui</mdb-btn>
            </mdb-modal-footer>
        </mdb-modal>

         <mdb-modal :show="modal2" @close="modal2 = false">
            <mdb-modal-header>
                <mdb-modal-title> Refuser colis </mdb-modal-title>
            </mdb-modal-header>
            <mdb-modal-body>
               Etes vous sur de votre choix?
            </mdb-modal-body>
            <mdb-modal-footer  style="height:50px" >
                <mdb-btn size="sm" class="rounded" color="danger" @click.native="modal2 = false">Annuler</mdb-btn>
                <mdb-btn size="sm" class="rounded" color="primary" @click="refuse()">Oui</mdb-btn>
            </mdb-modal-footer>
        </mdb-modal>

         <mdb-modal :show="modal3" @close="modal3 = false">
            <mdb-modal-header>
                <mdb-modal-title> Regler facture </mdb-modal-title>
            </mdb-modal-header>
            <mdb-modal-body>
              Si vous cliquez sur "Accepter" Vous allez etre contacté par les administrateurs pour le paiement
            </mdb-modal-body>
            <mdb-modal-footer  style="height:50px" >
                <mdb-btn size="sm" class="rounded" color="danger" @click.native="modal3 = false">Annuler</mdb-btn>
                <mdb-btn size="sm" class="rounded" color="primary" @click="reglerfacture()">Accepter</mdb-btn>
            </mdb-modal-footer>
        </mdb-modal>

      <h3 class="font-weight-bold text-center h3 my-3">Choisissez un colis à transporter</h3>
  <v-data-table
    :headers="headers"
    :items="pending"
    sort-by="Ville de départ"
    class="elevation-1"
  >
    <template v-slot:item.actions="{ item }">
      <v-btn 
      depressed
       small 
       rounded
       dark
       color="green darken-3"
       class="m-1"
       @click="modal = true, value = 'Accepter le colis', ind=item, id=item.id">
       Accepter
       </v-btn>
       
     <v-btn 
      depressed
       small 
       rounded
       dark
       color="red lighten-1"
       class="my-2"
        @click="modal2 = true, ind=item, id=item.id "
       >
       Refuser
       </v-btn>

       <!--  <v-btn 
       small 
       rounded
       color="grey lighten-1"
       class="my-2"
       style="font-size: 9.5px"
       @click="reservation(item)"
      >
       Détails
       </v-btn> -->

    </template>  
  </v-data-table>
  </section>
  <section>
      <h3 class="font-weight-bold text-center h3 my-3 mt-5">Réservations en cours de traitement</h3>
  <v-data-table
    :headers="headers"
    :items="wait"
    sort-by="Ville de départ"
    class="elevation-1"
  >
  </v-data-table>
  </section>
  <section class="mt-5">
     <h3 class="font-weight-bold text-center h3 my-3 mt-5">Paiements en attente</h3>
     <mdb-card>
    <mdb-tabs class="mb-5 my-2 mx-2"
    :active="0"
    default
    :links="[
      { text: 'Factures', slot: 'custom-slot' },
      { text: 'Reglements' },
      { text: 'Remboursement' }]"
      
    :transition-duration="0.5"
    transition-style="linear"
  >
  <template :slot="'custom-slot'">
    <v-data-table
    :headers="headers3"
    :items="factures"
    sort-by="Date"
    class="elevation-1"
  >

  </v-data-table>
    </template>
    <template :slot="'Reglements'">
    <v-data-table
    :headers="headers5"
    :items="reglements"
    sort-by="Date"
    class="elevation-1"
  >
  </v-data-table>
    </template>

    <template :slot="'Remboursement'">
    <v-data-table
    :headers="headers4"
    :items="remboursements"
    sort-by="Date"
    class="elevation-1"
  >
  </v-data-table>
    </template>
    
    
  </mdb-tabs>
  </mdb-card>
  </section>
  <v-snackbar
      v-model="snackbar"
    >
      {{ txt }}
      <v-btn
        color="red"
        text
        @click="snackbar = false"
      >
        Fermer
      </v-btn>
    </v-snackbar>
  
  </div>
</template>

<script>
import axios from 'axios'
import store from '../store/index'
/* import { mapActions } from 'vuex' */
export default {
  
  data () {
    return {
      snackbar:false,
      txt:'',
       pending:[],
       wait:[],
       cycle: false,
       modal:false,
       modal2:false,
       modal3:false,
       remboursements:[],
       reglements:[],
       factures:[],
       reservations:[],
       penalites:[],
       user:'',
       id:'',
       value:'',
       ind:'',
       headers: [
        {
          text: "#Numéro du voyage rattaché",
          align: 'start',
          sortable: false,
          value: 'user_number',
          
        },
          { text: "Nom de l'expéditeur", value: 'publisher_name' },
         { text: 'Ville de départ', value: 'depart' },
         { text: "Ville de d'arrivée", value: 'arrive' },
         { text: 'Date de départ', value: 'date_depart' },
         { text: "Date de d'arrivée", value: 'date_arrive' },
        { text: 'Poids du colis(Kg)', value: 'kilo' },
        { text: 'Prix du kilos(€)', value: 'price_kilo' },
        { text: 'Détails', value: 'description' },
        { text: 'Cout global de la réservation(€)', value: 'amount' },
        { text: '', value: 'actions', sortable: false },
      ],
      headers2: [
        {
          text: "#Numéro de la réservation",
          align: 'start',
          sortable: false,
          value: 'user_number',
          
        },
         { text: "Nom du voyageur", value: 'publisher_name' },
         { text: 'Ville de départ', value: 'depart' },
         { text: "Ville de d'arrivée", value: 'arrive' },
         { text: 'Date de départ', value: 'date_depart' },
         { text: "Date de d'arrivée", value: 'date_arrive' },
        { text: 'Poids du colis(Kg)', value: 'kilo' },
        { text: 'Prix du kilos(€)', value: 'price_kilo' },
        { text: 'Détails', value: 'description' },
        { text: 'Cout global de la réservation(€)', value: 'amount' },
        { text: '', value: 'actions', sortable: false },
      ],
      headers3: [
        {
          text: "Reference",
          align: 'start',
          sortable: false,
          value: 'reference',
          
        },
         { text: "#Numéro de la reférence", value: 'reference_id' },
         { text: "#Date", value: 'date' },
         { text: 'Intitulé', value: 'intitule' },
         { text: "Description", value: 'description' },
         { text: 'Montant(€)', value: 'total_price' },
         { text: "Statut", value: 'statut' },
        { text: '', value: 'actions', sortable: false },
      ],
      headers4: [
        {
          text: "Entreprise",
          align: 'start',
          sortable: false,
          value: 'custumer',
          
        },
         { text: "#Numéro de la réservation à rembourser", value: 'comment_id' },
         { text: "#Date", value: 'date' },
         { text: 'Intitulé', value: 'intitule' },
         { text: "Description", value: 'description' },
         { text: 'Montant(€)', value: 'total_price' },
         { text: "Statut", value: 'statut' },
        { text: '', value: 'actions', sortable: false },
      ],
      headers5: [
        {
          text: "Numéro du voyage",
          align: 'start',
          sortable: false,
          value: 'post_id',
          
        },
         { text: "#Date", value: 'date' },
         { text: 'Intitulé', value: 'intitule' },
         { text: 'Montant(€)', value: 'rem_voyageur' },
         { text: "Statut", value: 'statut' },
        { text: '', value: 'actions', sortable: false },
      ],
      editedIndex: -1,
      defaultItem: {
        origin: '',
        destination: 0,
        destination: 0,
        price_kilo: 0,
        kilo: 0,
        kilo_available:0,
      },
    }
  },
  
    methods:{

      initialise(){
         axios.get('https://safe-community.fr/lara/api/reservation/user/en_attente',{
           headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        }
         })
      .then(response => {
          this.pending =  response.data
          console.log('Transactions en attente : ',this.pending)
/*           localStorage.setItem('notif', this.pending.length)
 */           store.commit('increment',this.pending.length) 
      })
      },

       accept(){
          axios.post("https://safe-community.fr/lara/api/reservation/accepter",{
           id: this.id 
           },
           {
              headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        }
           })
      .then(response => {
        this.pending.splice(this.pending.indexOf(this.id), 1)
          console.log('Acceptation dun voyage : ',response.data)
          this.modal = false
          this.txt = response.data.message
          this.snackbar = true
           this.pending = response.data  
           let notif = store.state.count
     notif = notif - 1
     store.commit('increment',notif) 
       
          /* this.$store.commit('increment',this.pending.length) */
      })
      .catch(error => {
        this.modal = false
         this.txt = error.response.data.message
          this.snackbar = true
      })
     
      

    },

    reglerfacture(){
       axios.post("https://safe-community.fr/lara/api/notifier",{
           id: this.id 
           },
           {
              headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        }
           })
      .then(response => {
        this.factures.splice(this.factures.indexOf(this.id), 1)
          console.log('Regler facture : ',response.data)
          this.modal3 = false
          this.txt = response.data.message
          this.snackbar = true
           let notif = store.state.count
     notif = notif - 1
     store.commit('increment',notif) 
      })
      .catch(error => {
        this.modal3 = false
         this.txt = error.response.data.message
          this.snackbar = true
      })
    },

    refuse(item){
axios.post("https://safe-community.fr/lara/api/reservation/refuser",{
            id: this.id 
           },
           {
              headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        }
           })
      .then(response => {
        this.pending.splice(this.pending.indexOf(this.ind), 1)
          console.log('Refusation dun voyage : ',response.data)
          this.modal2 = false
           let notif = store.state.count
     notif = notif - 1
     store.commit('increment',notif) 
       
      })
      .catch(error => {
        this.modal2 = false
         this.txt = error.response.data.message
          this.snackbar = true
      })
     

    }

    },

    created(){
      
    this.user = JSON.parse(localStorage.getItem('user'))
    console.log("id de user", this.user.id)
    this.initialise()

      axios.get('https://safe-community.fr/lara/api/processed',{  //recupreration des reservations en attente
        headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        }
      })
      .then(response => {
         console.log(response);
          this.wait =  response.data
          console.log('Réservations en attente : ',this.wait)
/*           localStorage.setItem('notif', this.pending.length)
 */           
      })

      axios.get('https://safe-community.fr/lara/api/factures',{  //Penalites des reservations en attente
        headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        }
      })
      .then(response => {
         console.log(response);
          this.factures =  response.data
          console.log('Penalites en attente : ',this.factures) 
          store.commit('increment',this.factures.length)         
      })

      axios.get('https://safe-community.fr/lara/api/reglements',{  //recupreration des Voyages en attente
        headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        }
      })
      .then(response => {
         console.log(response);
          this.reglements =  response.data
          console.log('Voyages en attente : ',this.reglements)
           store.commit('increment',this.reglements.length)         
      })

       axios.get('https://safe-community.fr/lara/api/remboursements',{  //recupreration des reservations en attente
        headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        }
      })
      .then(response => {
         console.log(response);
          this.remboursements =  response.data
          console.log('Réservations en attente : ',this.remboursements)
          store.commit('increment',this.remboursements.length)           
      })

      
    },

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



