<template>
  <v-app>
  
    <mdb-modal frame position="bottom" direction="bottom" :show="modal5" @close="modal5 = false, spinner=false">
        <mdb-modal-body class="text-center">
            <span> {{ message3 }} </span>
            <mdb-btn color="secondary" @click.native="spinner=false, modal5 = false">Fermer</mdb-btn>
        </mdb-modal-body>
    </mdb-modal>
     
    <mdb-modal class="mt-5" :show="modal" @close="modal = false">
                <mdb-modal-header>
                    <mdb-modal-title> {{ ind.option }} </mdb-modal-title>
                </mdb-modal-header>
                <mdb-modal-body>
                  <div v-if="ind.id == 1" >
                    Vous confirmez votre choix?
                  </div>
                  <div v-if="ind.id !== 1" >

                  
                    Veillez choisir le mode de payement
               
               <v-container fluid>  
    <v-radio-group v-model="radios" :mandatory="false" row>
      <v-radio  color="orange" label="Orange money" value="orange money"></v-radio>
      <v-radio  color="yellow"  label="Mtn mobile money" value="mtn mobile money"></v-radio>
      <v-radio  color="green" label="Espèces" value="especes"></v-radio>
      <v-radio color="success"  label="Virement Bancaire" value="virement bancaire"></v-radio>
       <v-radio  color="blue" label="Paypal" value="paypal"></v-radio>
    </v-radio-group>
  </v-container>
  </div>
                </mdb-modal-body>
                <mdb-modal-footer  style="height:50px" >
                    <mdb-btn size="sm" class="rounded" color="danger" @click="modal = false">Annuler</mdb-btn>
                    <mdb-btn size="sm" class="rounded" color="primary" @click="submit(ind.id)">Terminer</mdb-btn>
                </mdb-modal-footer>
            </mdb-modal>
    <v-dialog
    v-model="dialog"
    max-width="290"
    >
      <v-card>
        <v-card-title color="red" class="headline"> Annulation </v-card-title>
        <v-card-text>
          Vous allez annuler votre souscription
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
        <v-btn color="green darken-1" text @click="annuler()">Continuer</v-btn>
        <v-btn color="red" text @click="dialog = false">Annuler</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    
    
    <mdb-card  class="my-3"> 
<template>
    <mdb-tabs class="mb-5 mx-2"
    :active="0"
    default
    :links="[
      { text: 'A propos', slot: 'custom-slot' },
      { text: 'Ma situation actuelle' },
      { text: 'Choisir une option' },
      { text: 'Payements en attente' },
      { text: 'Historique' }]"
      
    :transition-duration="0.5"
    transition-style="linear"
  >
    <template :slot="'custom-slot'">
         
    <v-stepper v-model="e1">
    <v-stepper-header>
      <v-stepper-step :complete="e1 > 1" step="1">A quoi sert l'assurance</v-stepper-step>

      <v-divider></v-divider>

      <v-stepper-step :complete="e1 > 2" step="2">Comment changer d'assurance?</v-stepper-step>

      <v-divider></v-divider>

      <v-stepper-step step="3">Quest ce que quest ce que</v-stepper-step>
    </v-stepper-header>

    <v-stepper-items>
      <v-stepper-content step="1">
        <v-card
          class="mb-12"
          color="grey lighten-1"
          height="200px"
        > <v-img
      class="white--text align-end"
      height="200px"
      src="https://safe-1267275.webdb13.lwspanel.com/Bleu%20Marron%20Simple%20Moderne%20Marque%20Directives%20Pr%C3%A9sentation.jpg"
    >
    </v-img>
        </v-card>

        <v-btn
        class="mt-3"
        dark
          color="indigo"
          @click="e1 = 2"
        >
          Suivant
        </v-btn>

      </v-stepper-content>

      <v-stepper-content step="2">
        <v-card
          class="mb-12"
          dark
          color="grey lighten-1"
          height="200px"
        ></v-card>

      <v-btn
        class="mt-3"
        dark
          color="indigo"
          @click="e1 = 3"
        >
          Suivant
        </v-btn>

      </v-stepper-content>

      <v-stepper-content step="3">
        <v-card
          class="mb-12"
          color="grey lighten-1"
          height="200px"
        ></v-card>

       <v-btn
        class="mt-3"
        dark
          color="indigo"
          @click="e1 = 1"
        >
          Suivant
        </v-btn>
      </v-stepper-content>
    </v-stepper-items>
  </v-stepper>
</template>
      
    <template :slot="'Ma situation actuelle'">
     <v-card
    class="mx-auto"
    max-width="344"
  >
    <v-card-text>
      <div>Ma situation</div>
      <div style="font-size:18px" class="my-2"> <u class="font-weight-bold">Option en cours:</u> {{insurance.option}} </div>
       <div style="font-size:18px" class="my-2 "> <u class="font-weight-bold">Description :</u> {{insurance.description}} </div>
        <div style="font-size:18px" class="my-2 "> <u class="font-weight-bold">Montant de la prime :</u> {{insurance.amount}} </div>
         <div style="font-size:18px" class="my-2"> <u class="font-weight-bold">Nombre de colis envoyés dans le mois:</u> {{insurance.ticket_used}} </div>
          <div style="font-size:18px" class="my-2"> <u class="font-weight-bold">nombre de colis max pouvant être envoyés dans le mois :</u> {{insurance.ticket}} </div>
           <div style="font-size:18px" class="my-2"> <u class="font-weight-bold">Date de la prochaine échéance pour le paiement de la prime :</u> {{insurance.end}} </div>
            <div style="font-size:18px" class="my-2"> <u class="font-weight-bold">Statut :</u> <span style="font-size:22px" class="font-weight-bold" :class="{'red-text':dette, 'green-text':!dette }">{{insurance.statut}}</span> </div>
            <mdb-btn v-if="dette" @click.native="modal = true, ind = insurance" size="sm" block color="default" class="rounded-pill">Regler ma prime d'assurance</mdb-btn>
    </v-card-text>
  </v-card>
    </template>
    <template :slot="'Payement'">
      <mdb-container>
        <mdb-jumbotron class="text-center hoverable">
          <form @submit.prevent="submit">
            <p class="h4 text-center mb-4">Validez votre choix d'assurance</p>
           <div class="mb-3"><span><a href="#">Details de la transactions</a></span></div>
            <div class="mb-3"><span><a href="#">Montant de la prime</a></span></div>
            <div class="mb-3"><span><a href="#">Nombre de colis envoyés dans le mois</a></span></div>
            <div class="mb-3"><span><a href="#">nombre de colis max pouvant être envoyés dans le mois</a></span></div>
            <div class="mb-3"><span><a href="#">Date de la prochaine échéance pour le paiement de la prime</a></span></div>
            <div class="mb-3"><span><a href="#">Statut </a></span></div>
           <mdb-btn block color="default" type="submit" class="rounded-pill">Valider</mdb-btn>
           <div class="mt-3">
             <div v-bind:class="{'spinner-border': spinner}" class="center" role="status"></div>
            <div class="alert" v-bind:class="{'alert-success': success, 'alert-danger': alert} "> {{ message }} </div>
           </div>
           </form>
        </mdb-jumbotron>
        
      </mdb-container>
    </template>

    <template :slot="'Choisir une option'">
      <v-card
    class="mt-3 mb-5 mx-auto"
    max-width="300"
    tile
  >
      <v-subheader>Choisir / changer assurance</v-subheader>

        <v-list-item
          v-for="item in items"
          :key="item.id"
        >

          <v-list-item-icon>
            <v-icon v-text="item.icon"></v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title v-text="item.text"></v-list-item-title>       
          </v-list-item-content>
           <v-list-item-content>
             <v-btn depressed small rounded dark class="mt-1" @click="modal = true, ind.option = item.option, ind.id = item.id">Choisir</v-btn>
          </v-list-item-content>
          
        </v-list-item>

    
  </v-card>
    </template>
     <template :slot="'Payements en attente'">

   <v-data-table
    :headers="headers2"
    :items="wait"
    sort-by="Ville de départ"
    class="elevation-1"
  >
   <template v-slot:item.actions="{ item }">
       
     <v-btn 
      depressed
       small 
       rounded
       dark
       color="red darken-2"
       class="mt-1"
        @click="dialog = true,id = item.id "
       >
       Annuler 
       </v-btn>

    </template> 
   </v-data-table>
   </template>

<template :slot="'Historique'">

   <v-data-table
    :headers="headers"
    :items="historique"
    sort-by="Ville de départ"
    class="elevation-1"
  >
   </v-data-table>
   
   </template> 
    </mdb-tabs>
</template>
<v-snackbar
      v-model="snackbar"
    >
      {{ txt }}
      <v-btn
        color="red"
        text
        @click="snackbar = false"
      >
        Fermer
      </v-btn>
    </v-snackbar>
  </mdb-card>
  
  
    </v-app>
</template>

<script>
import axios from 'axios'
import store from '../store/index'
export default {
  name: "assurance",
  data() {
    return {
      radios:'',
      ind:{},
      items: [
        { text: 'Premium', icon: 'mdi-clock', option:'Souscription au premium', id:3},
        { text: 'Medium', icon: 'mdi-account', option:'Souscription au medium', id:2},
        { text: 'Free', icon: 'mdi-flag', option:'Souscription au Free', id:1},
      ],
        date: new Date().toISOString().substr(0, 10),
      snackbar:false,
      id:'',
      message3:'',
      modal5:false,
      txt:'',
      alert:'',
      message:'',
      modal: false,
      spinner:false,
      insurance:{},
       headers: [
        {
          text: '#numéro de la souscription',
          align: 'start',
          sortable: false,
          value: 'id',
        },
         { text: 'Date de début', value: 'start' },
        { text: 'Date de fin', value: 'end' },
        { text: "Option choisie", value: 'option' },
        { text: 'Montant', value: 'amount' },
        
      ],
      headers2: [
        {
          text: '#numéro de la souscription',
          align: 'start',
          sortable: false,
          value: 'user_number',
        },
         { text: 'Date de début', value: 'start' },
        { text: 'Date de fin', value: 'end' },
        { text: "Option choisie", value: 'option' },
        { text: 'Montant', value: 'amount' },
        { text: '', value: 'actions', sortable: false },
        
      ],
      active:'',
      wait:[],
      historique:[],
      success:false,
      gratuit:'Gratuit',
      premium:'Premium',
      medium:'medium',
      time:null,
      date: new Date().toISOString().substr(0, 10),
      dette : false,
      e1: 1,
      dialog:false,

    };
    
  },

  methods:{
    
    annuler(){
        axios.post('https://safe-community.fr/lara/api/s_insurance/annuler',{
          id : this.id
        },{
      headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        } 
    })
        .then( response => {
          console.log('Assurance annulée : ', response)
          this.dialog = false
          this.txt = response.data.message
          this.snackbar = true
           this.wait.splice(this.wait.indexOf(this.id), 1)
          
        })
    },

    index(){
      console.log('lindex est ',this.ind)
    },
    regle(){
      console.log('loption choisie : ', this.ind)
      store.commit('takeassur', this.ind.id)
       this.$router.push({ name: 'd.bank'});
   },

   update(){ 
     store.commit('takeassur', this.insurance.Insurance_id)
     this.$router.push({ name: 'd.bank'});
   },

  submit(param){
    axios.post('https://safe-community.fr/lara/api/s_insurance',{
      insurance_id:param,
      paid_mode:this.radios,
    },{
      headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        } 
    })
    .then(response=>{
      console.log(response)
        this.$router.push({ name: 'd.assurance'});
        console.log(response)
        this.modal5 = true
        this.modal  = false
        this.message3 = 'Un administrateur vous contactera pour effectuer le payement'
        location.reload();
        store.commit('takeassur',0)
    })
    .catch(error => {
      this.modal  = false
       this.txt = error.message
        this.snackbar = true
      console.log(error)
    })
    
  },

  chargeassurance(){
    axios.get("https://safe-community.fr/lara/api/s_insurances/latest",
       {
          headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        }
       })
      .then(response =>  {
        console.log('la derniere assurance : ',response)
        if (response.data == "") {
          this.insurance.option = ''
          this.insurance.description=''
          this.insurance.amount=''
          this.insurance.ticket_used=''
          this.insurance.ticket=''
          this.insurance.end=''
          this.insurance.statut=''

        }
        else{
           this.insurance = response.data.s_insurance
        }
         
          console.log('Ma situation actuelle : ',response)
          if(this.insurance.statut == "Expiré"){
            this.dette = true
          }
      })
  }
},

created(){
  this.dette = false
   this.chargeassurance()

       axios.get("https://safe-community.fr/lara/api/s_insurances/attente",//recuperer lassurance en attente de payement
       {
          headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        }
       })
      .then(response =>  {
          this.wait = response.data
      })

       axios.get("https://safe-community.fr/lara/api/s_insurance", //recuperer lhistorique des transactions assurance
       {
          headers:{
          Authorization: "Bearer " + localStorage.getItem('token')
        }
       })
      .then(response =>  {
          this.historique = response.data
      })

}

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.card.card-cascade .view.gradient-card-header {
  padding: 1rem 1rem;
  text-align: center;
}
.card.card-cascade h3,
.card.card-cascade h4 {
  margin-bottom: 0;
}
</style>



