<template>
<div>
    <v-app style="background-color:rgb(236,236,237)">
        <v-dialog
    v-model="dialog"
    max-width="290"
    >
      <v-card>
        <v-card-title color="red" class="headline"> Modifier le statut </v-card-title>
        <v-card-text>
        <v-select
                :items="select"
                v-model="statut"
                label="Choisissez un statut"
                ></v-select>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
        <v-btn color="blue darken-1" text @click="editStatut()">Continuer</v-btn>
        <v-btn color="red" text @click="dialog = false">Annuler</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
   
  <section>
    <h3 class="font-weight-bold text-center h3 mb-3 mt-5">Paiements de réservations</h3>
  <v-card>
    <v-card-title>
      <v-text-field
        v-model="search1"
       
        label="Rechercher un utilisateur"
        single-line
        hide-details
      ></v-text-field>
    </v-card-title>
    <v-data-table
      :headers="headers2"
      :items="reservations"
      :search="search"
    >
    <template v-slot:item.actions="{ item }">
       
     <v-btn 
      depressed
       small 
       rounded
       dark
       color="red darken-2"
       class="mt-1"
        @click="dialog = true,id = item.id "
       >
       Changer le statut
       </v-btn>

    </template> 
    </v-data-table>
  </v-card>
  </section>
  
  <section>
    <h3 class="font-weight-bold text-center h3 mb-3 mt-5">Paiements d'assurance</h3>
  <v-card>
    <v-card-title>
      <v-text-field
        v-model="search2"
       
        label="Rechercher un utilisateur"
        single-line
        hide-details
      ></v-text-field>
    </v-card-title>
    <v-data-table
      :headers="headers3"
      :items="assurances"
      :search="search"
    >
    <template v-slot:item.actions="{ item }">
       
     <v-btn 
      depressed
       small 
       rounded
       dark
       color="red darken-2"
       class="mt-1"
        @click="dialog = true,id = item.id "
       >
       Changer le statut
       </v-btn>

    </template> 
    </v-data-table>
  </v-card>
  </section>
 
  <section>
    <h3 class="font-weight-bold text-center h3 mb-3 mt-5">Paiements de pénalités </h3>
  <v-card>
    <v-card-title>
      <v-text-field
        v-model="search3"
        label="Rechercher un utilisateur"
        single-line
        hide-details
      ></v-text-field>
    </v-card-title>
    <v-data-table
      :headers="headers"
      :items="voyages"
      :search="search1"
    >
    <template v-slot:item.actions="{ item }">
       
     <v-btn 
      depressed
       small 
       rounded
       dark
       color="red darken-2"
       class="mt-1"
        @click="dialog = true,id = item.id"
       >
       Changer le statut
       </v-btn>

    </template> 
    </v-data-table>
  </v-card>
  </section>
<v-snackbar
      v-model="snackbar"
    >
      {{ txt }}
      <v-btn
        color="red"
        text
        @click="snackbar = false"
      >
        Fermer
      </v-btn>
    </v-snackbar>
     </v-app>
    </div>
</template>

<script>
import axios from 'axios'
import jsPDF from 'jspdf';
export default {
    data(){
        return{
             search:'',
             headers: [
                        {
                        text: "Motif de la transaction",
                        align: 'start',
                        sortable: false,
                        value: 'user_number',
                        
                        },
                        { text: "#Numéro de  la transaction", value: 'postuser_number' },
                        { text: "#Numéro de reférence", value: 'publisher_name' },
                        { text: 'Crediteur', value: 'depart' },
                        { text: "Debiteur", value: 'arrive' },
                        { text: 'Moyen de paiement', value: 'date_depart' },
                        { text: 'Montant', value: 'amount' },
                        { text: "Statut", value: 'statut' },
                        { text: '', value: 'actions', sortable: false },
                    ],
             headers2: [
                        {
                        text: "#Numéro de la réservation",
                        align: 'start',
                        sortable: false,
                        value: 'user_number',
                        
                        },
                        { text: "Nom du voyageur", value: 'publisher_name' },
                        { text: 'Ville de départ', value: 'depart' },
                        { text: "Ville de d'arrivée", value: 'arrive' },
                        { text: 'Date de départ', value: 'date_depart' },
                        { text: "Date de d'arrivée", value: 'date_arrive' },
                        { text: 'Poids du colis(Kg)', value: 'kilo' },
                        { text: 'Prix du kilos(€)', value: 'price_kilo' },
                        { text: 'Détails', value: 'description' },
                        { text: 'Cout global de la réservation(€)', value: 'amount' },
                        { text: '', value: 'actions', sortable: false },
                    ],
            headers3: [
                        {
                        text: '#numéro de la souscription',
                        align: 'start',
                        sortable: false,
                        value: 'id',
                        },
                        { text: 'Date de début', value: 'start' },
                        { text: 'Date de fin', value: 'end' },
                        { text: "Option choisie", value: 'option' },
                        { text: 'Montant', value: 'amount' },
                        { text: '', value: 'actions', sortable: false },
                    ],
            select:['réglé'],
            reservations:[],
            voyages:[],
            assurances:[],
            search1: '',
            search2: '',
            search3: '',
            dialog:false,
            statut:[],
            snackbar : false,
            txt:'',
            assurances : [],
        }
    },

    methods : {

        editReservation(){
             axios.post('https://safe-community.fr/lara/api/s_insurance/annuler',{
            statut : this.statut
            },{
        headers:{
            Authorization: "Bearer " + localStorage.getItem('token')
            } 
    })
        },

        editVoyage(){
             axios.post('https://safe-community.fr/lara/api/s_insurance/annuler',{
            statut : this.statut
            },{
        headers:{
            Authorization: "Bearer " + localStorage.getItem('token')
            } 
    })
        },

        editAssurance(){
             axios.post('https://safe-community.fr/lara/api/s_insurance/annuler',{
            statut : this.statut
            },{
        headers:{
            Authorization: "Bearer " + localStorage.getItem('token')
            } 
    })
        },
      
        allreservations(){
          let user = JSON.parse(localStorage.getItem('user')) 
          axios.get('https://safe-community.fr/lara/api/unpaidreservations',{
            headers:{
            Authorization: "Bearer " + localStorage.getItem('token')
            } 
          })
          .then(response => {
            console.log('paiements de reservations:',response);          
            this.reservations = response.data
          })
        },

        Voyages(){
          let user = JSON.parse(localStorage.getItem('user')) 
          axios.get('https://safe-community.fr/lara/api/admin/voyage',{
            id : user.id
          },{
            headers:{
            Authorization: "Bearer " + localStorage.getItem('token')
            } 
          })
          .then(response => { 
            console.log('Les voyages : ',response);      
            this.voyages = response.data
          })
        },

        Assurances(){
          let user = JSON.parse(localStorage.getItem('user')) 
          axios.get('https://safe-community.fr/lara/api/unpaidinsurance',{
            headers:{ 
            Authorization: "Bearer " + localStorage.getItem('token')
            } 
          })
          .then(response => {
            console.log('paiement Assurances : ',response);
            this.assurances = response.data
          })
        },

        editStatut(){
          axios.post('https://safe-community.fr/lara/api/admin/t_valider',{
            id: this.id
          },{
              headers:{
            Authorization: "Bearer " + localStorage.getItem('token')
            } 
          })
          .then(response => {
            this.dialog = false
            this.snackbar = true,
            this.txt = response.data.message
          })
          .catch(error => {
            this.dialog = false
            this.snackbar =true,
            this.txt = eror.response.data.message
          })
        }
      
    },

    created(){

  /*     var doc = new jsPDF()

doc.text('Hello world!', 10, 10)
doc.save('a4.pdf') */


      this.Voyages()
      this.Assurances()
      this.allreservations()
    }


}
</script>

<style>

</style>


